import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions,
  FlatList, ScrollView,
} from 'react-native';
import { AntDesign } from '@expo/vector-icons';
import { debug } from '../assets/utility';
import intl from 'react-intl-universal';
import theme from '../assets/theme';
import OverlayAlert from './OverlayAlert';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import OverlayRequestAccess from './OverlayRequestAccess';

export default class Limit extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      masters: props.route.params.masters,

      privileges: props.route.params.masters.privileges,
      settings: props.route.params.masters.settings,
      menus: [...new Map(props.route.params.masters.menus.map(v => [v.menuId, v])).values()],
      availableMenus: [...new Map(props.route.params.masters.menus.map(v => [v.menuId, v])).values()],

      searchText: '',
      displayMode: 'ALL',

      limiteds: [],
      selectedMenu: null,
      statusFlg: '',
      serveLimit: '',

      loading: false,
    };

    this.handlePressDisplayMode = this.handlePressDisplayMode.bind(this);

    this.handleSearchText = this.handleSearchText.bind(this);
    this.handleUpdateFromRequestAccess = this.handleUpdateFromRequestAccess.bind(this);
    this.handleUpdateFromAlert = this.handleUpdateFromAlert.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  componentDidMount() {
    // real time fast changing data
    this.doGet();
  }

  doGet() {
    const { serviceEntry, home, } = this.state;
    let url = serviceEntry + 'api/restmenu-serve';
    const params = new URLSearchParams();
    // params.append('recKey', recKey);
    url += ('?' + params);
    console.log(url)
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        this.setState({
          limiteds: response,
        });
      })
  }

  doSelectMenu(item) {
    const { limiteds } = this.state;
    this.setState({
      selectedMenu: item,
      statusFlg: item.statusFlg,
      serveLimit: limiteds.find(el => el.recKey === item.recKey)?.serveLimit.toString() || '0',
    })
  }

  doToggleStatus() {
    const { statusFlg } = this.state;
    if (statusFlg === 'A') {
      this.setState({ statusFlg: 'B' })
    } else {
      this.setState({ statusFlg: 'A' })
    }
  }

  doSubmit() {
    const { selectedMenu } = this.state;
    let options = [];
    options.push({ text: 'Yes', handleText: 'doLimitMenu' });
    this.setState({
      overlayAlertInputValue: {
        title: intl.get('action'),
        subtitle: 'Limit Menu',
        text: 'Limit ' + selectedMenu.menuName + '?',
        ok: false,
        cancel: true,
        options: options,
      },
      overlayAlertVisible: true,
    });
  }

  doLimitMenu(superUserId) {
    const { navigation } = this.props;
    const { home, settings, privileges, serviceEntry,
      selectedMenu, statusFlg, serveLimit } = this.state;
    // if (!(home.adminFlg === 'Y' || privileges.some(el => el.priId === 'UPDATEMENU')) && !superUserId) {
    //   this.setState({
    //     overlayRequestAccessVisible: true,
    //   });
    //   return;
    // }

    let url = serviceEntry + 'api/update-restmenu-serve';
    const body = {
      userId: home.userId,
      orgId: home.orgId,
      shopId: home.shopId,
      restmenuRecKey: selectedMenu.recKey,
      statusFlg: statusFlg,
      serveLimit: serveLimit,
    };

    this.setState({
      loading: true,
    });

    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post update-restmenu-serve');
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Limit Menu',
            home.userName,
            response.message,
            '',
            selectedMenu.menuName,
            serveLimit + ',' + statusFlg,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
            loading: false,
          })
          return;
        }
        this.setState({
          loading: false,
          displayMode: 'ALL',
          menus: response,
          availableMenus: response,
        }, () => this.doGet())
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });

  }

  handleSearchText(text) {
    const { menus, availableMenus, searchText } = this.state;
    let availableMenusUpdate = [];
    this.setState({
      searchText: text,
      displayMode: 'ALL',
    });
    let caseText = text.toUpperCase();

    availableMenusUpdate = menus;

    if (!text) {
      this.setState({
        availableMenus: availableMenusUpdate
      });
      return;
    }
    availableMenusUpdate = availableMenusUpdate.filter(item => {
      return item.menuName.toUpperCase().includes(caseText) || item.menuId.toUpperCase().includes(caseText);
    });
    this.setState({
      availableMenus: availableMenusUpdate
    });
  }

  handlePressDisplayMode() {
    const { displayMode, menus, availableMenus, limiteds } = this.state;
    if (displayMode === 'LIMITED ONLY') {
      this.setState({
        availableMenus: menus,
        searchText: '',
        displayMode: 'ALL',
      });
    } else if (displayMode === 'ALL') {
      this.setState({
        availableMenus: menus.filter(el => limiteds.some(obj => obj.recKey === el.recKey)),
        searchText: '',
        displayMode: 'LIMITED ONLY',
      });
    }
  }

  handleUpdateFromRequestAccess(text) {
    const { home, serviceEntry, settings } = this.state;
    if (!text) {
      this.setState({
        overlayRequestAccessVisible: false,
      });
      return;
    }
    let url = serviceEntry + 'api/super-user';
    const body = {
      locId: home.locId,
      accessCode: text,
      priId: 'UPDATEMENU',
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post super-user successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Request Access UPDATEMENU',
            home.userName,
            response.message,
            '',
            '',
            response.userId,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          overlayRequestAccessVisible: false,
        });
        this.doLimitMenu(response.userId);
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromAlert(text) {
    this.setState({
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    });
    if (!text) {
      return;
    }
    if (text === 'doLimitMenu') {
      this.doLimitMenu();
    }
  }

  renderItem(item) {
    const { limiteds } = this.state;
    const found = limiteds.find(el => el.recKey === item.recKey);

    return (
      <TouchableOpacity
        style={styles.grid}
        onPress={() => this.doSelectMenu(item)}>
        <View style={styles.gridFirst}>
          <View style={styles.gridFirstLeft}>
            <Text style={styles.gridFirstLeftText}>{item.menuId}</Text>
          </View>
          <View style={styles.gridFirstRight}>
            {
              item.statusFlg === 'A' ?
                <Text style={[styles.gridFirstRightText, { color: theme.reserved }]}>ACTIVE</Text>
                :
                <Text style={[styles.gridFirstRightText, { color: theme.paid }]}>INACTIVE</Text>
            }
          </View>
        </View>
        <View style={styles.gridSecond}>
          <Text style={styles.gridSecondText}>
            {item.menuName}
          </Text>
        </View>
        <View style={styles.gridThird}>
          <View style={styles.gridThirdLeft}>
            {
              found &&
              <Text style={{ fontFamily: 'varela-round', color: theme.vacant, fontSize: 12 }}>LIMIT {found.serveLimit} SERVED {found.serveCount}</Text>
            }
          </View>
          <View style={styles.gridThirdRight}>
            <Text style={styles.gridThirdRightText}>${item.listPrice.toFixed(2)}</Text>
          </View>
        </View>
      </TouchableOpacity>
    )
  }

  render() {
    const { navigation } = this.props;
    const { home, availableMenus, selectedMenu, searchText, displayMode,
      overlayRequestAccessVisible, overlayProcedureMessageVisible, overlayAlertVisible, overlayAlertInputValue,
      loading, statusFlg, serveLimit } = this.state;
    console.log('in activate');

    return (
      <View style={styles.container}>
        {
          overlayAlertVisible &&
          <OverlayAlert
            input={overlayAlertInputValue}
            handleUpdateFromAlert={this.handleUpdateFromAlert} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        {
          overlayRequestAccessVisible &&
          <OverlayRequestAccess
            home={home}
            requestedPrivilege='UPDATEMENU'
            handleUpdateFromRequestAccess={this.handleUpdateFromRequestAccess} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
              <View style={styles.selectionContainer}>
              </View>
            </View>
          </View>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingRight: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.searchBox}>
                  <TextInput style={styles.searchBoxInputValue}
                    placeholder='ENTER SEARCH TEXT'
                    placeholderTextColor='rgba(255,255,255,0.2)'
                    value={searchText}
                    onChangeText={(searchText) => this.handleSearchText(searchText)}
                  />
                  <TouchableOpacity style={styles.searchBoxButton}
                    onPress={() => {
                      this.setState({ searchText: '' }, () => this.handleSearchText(''))
                      Keyboard.dismiss()
                    }}>
                    <AntDesign name='close' size={24} color={theme.grey4} />
                  </TouchableOpacity>
                </View>
                <View style={styles.searchBoxInfo}>
                  <TouchableOpacity style={styles.searchBoxInfoLeft}
                    onPress={() => this.handlePressDisplayMode()}
                  >
                    <Text style={styles.searchBoxInfoText}>{displayMode}</Text>
                  </TouchableOpacity>
                  <View style={styles.searchBoxInfoRight}>
                    <Text style={styles.searchBoxInfoText}>{availableMenus.length} ITEMS</Text>
                  </View>
                </View>
                <FlatList
                  style={{ flex: 1 }}
                  data={availableMenus}
                  showsVerticalScrollIndicator={false}
                  keyExtractor={(item, index) => index.toString()}
                  renderItem={({ item }) => this.renderItem(item)}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingLeft: 5 }]}>
              <View style={styles.gap}></View>
              {
                selectedMenu &&
                <View style={{ flex: 1, backgroundColor: theme.grey3, borderRadius: 5 }}>
                  <View style={{ alignItems: 'center', marginTop: 10, }}>
                    <Text style={{ fontFamily: 'varela-round', color: theme.paid, fontSize: 16, }}>{selectedMenu.menuId}</Text>
                    <Text style={{ fontFamily: 'varela-round', color: 'white', fontSize: 20, textAlign: 'center', paddingHorizontal: 20, }}>{selectedMenu.menuName}</Text>
                  </View>
                  <View style={styles.inputContainer}>

                    <View style={styles.inputRow}>
                      <View style={styles.inputRowLeft}>
                        <Text style={styles.inputRowLeftText}>Status</Text>
                      </View>
                      <TouchableOpacity style={styles.inputRowRight}
                        onPress={() => this.doToggleStatus()}>
                        <Text style={[styles.inputRowRightText, { color: statusFlg === 'A' ? theme.reserved : theme.paid }]}>{statusFlg === 'A' ? 'Active' : 'Inactive'}</Text>
                      </TouchableOpacity>
                    </View>
                    <View style={styles.inputRow}>
                      <View style={styles.inputRowLeft}>
                        <Text style={styles.inputRowLeftText}>Limit</Text>
                      </View>
                      <View style={styles.inputRowRight}>
                        <TextInput style={styles.textInput}
                          keyboardType='numeric'
                          selectTextOnFocus={true}
                          value={serveLimit}
                          onChangeText={text => this.setState({ serveLimit: text })}
                        />
                      </View>
                    </View>
                  </View>
                </View>
              }
              <View style={styles.gap}></View>
            </View>
          </View>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={[styles.button, !selectedMenu && { opacity: 0.2 }]}
                disabled={loading || !selectedMenu}
                onPress={() => this.doSubmit()}>
                <AntDesign
                  name='staro'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('update')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

