import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TouchableOpacity,
} from 'react-native';
import intl from 'react-intl-universal';
import { AntDesign, Feather, MaterialCommunityIcons } from '@expo/vector-icons';
import theme from '../assets/theme';

export default class OverlayLanguage extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    console.log('in overlaylanguage', this.props);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Language 语言 ランゲージ</Text>
            {/* <Text style={styles.overlayText}>Select a message for the chef</Text> */}
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromLanguage('en')}>
              <Text style={{ marginTop: 5, color: 'white', fontFamily: 'varela-round', fontSize: 24 }}>Aa</Text>
              <Text style={[styles.buttonText,]}>ENGLISH</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromLanguage('zhs')}>
              <Text style={{ color: 'white', fontFamily: 'varela-round', fontSize: 24 }}>简体</Text>
              <Text style={[styles.buttonText,]}>简体中文</Text>
            </TouchableOpacity>
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromLanguage('zht')}>
              <Text style={{ color: 'white', fontFamily: 'varela-round', fontSize: 24 }}>繁體</Text>
              <Text style={styles.buttonText}>繁體中文</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromLanguage('ja')}>
              <Text style={{ color: 'white', fontFamily: 'varela-round', fontSize: 24 }}>日本語</Text>
              <Text style={styles.buttonText}>日本語</Text>
            </TouchableOpacity>
          </View>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid, marginTop: 20 }]}
            onPress={() => this.props.handleUpdateFromLanguage('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View >
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    minWidth: '50%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  buttonsContainer: {
    paddingHorizontal: 10,
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
  },
  button: {
    height: 100,
    width: 100,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: theme.grey1,
    borderRadius: 5,
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
});