import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions, ScrollView,
  FlatList,
} from 'react-native';
import moment from 'moment';
import intl from 'react-intl-universal';
import { AntDesign } from '@expo/vector-icons';
import OverlayChoosePrinter from './OverlayChoosePrinter';
import OverlayEnterDate from './OverlayEnterDate';
import theme from '../assets/theme';

export default class Reprint extends Component {

  constructor(props) {
    super(props);
    this.state = {
      serviceEntry: props.route.params.serviceEntry,
      home: props.route.params.home,
      cashierPrinters: props.route.params.masters.cashierPrinters,

      selectedButton: 'ORDERS',
      masters: [],
      availableMasters: [],
      searches: [],
      selectedMaster: '',
      details: [],
      searchText: '',
      receiptsxText: '',

      headers: '',
      pays: '',

      selectedDateString: moment(new Date()).format('YYYY-MM-DD'),

      overlayChoosePrinterVisible: false,
    };
    this.handlePressDate = this.handlePressDate.bind(this);
    this.handlePressSearch = this.handlePressSearch.bind(this);
    this.handleSelectButton = this.handleSelectButton.bind(this);
    this.handleSelectMaster = this.handleSelectMaster.bind(this);
    this.handleReprint = this.handleReprint.bind(this);
    this.handleSearchText = this.handleSearchText.bind(this);
    this.handleUpdateFromEnterDate = this.handleUpdateFromEnterDate.bind(this);
    this.handleUpdateFromChoosePrinter = this.handleUpdateFromChoosePrinter.bind(this);
  }

  componentDidMount() {
    const { serviceEntry, home, selectedDateString } = this.state;
    let url = serviceEntry + 'api/orders';
    const params = new URLSearchParams();
    params.append('orgId', home.orgId);
    params.append('shopId', home.shopId);
    params.append('docDateString', selectedDateString);
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        console.log('orders', response);

        // to state
        this.setState({
          masters: response,
          availableMasters: response,
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handlePressDate() {
    this.setState({
      overlayEnterDateVisible: true,
    })
  }

  handlePressSearch() {
    const { serviceEntry, home, receiptsxText } = this.state;
    this.setState({
      selectedMaster: '',
      details: [],
      pays: '',
      headers: '',
    });
    if (!receiptsxText) {
      return;
    }
    let url = serviceEntry + 'api/receipts-search';
    const params = new URLSearchParams();
    params.append('orgId', home.orgId);
    params.append('shopId', home.shopId);
    params.append('searchInput', receiptsxText);
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        console.log('masters', response);

        // to state
        this.setState({
          masters: response.receiptmas,
          availableMasters: response.receiptmas,
          searches: response.receiptpaydtl,
          details: [],
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleUpdateFromEnterDate(text) {
    this.setState({
      overlayEnterDateVisible: false,
      selectedMaster: '',
      details: [],
      pays: '',
      headers: '',
    });
    if (!text) {
      return;
    }
    const { serviceEntry, home, selectedButton } = this.state;
    let url = serviceEntry;
    if (selectedButton === 'ORDERS') {
      url += 'api/orders';
    } else if (selectedButton === 'KOTS') {
      url += 'api/kots';
    } else if (selectedButton === 'RECEIPTS') {
      url += 'api/receipts';
    } else {
      url += 'api/daycloses';
    }
    const params = new URLSearchParams();
    const dateString = moment(text).format('YYYY-MM-DD');
    params.append('orgId', home.orgId);
    params.append('shopId', home.shopId);
    params.append('docDateString', dateString);
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        console.log('masters', response);

        // to state
        if (selectedButton === 'RECEIPTS') {
          this.setState({
            masters: response.receiptmas,
            availableMasters: response.receiptmas,
            searches: response.receiptpaydtl,
            selectedDateString: dateString,
            details: [],
          });
        } else {
          this.setState({
            masters: response,
            availableMasters: response,
            searches: [],
            selectedDateString: dateString,
            details: [],
          });
        }
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleSelectButton(type) {
    const { serviceEntry, home, selectedDateString } = this.state;

    this.setState({
      details: [],
      selectedMaster: '',
      selectedButton: type,
      pays: '',
      headers: '',
    })

    // early exit for RECEIPTSX 
    if (type === 'RECEIPTSX') {
      this.setState({
        masters: [],
        availableMasters: [],
      });
      return;
    }

    let url = serviceEntry;
    if (type === 'ORDERS') {
      url += 'api/orders';
    } else if (type === 'KOTS') {
      url += 'api/kots';
    } else if (type === 'RECEIPTS') {
      url += 'api/receipts';
    } else {
      url += 'api/daycloses';
    }
    const params = new URLSearchParams();
    params.append('orgId', home.orgId);
    params.append('shopId', home.shopId);
    params.append('docDateString', selectedDateString);
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        console.log('masters', response);
        if (response.message) {
          console.log(response.message);
          return;
        }
        // to state
        if (type === 'RECEIPTS') {
          this.setState({
            masters: response.receiptmas,
            availableMasters: response.receiptmas,
            searches: response.receiptpaydtl,
          });
        } else {
          this.setState({
            masters: response,
            availableMasters: response,
            searches: [],
          });
        }
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleSelectMaster(item) {
    const { serviceEntry, selectedButton } = this.state;
    this.setState({
      selectedMaster: item,
    });
    let url = serviceEntry;
    console.log('item', selectedButton, item);
    if (selectedButton === 'ORDERS') {
      url += 'api/orders/' + item.recKey + '/items';
    } else if (selectedButton === 'KOTS') {
      url += 'api/kots/' + item.recKey + '/items';
    } else if (selectedButton === 'RECEIPTS' || selectedButton === 'RECEIPTSX') {
      url += 'api/receipts/' + item.recKey + '/bundle';
    } else {
      url += 'api/daycloses/' + item.recKey + '/amts';
    }
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        console.log('details', response);

        // to state
        if (selectedButton === 'RECEIPTS' || selectedButton === 'RECEIPTSX') {
          this.setState({
            details: response.receiptitems,
            pays: response.receiptpays,
            headers: response.receiptmas
          });
        } else {
          this.setState({
            details: response,
          });
        }
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleReprint(printer) {
    const { serviceEntry, home, cashierPrinters, selectedMaster, selectedButton } = this.state;

    if (!selectedMaster) {
      return;
    }

    if (cashierPrinters.length > 1 && !printer && !home.defaultPrinter) {
      this.setState({
        overlayChoosePrinterVisible: true,
      });
      return;
    }

    let url = serviceEntry;
    // console.log('item', item);
    if (selectedButton === 'ORDERS') {
      url += 'api/orders/' + selectedMaster.recKey + '/reprint';
    } else if (selectedButton === 'KOTS') {
      url += 'api/kots/' + selectedMaster.recKey + '/reprint';
    } else if (selectedButton === 'RECEIPTS' || selectedButton === 'RECEIPTSX') {
      url += 'api/receipts/' + selectedMaster.recKey + '/reprint';
    } else {
      url += 'api/daycloses/' + selectedMaster.recKey + '/reprint';
    }
    console.log('debug', home.defaultPrinter);
    const body = {
      userId: home.userId,
      orgId: home.orgId,
      locId: home.locId,
      iomodelId: (home.defaultPrinter && home.defaultPrinter.iomodelId) || (printer && printer.iomodelId),
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post reprint successful', response);

        // to state

      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleSearchText(text) {
    const { masters, availableMasters, searchText, searches } = this.state;
    let availableMastersUpdate = [];
    this.setState({
      searchText: text,
      selectedMaster: '',
      details: [],
      pays: '',
      headers: '',
    });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);

    availableMastersUpdate = masters;

    if (!text) {
      this.setState({
        availableMasters: availableMastersUpdate
      });
      return;
    }
    availableMastersUpdate = availableMastersUpdate.filter(item => {

      let c1 = searches.filter(el => el.payMoney === Number(caseText));
      let c2 = searches.filter(el => el.pmId.toUpperCase().includes(caseText));

      return item.docId.toUpperCase().includes(caseText)
        || item.tableId.toUpperCase().includes(caseText)
        || item.createUserId.toUpperCase().includes(caseText)
        || c1.some(el => el.docId === item.docId)
        || c2.some(el => el.docId === item.docId);
    });
    this.setState({
      availableMasters: availableMastersUpdate
    });
  }

  handleUpdateFromChoosePrinter(printer, purpose) {
    this.setState({
      overlayChoosePrinterVisible: false,
    });
    if (!printer) {
      return;
    }
    this.handleReprint(printer);
  }

  render() {
    const { navigation } = this.props;
    const { cashierPrinters, selectedButton, masters, availableMasters, details, searchText, receiptsxText,
      selectedMaster, selectedDateString,
      pays, headers,
      overlayChoosePrinterVisible, overlayEnterDateVisible, } = this.state;
    // console.log('in reprint state', this.state);
    console.log('in reprint');

    return (
      <View style={styles.container}>
        {
          overlayEnterDateVisible &&
          <OverlayEnterDate
            dateString={selectedDateString}
            handleUpdateFromEnterDate={this.handleUpdateFromEnterDate} />
        }
        {
          overlayChoosePrinterVisible &&
          <OverlayChoosePrinter
            printPurpose='REPRINT'
            cashierPrinters={cashierPrinters}
            handleUpdateFromChoosePrinter={this.handleUpdateFromChoosePrinter} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
              <ScrollView
                style={styles.selectionContainer}
                alwaysBounceVertical={false}
                showsVerticalScrollIndicator={false}>
                <TouchableOpacity
                  style={[styles.selection, selectedButton === 'ORDERS' && { backgroundColor: 'white' }]}
                  onPress={() => this.handleSelectButton('ORDERS')}>
                  <Text style={[styles.selectionText, selectedButton === 'ORDERS' && { color: theme.grey1 }]}>ORDERS</Text>
                </TouchableOpacity>
                <View style={styles.gap}></View>
                <TouchableOpacity
                  style={[styles.selection, selectedButton === 'KOTS' && { backgroundColor: 'white' }]}
                  onPress={() => this.handleSelectButton('KOTS')}>
                  <Text style={[styles.selectionText, selectedButton === 'KOTS' && { color: theme.grey1 }]}>KOTS</Text>
                </TouchableOpacity>
                <View style={styles.gap}></View>
                <TouchableOpacity
                  style={[styles.selection, selectedButton === 'RECEIPTS' && { backgroundColor: 'white' }]}
                  onPress={() => this.handleSelectButton('RECEIPTS')}>
                  <Text style={[styles.selectionText, selectedButton === 'RECEIPTS' && { color: theme.grey1 }]}>RECEIPTS</Text>
                </TouchableOpacity>
                <View style={styles.gap}></View>
                <TouchableOpacity
                  style={[styles.selection, selectedButton === 'RECEIPTSX' && { backgroundColor: 'white' }]}
                  onPress={() => this.handleSelectButton('RECEIPTSX')}>
                  <Text style={[styles.selectionText, selectedButton === 'RECEIPTSX' && { color: theme.grey1 }]}>RECEIPTS</Text>
                  <Text style={[styles.selectionText, { color: theme.paid }]}>UNKNOWN DATE</Text>
                </TouchableOpacity>
                <View style={styles.gap}></View>
                <TouchableOpacity
                  style={[styles.selection, selectedButton === 'DAYCLOSES' && { backgroundColor: 'white' }]}
                  onPress={() => this.handleSelectButton('DAYCLOSES')}>
                  <Text style={[styles.selectionText, selectedButton === 'DAYCLOSES' && { color: theme.grey1 }]}>DAY CLOSES</Text>
                </TouchableOpacity>
                <View style={styles.gap}></View>
              </ScrollView>
            </View>
          </View>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingRight: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                {
                  selectedButton !== 'RECEIPTSX' ?
                    <View style={styles.searchBox}>
                      <TextInput style={styles.searchBoxInputValue}
                        placeholder='ENTER SEARCH TEXT'
                        placeholderTextColor='rgba(255,255,255,0.2)'
                        value={searchText}
                        onChangeText={(searchText) => this.handleSearchText(searchText)}
                      >
                      </TextInput>
                      <TouchableOpacity style={styles.searchBoxButton}
                        onPress={() => {
                          this.setState({ searchText: '' }, () => this.handleSearchText(''))
                          Keyboard.dismiss()
                        }}
                      >
                        <AntDesign name='close' size={24} color={theme.grey4} />
                      </TouchableOpacity>
                    </View>
                    :
                    <View style={styles.searchBox}>
                      <TextInput style={styles.searchBoxInputValue}
                        placeholder='ENTER SEARCH TEXT'
                        placeholderTextColor='rgba(255,255,255,0.2)'
                        value={receiptsxText}
                        onChangeText={(text) => this.setState({ receiptsxText: text })}
                      >
                      </TextInput>
                      <TouchableOpacity style={styles.searchBoxButton}
                        onPress={() => {
                          this.handlePressSearch();
                          Keyboard.dismiss();
                        }}
                      >
                        <AntDesign name='search1' size={24} color={theme.grey4} />
                      </TouchableOpacity>
                    </View>
                }
                <View style={styles.searchBoxInfo}>
                  {
                    selectedButton !== 'RECEIPTSX' &&
                    <TouchableOpacity style={styles.searchBoxInfoLeft}
                      onPress={() => this.handlePressDate()}
                    >
                      <Text style={styles.searchBoxInfoText}>{selectedDateString}</Text>
                    </TouchableOpacity>
                  }
                  <View style={styles.searchBoxInfoRight}>
                    <Text style={styles.searchBoxInfoText}>{availableMasters.length} DOCS</Text>
                  </View>
                </View>
                <FlatList
                  style={{ flex: 1 }}
                  data={availableMasters}
                  keyExtractor={item => item.recKey.toString()}
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      style={[styles.grid, selectedMaster.recKey === item.recKey && { backgroundColor: theme.occupied }]}
                      onPress={() => this.handleSelectMaster(item)}
                    >
                      <View style={styles.gridFirst}>
                        <View style={styles.gridFirstLeft}>
                          <Text style={styles.gridFirstLeftText}>{item.tableId}</Text>
                        </View>
                        <View style={styles.gridFirstRight}>
                          <Text style={styles.gridFirstRightText}>{item.createUserId}</Text>
                        </View>
                      </View>
                      <View style={styles.gridSecond}>
                        <Text style={styles.gridSecondText}>
                          {item.docId}
                        </Text>
                      </View>
                      <View style={styles.gridThird}>
                        <View style={styles.gridThirdLeft}>
                        </View>
                        <View style={styles.gridThirdRight}>
                          <Text style={styles.gridThirdRightText}>{selectedButton === 'RECEIPTSX' && moment(item.createDate).format("YYYY-MM-DD ")}{moment(item.createDate).format("HH:mm")}</Text>
                        </View>
                      </View>
                    </TouchableOpacity>
                  )}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingLeft: 5 }]}>
              <View style={styles.gap}></View>
              {
                selectedButton === 'RECEIPTS' || selectedButton === 'RECEIPTSX' ?
                  <View style={styles.gridContainer}>
                    <View style={styles.overlayHeader}>
                      <Text style={styles.overlayTitleText}>{selectedMaster ? (selectedMaster.tableId || ' ') : ' '}</Text>
                      <Text style={styles.overlaySubtitleText}>{selectedMaster ? selectedMaster.docId : ' '}</Text>
                    </View>
                    <View style={styles.searchBoxInfo}>
                      <View style={styles.titleFirstContainer}>
                        <Text style={styles.titleText}>Qty</Text>
                      </View>
                      <View style={styles.titleSecondContainer}>
                        <Text style={styles.titleText}>Desc</Text>
                      </View>
                      <View style={styles.titleThirdContainer}>
                        <Text style={styles.titleText}>Price</Text>
                      </View>
                      <View style={styles.titleFourthContainer}>
                        <Text style={styles.titleText}>Total</Text>
                      </View>
                    </View>
                    <FlatList
                      style={{ flex: 1 }}
                      data={details}
                      keyExtractor={item => item.recKey.toString()}
                      renderItem={({ item }) => (
                        <TouchableOpacity style={[styles.tableItemContainer]}>
                          <View style={styles.tableItemFirstContainer}><Text style={styles.tableItemText}>{item.orderQty}</Text></View>
                          <TouchableOpacity style={styles.tableItemSecondContainer}>
                            <Text style={styles.tableItemText}>
                              {item.refRecKey ? '- ' : null}{item.menuName}
                            </Text>
                            {item.menuMod ? <Text style={styles.tableItemModText}>{item.menuMod}</Text> : null}
                          </TouchableOpacity>
                          <View style={styles.tableItemThirdContainer}>
                            <Text style={styles.tableItemText}>
                              {item.netPrice && item.netPrice.toFixed(2)}
                            </Text>
                          </View>
                          <View style={styles.tableItemFourthContainer}>
                            <Text style={styles.tableItemText}>
                              {item.lineTotal && item.lineTotal.toFixed(2)}
                            </Text>
                          </View>
                        </TouchableOpacity>
                      )}
                    />
                    <View style={styles.searchBoxInfo}>
                      <View style={styles.titleFirstContainer}>
                        <Text style={styles.titleText}>No.</Text>
                      </View>
                      <View style={[styles.titleSecondContainer, { flex: 10 }]}>
                        <Text style={styles.titleText}>PM ID</Text>
                      </View>
                      <View style={styles.titleFourthContainer}>
                        <Text style={styles.titleText}>Amount</Text>
                      </View>
                    </View>
                    <FlatList
                      style={{ flex: 1 }}
                      data={pays}
                      keyExtractor={item => item.recKey.toString()}
                      renderItem={({ item, index }) => (
                        <TouchableOpacity
                          style={[styles.tableItemContainer]}
                        >
                          <View style={styles.tableItemFirstContainer}><Text style={styles.tableItemText}>{index + 1}</Text></View>
                          <TouchableOpacity style={[styles.tableItemSecondContainer, { flex: 10 }]}
                          >
                            <Text style={styles.tableItemText}>
                              {item.pmId}
                            </Text>
                            {
                              item.payRef &&
                              <Text style={[styles.tableItemText, { color: theme.reserved }]}>
                                {item.payRef}
                              </Text>
                            }
                          </TouchableOpacity>
                          <View style={styles.tableItemFourthContainer}>
                            <Text style={styles.tableItemText}>
                              {item.payMoney && '$' + item.payMoney.toFixed(2)}
                            </Text>
                          </View>
                        </TouchableOpacity>
                      )}
                    />
                    <View style={{ flex: 1 }}>
                      <View style={{ flex: 1, marginHorizontal: 60, flexDirection: 'row' }}>
                        <View style={{ flex: 1, justifyContent: 'center' }}>
                          <Text style={styles.titleText}>SUBTOTAL</Text>
                        </View>
                        <View style={{ flex: 1, justifyContent: 'center', alignItems: 'flex-end' }}>
                          <Text style={styles.titleText}>{headers.beforeDisc && headers.beforeDisc.toFixed(2)}</Text>
                        </View>
                      </View>
                      <View style={{ flex: 1, marginHorizontal: 60, flexDirection: 'row' }}>
                        <View style={{ flex: 1, justifyContent: 'center' }}>
                          <Text style={styles.titleText}>DISCOUNT</Text>
                        </View>
                        <View style={{ flex: 1, justifyContent: 'center', alignItems: 'flex-end' }}>
                          <Text style={styles.titleText}>{headers.totalDisc && headers.totalDisc.toFixed(2)}</Text>
                        </View>
                      </View>
                      <View style={{ flex: 1, marginHorizontal: 60, flexDirection: 'row' }}>
                        <View style={{ flex: 1, justifyContent: 'center' }}>
                          <Text style={styles.titleText}>SVC CHARGE</Text>
                        </View>
                        <View style={{ flex: 1, justifyContent: 'center', alignItems: 'flex-end' }}>
                          <Text style={styles.titleText}>{headers.serviceCharge && headers.serviceCharge.toFixed(2)}</Text>
                        </View>
                      </View>
                      <View style={{ flex: 1, marginHorizontal: 60, flexDirection: 'row' }}>
                        <View style={{ flex: 1, justifyContent: 'center' }}>
                          <Text style={[styles.titleText, { color: 'white' }]}>TOTAL</Text>
                        </View>
                        <View style={{ flex: 1, justifyContent: 'center', alignItems: 'flex-end' }}>
                          <Text style={[styles.titleText, { color: 'white' }]}>{headers.grandTotal && headers.grandTotal.toFixed(2)}</Text>
                        </View>
                      </View>
                    </View>
                  </View>
                  :
                  <View style={styles.gridContainer}>
                    <TouchableOpacity style={styles.overlayHeader}
                      onPress={() => this.setState({ selectedMaster: '', details: [] })}
                    >
                      <Text style={styles.overlayTitleText}>{selectedMaster ? (selectedMaster.tableId || ' ') : ' '}</Text>
                      <Text style={styles.overlaySubtitleText}>{selectedMaster ? selectedMaster.docId : ' '}</Text>
                    </TouchableOpacity>
                    {
                      selectedButton === 'DAYCLOSES' ?
                        <View style={styles.searchBoxInfo}>
                          <View style={[styles.titleSecondContainer, { flex: 3 }]}>
                            <Text style={styles.titleText}>Payment Method</Text>
                          </View>
                          <View style={[styles.titleFourthContainer, { flex: 1 }]}>
                            <Text style={styles.titleText}>Amount</Text>
                          </View>
                        </View>
                        :
                        <View style={styles.searchBoxInfo}>
                          <View style={styles.titleFirstContainer}>
                            <Text style={styles.titleText}>Qty</Text>
                          </View>
                          <View style={styles.titleSecondContainer}>
                            <Text style={styles.titleText}>Desc</Text>
                          </View>
                          <View style={styles.titleThirdContainer}>
                            <Text style={styles.titleText}>{selectedButton !== 'KOTS' && 'Price'}</Text>
                          </View>
                          <View style={styles.titleFourthContainer}>
                            <Text style={styles.titleText}>{selectedButton !== 'KOTS' && 'Total'}</Text>
                          </View>
                        </View>
                    }
                    {
                      selectedButton === 'DAYCLOSES' ?
                        <FlatList
                          style={{ flex: 1 }}
                          data={details}
                          keyExtractor={item => item.recKey.toString()}
                          renderItem={({ item }) => (
                            <TouchableOpacity
                              style={[styles.tableItemContainer]}
                            >
                              <TouchableOpacity style={styles.tableItemSecondContainer}
                              >
                                <Text style={styles.tableItemText}>
                                  {item.pmId}
                                </Text>
                              </TouchableOpacity>
                              <View style={styles.tableItemFourthContainer}>
                                <Text style={styles.tableItemText}>
                                  ${item.systemAmt && item.systemAmt.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                                </Text>
                              </View>
                            </TouchableOpacity>
                          )}
                        />
                        :
                        <FlatList
                          style={{ flex: 1 }}
                          data={details}
                          keyExtractor={item => item.recKey.toString()}
                          renderItem={({ item }) => (
                            <TouchableOpacity
                              style={[styles.tableItemContainer]}
                            >
                              <View style={styles.tableItemFirstContainer}><Text style={styles.tableItemText}>{item.orderQty}</Text></View>
                              <TouchableOpacity style={styles.tableItemSecondContainer}
                              >
                                <Text style={styles.tableItemText}>
                                  {item.refRecKey ? '- ' : null}{item.menuName}
                                </Text>
                                {item.menuMod ? <Text style={styles.tableItemModText}>{item.menuMod}</Text> : null}
                              </TouchableOpacity>
                              <View style={styles.tableItemThirdContainer}>
                                <Text style={styles.tableItemText}>
                                  {selectedButton !== 'KOTS' && '$'}{item.netPrice && item.netPrice.toFixed(2)}
                                </Text>
                              </View>
                              <View style={styles.tableItemFourthContainer}>
                                <Text style={styles.tableItemText}>
                                  {selectedButton !== 'KOTS' && '$'}{item.lineTotal && item.lineTotal.toFixed(2)}
                                </Text>
                              </View>
                            </TouchableOpacity>
                          )}
                        />
                    }
                  </View>
              }
              <View style={styles.gap}></View>
            </View>
          </View>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={[styles.button, !selectedMaster && { opacity: 0.2 }]}
                onPress={() => this.handleReprint()}
                disabled={!selectedMaster}
              >
                <AntDesign
                  name='printer'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('reprint')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

