import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TouchableOpacity,
} from 'react-native';
import intl from 'react-intl-universal';
import { AntDesign } from '@expo/vector-icons';
import theme from '../assets/theme';

export default class OverlayReviseItem extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    const { selectedTable, selectedTableItem, tableItems } = this.props;
    console.log('in overlayreviseitem', this.props);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('table')} {selectedTable.tableId}</Text>
            <Text style={styles.overlaySubtitleText}>{intl.get('reviseitem')}</Text>
            <Text style={styles.overlayText}>{selectedTableItem.menuName.toUpperCase()}</Text>
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromReviseItem('voidItem')}>
              <AntDesign
                name='close'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('remove')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromReviseItem('duplicateItem')}>
              <AntDesign
                name='copy1'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('duplicate')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, selectedTableItem.confirmFlg === 'Y' && { opacity: 0.2 }]}
              disabled={selectedTableItem.confirmFlg === 'Y'}
              onPress={() => this.props.handleUpdateFromReviseItem('lineBreak')}>
              <AntDesign
                name='minus'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('linebreak')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromReviseItem('editItem')}>
              <AntDesign
                name='edit'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('edit')}</Text>
            </TouchableOpacity>
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={[styles.button, !(selectedTableItem.menuType === 'S' || selectedTableItem.menuType === 'X') && { opacity: 0.2 }]}
              disabled={!(selectedTableItem.menuType === 'S' || selectedTableItem.menuType === 'X')}
              onPress={() => this.props.handleUpdateFromReviseItem('revertItem')}>
              <AntDesign
                name='man'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('revert')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, (selectedTableItem.discFlg === 'N' && selectedTableItem.listPrice !== 0) && { opacity: 0.2 }]}
              disabled={selectedTableItem.discFlg === 'N' && selectedTableItem.listPrice !== 0}
              onPress={() => this.props.handleUpdateFromReviseItem('changePrice')}>
              <AntDesign
                name='exception1'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('changeprice')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, selectedTableItem.discFlg === 'N' && { opacity: 0.2 }]}
              disabled={selectedTableItem.discFlg === 'N'}
              onPress={() => this.props.handleUpdateFromReviseItem('lineDiscount')}>
              <AntDesign
                name='exception1'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('linedisc')}</Text>
            </TouchableOpacity>
          </View>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid, marginTop: 20 }]}
            onPress={() => this.props.handleUpdateFromReviseItem('')}
          >
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    minWidth: '50%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    margin: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  buttonsContainer: {
    paddingHorizontal: 10,
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
  },
  button: {
    height: 100,
    width: 100,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: theme.grey1,
    borderRadius: 5,
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
});