import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, FlatList,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';
import OverlayChoosePrinter from './OverlayChoosePrinter';

export default class OverlayMergeBill extends Component {

  constructor(props) {
    super(props);
    this.state = {
      overlaySearchText: '',
      tables: '',
      availableTables: '',
      selectedTargetTable: '',
      overlayChoosePrinterVisible: false,
    };
    this.handleOverlaySearchText = this.handleOverlaySearchText.bind(this);
    this.handleUpdateFromChoosePrinter = this.handleUpdateFromChoosePrinter.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    const { serviceEntry, home, selectedTable } = this.props;
    let url = serviceEntry + 'api/tables';
    const params = new URLSearchParams();
    params.append('shopId', home.shopId);
    params.append('statusFlg', 'C');
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        // console.log('tablesForFilter', response);
        // to state
        const filteredResponse = response.filter(el => {
          return el.tableId !== selectedTable.tableId;
        });
        this.setState({
          tables: filteredResponse,
          availableTables: filteredResponse,
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleOverlaySearchText(text) {
    const { tables, availableTables } = this.state;
    let availableTablesUpdate = [];
    this.setState({ overlaySearchText: text });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);

    availableTablesUpdate = tables;
    if (!text) {
      this.setState({
        availableTables: availableTablesUpdate
      });
      return;
    }
    availableTablesUpdate = availableTablesUpdate.filter(item => {
      return item.tableId.toUpperCase().includes(caseText) || item.sectionId.toUpperCase().includes(caseText);
    });
    this.setState({
      availableTables: availableTablesUpdate
    });
  }

  handleUpdateFromChoosePrinter(printer, purpose) {
    const { selectedTargetTable } = this.state;
    if (!printer) {
      this.setState({
        overlayChoosePrinterVisible: false,
      });
      return;
    }
    this.setState({
      overlayChoosePrinterVisible: false,
    });
    this.handleSubmit(selectedTargetTable, printer);
  }

  handleSubmit(item, printer) {
    const { home } = this.props;
    this.setState({
      selectedTargetTable: item,
    });
    const { cashierPrinters } = this.props;
    if (cashierPrinters.length > 1 && !printer && !home.defaultPrinter) {
      this.setState({
        overlayChoosePrinterVisible: true,
      });
      return;
    }
    this.props.handleUpdateFromMergeBill(item, home.defaultPrinter || printer);
  }

  render() {
    const { selectedTable, cashierPrinters } = this.props;
    const { overlaySearchText, availableTables, tables, overlayChoosePrinterVisible } = this.state;
    console.log('in overlaymergebill', selectedTable);

    return (
      <View style={styles.overlay}>
        {
          overlayChoosePrinterVisible &&
          <OverlayChoosePrinter
            printPurpose='MERGE BILL'
            cashierPrinters={cashierPrinters}
            handleUpdateFromChoosePrinter={this.handleUpdateFromChoosePrinter} />
        }
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>TABLE {selectedTable.tableId}</Text>
            <Text style={styles.overlaySubtitleText}>Merge Bill</Text>
          </View>
          <View style={styles.searchBoxWrapper}>
            <TouchableOpacity style={styles.overlayBackButton}
              onPress={() => this.props.handleUpdateFromMergeBill('')}
            >
              <AntDesign name='close' size={24} color='white' />
            </TouchableOpacity>
            <View style={styles.overlaySearchBox}>
              <TextInput style={styles.overlaySearchBoxInput}
                placeholder='ENTER SEARCH TEXT'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(searchText) => this.handleOverlaySearchText(searchText)}
                value={overlaySearchText}
              >
              </TextInput>
              <TouchableOpacity style={styles.overlaySearchBoxButton}
                onPress={() => {
                  this.setState({ overlaySearchText: '' }, () => this.handleOverlaySearchText(''))
                  Keyboard.dismiss()
                }}
              >
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
          </View>
          <View style={styles.searchBoxInfo}>
            <Text style={styles.searchBoxInfoText}>{availableTables.length} ITEMS</Text>
          </View>
          <FlatList
            style={[{ flex: 1 }]}
            showsVerticalScrollIndicator={false}
            data={availableTables}
            keyExtractor={item => item.recKey.toString()}
            renderItem={({ item }) => (
              <TouchableOpacity
                style={[styles.overlayGrid, item.dummy ? styles.hidden : null]}
                onPress={() => this.handleSubmit(item)}>
                <View style={styles.overlayGridFirst}>
                  <View style={styles.overlayGridFirstLeft}>
                    <Text style={styles.overlayGridFirstLeftText}>{item.sectionId}</Text>
                  </View>
                  <View style={styles.overlayGridFirstRight}>
                    <Text style={styles.overlayGridFirstRightText}>OCCUPIED</Text>
                  </View>
                </View>
                <View style={styles.overlayGridSecond}>
                  <Text style={styles.overlayGridSecondText}>
                    {item.tableId}
                  </Text>
                </View>
                <View style={styles.overlayGridThird}>
                  <View style={styles.overlayGridThirdLeft}>
                  </View>
                  <View style={styles.overlayGridThirdRight}>
                    <Text style={styles.overlayGridThirdRightText}></Text>
                  </View>
                </View>
              </TouchableOpacity>
            )}
          />
        </View>
      </View>
    );
  }
}

