import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TouchableOpacity,
} from 'react-native';
import intl from 'react-intl-universal';
import { AntDesign } from '@expo/vector-icons';
import theme from '../assets/theme';

export default class OverlayAdditionalFunctions extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    console.log('in overlayadditionalfunctions', this.props);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>{intl.get('additionalfunctions')}</Text>
            {/* <Text style={styles.overlayText}>Select an operation</Text> */}
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewReprint')}>
              <AntDesign
                name='printer'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('reprint')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button]}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewVoid')}>
              <AntDesign
                name='exception1'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('voidreceipts')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button]}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewCollect')}>
              <AntDesign
                name='gift'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('collection')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('openDrawer')}>
              <AntDesign
                name='laptop'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('opendrawer')}</Text>
            </TouchableOpacity>
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={[styles.button, { backgroundColor: theme.grey2 }]}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('logOut')}>
              <AntDesign
                name='doubleleft'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('logout')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewTrace')}>
              <AntDesign
                name='eyeo'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('trace')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewPrinters')}>
              <AntDesign
                name='fork'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('testprint')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, { backgroundColor: theme.grey2 }]}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('syncMasters')}>
              <AntDesign
                name='retweet'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('syncmasters')}</Text>
            </TouchableOpacity>
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewActivate')}>
              <AntDesign
                name='bulb1'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('activate')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewLimit')}>
              <AntDesign
                name='staro'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('limited')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewDayclose')}>
              <AntDesign
                name='isv'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('dayend')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.props.handleUpdateFromAdditionalFunctions('viewReports')}>
              <AntDesign
                name='solution1'
                color='white'
                size={32}
              />
              <Text style={styles.buttonText}>{intl.get('reports')}</Text>
            </TouchableOpacity>
          </View>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid, marginTop: 20 }]}
            onPress={() => this.props.handleUpdateFromAdditionalFunctions('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View >
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    minWidth: '50%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  buttonsContainer: {
    paddingHorizontal: 10,
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
  },
  button: {
    height: 100,
    width: 100,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: theme.grey1,
    borderRadius: 5,
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
});