import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Image, TextInput, Alert, ScrollView, FlatList, Platform, Keyboard } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Text } from '../assets/utility';
import theme from '../assets/theme';
import intl from 'react-intl-universal';

export default class MainMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      home: props.home,
      serviceEntry: props.serviceEntry,
      serviceEntryObj: props.serviceEntryObj,
      preference: props.preference,

      settings: props.settings,
      foldergrps: props.foldergrps,
      folders: props.folders,
      uniqueFolders: [...new Map(props.folders.map(v => [v.folderId, v])).values()],
      menus: props.menus,
      uniqueMenus: [...new Map(props.menus.map(v => [v.menuId, v])).values()],
      modBundles: props.modBundles,

      displayMode: '',
      displays: [],
      limiteds: [],

      searchText: '',
    };
  }

  componentDidMount() {
    console.log('mount mainmenu')
    const { foldergrps, folders, menus } = this.state;

    this.doGet();

    let displayMode = '';
    let displays = [];

    if (folders.filter(el => el.foldergrpId === foldergrps[0].foldergrpId).length === 1) {
      let found = folders.find(el => el.foldergrpId === foldergrps[0].foldergrpId);
      displayMode = 'MENU';
      displays = menus.filter(el => el.folderId === found.folderId && el.foldergrpId === found.foldergrpId);
    } else {
      displayMode = 'FOLDER';
      displays = folders.filter(el => el.foldergrpId === foldergrps[0].foldergrpId);
    }

    this.setState({
      selectedFoldergrp: foldergrps[0],
      displays,
      displayMode,
    });
  }

  doGet() {
    // real time fast changing data
    const { serviceEntry, home, } = this.state;
    let url = serviceEntry + 'api/restmenu-serve';
    const params = new URLSearchParams();
    // params.append('recKey', recKey);
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        this.setState({
          limiteds: response,
        });
      })
  }

  doPressFoldergrp(item) {
    const { folders, menus, } = this.state;
    let displayMode = '';
    let displays = [];

    this.doGet();

    if (folders.filter(el => el.foldergrpId === item.foldergrpId).length === 1) {
      let found = folders.find(el => el.foldergrpId === item.foldergrpId);
      displayMode = 'MENU';
      displays = menus.filter(el => el.folderId === found.folderId && el.foldergrpId === found.foldergrpId);
    } else {
      displayMode = 'FOLDER';
      displays = folders.filter(el => el.foldergrpId === item.foldergrpId);
    }

    this.setState({
      selectedFoldergrp: item,
      displays,
      displayMode,
      searchText: '',
    });
  }

  doPressFolder(item) {
    const { menus } = this.state;
    this.setState({
      displayMode: 'MENU',
      displays: menus.filter(el => el.folderId === item.folderId && el.foldergrpId === item.foldergrpId),
    })
  }

  doPressMenu(item, quick) {
    this.props.handlePressMenu(item, quick);
  }

  doSearchText(text) {
    const { menus, foldergrps } = this.state;
    this.setState({ searchText: text });
    if (!text) {
      this.setState({
        displayMode: 'MENU',
        displays: '',
        selectedFoldergrp: '',
      })
      this.doPressFoldergrp(foldergrps[0]);
      return;
    }

    let caseText = text.toUpperCase();
    let displaysUpdate = menus.filter(item => {
      return item.menuName.toUpperCase().includes(caseText) || item.menuId.toUpperCase().includes(caseText);
    });
    this.setState({
      displayMode: 'MENU',
      displays: displaysUpdate,
      selectedFoldergrp: '',
    });
  }

  render() {
    const { preference, settings, displayMode, displays, foldergrps, folders, uniqueFolders, uniqueMenus, menus, modBundles,
      searchText,
      selectedFoldergrp } = this.state;

    return (
      <View style={{ flex: 1, flexDirection: 'row', backgroundColor: theme.grey1, padding: 5 }}>
        <View style={{ flex: 1 }}>
          <View style={{ marginHorizontal: 10, marginTop: -5, height: 20, flexDirection: 'row', alignItems: 'flex-end', justifyContent: 'space-between' }}>
            <Text style={{ fontSize: 10, color: theme.white8 }}><Text style={{ fontSize: 10, color: theme.paid }}>{uniqueMenus.length}</Text> MENU <Text style={{ fontSize: 10, color: theme.reserved }}>{uniqueFolders.length}</Text> FOLDER <Text style={{ fontSize: 10, color: theme.reserved }}>{foldergrps.length}</Text> FOLDERGRP <Text style={{ fontSize: 10, color: theme.vacant }}>{modBundles.length}</Text> MODIFIER</Text>
          </View>
          <View style={styles.searchBox}>
            <TextInput style={styles.searchBoxInputValue}
              placeholder={`${intl.get('enter')?.toUpperCase()} ${intl.get('search')?.toUpperCase()} ${intl.get('text')?.toUpperCase()}`}
              placeholderTextColor='rgba(255,255,255,0.2)'
              value={searchText}
              onChangeText={(text) => this.doSearchText(text)} />
            <TouchableOpacity style={styles.searchBoxButton}
              onPress={() => {
                this.doSearchText('');
                Keyboard.dismiss();
              }}>
              <AntDesign name='close' size={24} color={theme.grey4} />
            </TouchableOpacity>
          </View>
          <View style={{ flex: 1, margin: 5, backgroundColor: theme.grey2, borderRadius: 5 }}>
            {
              displayMode === 'MENU' ?
                (
                  settings.find(el => el.setId === 'POSMODE')?.setString === 'A' ?
                    <FlatList
                      key={'MENU' + selectedFoldergrp?.foldergrpId}
                      style={{ flex: 1, padding: 2.5 }}
                      showsVerticalScrollIndicator={false}
                      numColumns={preference.mcolsize}
                      horizontal={false}
                      data={displays}
                      keyExtractor={(item, index) => item.recKey.toString()}
                      renderItem={({ item }) => this.renderMenu(item)}
                    />
                    :
                    <FlatList
                      key={'MENU' + selectedFoldergrp?.foldergrpId}
                      style={{ flex: 1, padding: 2.5 }}
                      showsVerticalScrollIndicator={false}
                      numColumns={preference.pcolsize}
                      horizontal={false}
                      data={displays}
                      keyExtractor={(item, index) => item.recKey.toString()}
                      renderItem={({ item }) => this.renderMenuB(item)}
                    />
                )
                :
                <FlatList
                  key={'FOLDER' + selectedFoldergrp?.foldergrpId}
                  style={{ flex: 1, padding: 2.5 }}
                  showsVerticalScrollIndicator={false}
                  numColumns={preference.mcolsize}
                  horizontal={false}
                  data={displays}
                  keyExtractor={(item, index) => item.recKey.toString()}
                  renderItem={({ item }) => this.renderFolder(item)}
                />
            }
          </View>
        </View>
        <View style={{ margin: 5, width: 120, }}>
          <View style={{ height: 90 }}>
            <ScrollView
              horizontal={true}
              pagingEnabled={true}
              showsHorizontalScrollIndicator={false}>
              {
                Platform.OS !== 'web' &&
                <TouchableOpacity
                  style={{ alignItems: 'center', justifyContent: 'center', height: 90, width: 120 }}
                  onPress={() => this.props.handlePressScan(true)}>
                  <MaterialCommunityIcons name="camera-iris" size={30} color="white" style={{ height: 28 }} />
                  <Text style={{ marginTop: 10, fontFamily: 'varela-round', color: 'white', fontSize: 10, textTransform: 'uppercase' }}>{intl.get('scan')}</Text>
                </TouchableOpacity>
              }
              <TouchableOpacity
                style={{ alignItems: 'center', justifyContent: 'center', height: 90, width: 120 }}
                onPress={() => this.props.handlePressScan()}>
                <MaterialCommunityIcons name="movie-filter" size={28} color="white" style={{ height: 28 }} />
                <Text style={{ marginTop: 10, fontFamily: 'varela-round', color: 'white', fontSize: 10, textTransform: 'uppercase' }}>{intl.get('barcode')}</Text>
              </TouchableOpacity>
            </ScrollView>
          </View>
          <ScrollView style={{ flex: 1, }}
            showsVerticalScrollIndicator={false}>
            {
              foldergrps.map((item, index) => (
                <TouchableOpacity key={index}
                  style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: item.foldergrpId === selectedFoldergrp?.foldergrpId ? 'white' : theme.grey1, borderRadius: 5, marginVertical: 5 }}
                  onPress={() => this.doPressFoldergrp(item)}>
                  <Text style={{ textAlign: 'center', color: item.foldergrpId === selectedFoldergrp?.foldergrpId ? 'black' : 'white' }}>{item.name}</Text>
                </TouchableOpacity>
              ))
            }
          </ScrollView>
        </View>
      </View>
    );
  }

  renderFolder(item) {
    const { preference, menus } = this.state;
    return (
      <View style={{ flex: 1 / preference.mcolsize }}>
        <TouchableOpacity
          style={{ margin: 2.5, height: 90, backgroundColor: 'black', alignItems: 'center', justifyContent: 'center' }}
          onPress={() => this.doPressFolder(item)}>
          <Text style={{ color: theme.paid, fontSize: 12 }}>{item.folderId}</Text>
          <Text style={{ color: 'white', paddingHorizontal: 10, textAlign: 'center', fontSize: 16 }}>{item.name}</Text>
          <Text style={{ color: theme.vacant }}>{menus.filter(el => el.folderId === item.folderId && el.foldergrpId === item.foldergrpId).length}</Text>
        </TouchableOpacity>
      </View>
    )
  }

  renderMenu(item) {
    const { preference, limiteds, searchText, foldergrps, folders } = this.state;

    const found = limiteds.find(el => el.recKey === item.recKey);

    return (
      <View style={{ flex: 1 / preference.mcolsize }}>
        <View style={[{ margin: 2.5, flexDirection: 'row', height: 90, backgroundColor: theme.grey4, }, item.statusFlg === 'B' && { opacity: 0.2 }]}>
          {
            preference.mquick === 'Y' &&
            <TouchableOpacity
              style={{ width: 50, backgroundColor: theme.grey3, justifyContent: 'center', alignItems: 'center' }}
              disabled={item.statusFlg === 'B'}
              onPress={() => this.doPressMenu(item, true)}>
              <AntDesign name="check" size={18} color={theme.grey1} />
              {/* {
                serviceEntry === 'https://www.epbrowser.com:8090/fnb-ws/' &&
                <Image
                  style={{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0 }}
                  source={{
                    uri: 'http://203.208.248.200:9090/image-proxy-epbsg/ASP/EPBSG/NORMAL/Shell/picture/' + item.stkId + '.jpg'
                    // uri: imageEntry + item.menuId + '.jpg'
                  }}
                />
              } */}
            </TouchableOpacity>
          }
          <TouchableOpacity
            style={{ flex: 1 }}
            disabled={item.statusFlg === 'B'}
            onPress={() => this.doPressMenu(item)}>
            <View style={{ flex: 1, padding: 10 }}>
              <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                <Text style={{ color: 'white', fontSize: 12, }}></Text>
                <Text style={[{ color: theme.reserved, fontSize: 12, }, Number(item.listPrice) === 0 && { color: 'white' }]}>${item.listPrice?.toFixed(2)}</Text>
              </View>
              <View style={{ flex: 1 }}>
                <Text style={{ color: 'white', }} numberOfLines={2}>{item.menuName}</Text>
              </View>
              <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                <Text style={{ color: theme.vacant, fontSize: 12, textTransform: 'uppercase' }}>{found ? `${Number(found.serveLimit - found.serveCount)} ${intl.get('left')}` : ' '}</Text>
                <Text style={{ color: theme.paid, fontSize: 12, }}>{item.menuId}</Text>
              </View>
            </View>
          </TouchableOpacity>
          {
            searchText.length > 0 &&
            <Text style={{ position: 'absolute', top: 0, left: 0, paddingHorizontal: 5, backgroundColor: 'black', color: 'white', fontSize: 10 }} numberOfLines={1}>{foldergrps.find(el => el.foldergrpId === item.foldergrpId)?.name} &gt; {folders.find(el => el.folderId === item.folderId)?.name}</Text>
          }
        </View>
      </View>
    )
  }

  renderMenuB(item) {
    const { preference, limiteds, serviceEntryObj, searchText, foldergrps, folders } = this.state;

    const found = limiteds.find(el => el.recKey === item.recKey);

    return (
      <View style={{ flex: 1 / (preference.pcolsize) }}>
        <TouchableOpacity style={{ margin: 2.5, }}
          disabled={item.statusFlg === 'B'}
          onPress={() => this.doPressMenu(item)}>
          <View style={{ backgroundColor: theme.grey3 }}>
            <Image
              style={{ aspectRatio: 1 }}
              source={{
                uri: serviceEntryObj?.restServiceUrl + 'epbm/api/image/stock?stkId=' + item.urlAddr + '&t=' + item.lastupdate
              }}
            />
            {
              searchText.length > 0 &&
              <Text style={{ position: 'absolute', bottom: 0, left: 0, paddingHorizontal: 5, backgroundColor: 'black', color: 'white', fontSize: 10 }} numberOfLines={1}>{foldergrps.find(el => el.foldergrpId === item.foldergrpId)?.name} &gt; {folders.find(el => el.folderId === item.folderId)?.name}</Text>
            }
          </View>
          <View style={[{ flexDirection: 'row', height: 70, backgroundColor: theme.grey4, }, item.statusFlg === 'B' && { opacity: 0.2 }]}>
            <View
              style={{ flex: 1 }}>
              <View style={{ flex: 1, padding: 10 }}>
                <View style={{ flex: 1 }}>
                  <Text style={{ color: 'white', }} numberOfLines={2}>{item.menuName}</Text>
                </View>
                <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                  <Text style={{ color: theme.vacant, fontSize: 12, textTransform: 'uppercase' }}>{found ? `${Number(found.serveLimit - found.serveCount)} ${intl.get('left')}` : ' '}</Text>
                  <Text style={[{ color: theme.reserved, fontSize: 12, }, Number(item.listPrice) === 0 && { color: 'white' }]}>${item.listPrice?.toFixed(2)}</Text>
                </View>
              </View>
            </View>
          </View>
        </TouchableOpacity>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  searchBox: {
    margin: 10,
    // marginTop: 25,
    marginBottom: 5,
    height: 50,
    borderRadius: 100,
    backgroundColor: 'black',
    flexDirection: 'row',
  },
  searchBoxInputValue: {
    flex: 1,
    fontFamily: 'varela-round',
    fontSize: 16,
    color: 'white',
    paddingHorizontal: 20,
    height: 50,
    borderRadius: 100,
  },
  searchBoxButton: {
    position: 'absolute',
    right: 0,
    top: 0,
    height: 50,
    width: 50,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 50,
  },
});
