import { Component } from 'react';
import { AntDesign, MaterialCommunityIcons, Feather, Ionicons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Image, Text, TextInput, Alert, FlatList, ScrollView, Platform } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { } from '../assets/utility';
import theme from '../assets/theme';
import intl from 'react-intl-universal';
import moment from 'moment';

export default class MainTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      home: props.home,
      serviceEntry: props.serviceEntry,
      preference: props.preference,
      settings: props.masters.settings,
      sections: props.masters.sections || [],
      tablemas: props.masters.tablemas,
      tables: [],
      floorWidth: null,

      selectedSection: null,
      focused: null,
    };
  }

  async componentDidMount() {
    await this.doRefresh(true);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.refresh && prevProps.refresh !== this.props.refresh) {
      console.log('componentDidUpdate', prevProps.refresh, this.props.refresh)
      this.setState({
        focused: null,
      })
      this.doRefresh();
    }
  }

  async doRefresh(initial) {
    const { sections, selectedSection } = this.state;
    if (initial) {
      if (sections.length > 0) {
        await this.doPressFloor();
      } else {
        await this.doPressSection({ sectionId: 'SYS' });
      }
    } else {
      if (selectedSection?.sectionId === 'FLOOR') {
        await this.doPressFloor();
      } else if (selectedSection) {
        await this.doPressSection(selectedSection);
      } else {
        this.doRefresh(true);
      }
    }
  }

  async doPressFloor() {
    const { serviceEntry, home, selectedTable } = this.state;
    let url = serviceEntry + 'api/tables';
    const params = new URLSearchParams();
    params.append('shopId', home.shopId);
    params.append('tableFlg', 'Y');
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        this.setState({
          tables: response,
          selectedSection: { sectionId: 'FLOOR' },
        });
      })
  }

  async doPressSection(item) {
    const { serviceEntry, home, selectedTable } = this.state;
    let url = serviceEntry + 'api/tables';
    const params = new URLSearchParams();
    params.append('shopId', home.shopId);

    if (item?.sectionId === 'OCCUPIED') {
      params.append('statusFlg', 'C');
      params.append('tableFlg', 'Y');
    } else if (item?.sectionId === 'RESERVED') {
      params.append('statusFlg', 'B');
    } else if (item?.sectionId === 'SYS') {
      params.append('sectionId', item.sectionId)
      params.append('statusFlg', 'C');
    } else {
      params.append('sectionId', item.sectionId)
    }

    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        this.setState({
          tables: response,
          selectedSection: { sectionId: item.sectionId },
        });
      })
  }

  doPressTable(item) {
    const { focused, selectedSection } = this.state;
    if (focused?.tableId === item.tableId) {
      this.setState({
        focused: null
      })
      this.doPressFocusedTable(item);
      return;
    }
    if (item.sectionId !== 'SYS') {
      this.doPressSection(selectedSection);
      this.setState({
        focused: item,
      })
    }
    this.props.handlePressTable(item);
  }

  doPressFloorTable(item) {
    const { tables, focused } = this.state;
    if (focused?.tableId === item.tableId) {
      this.setState({
        focused: null
      })
      this.doPressFocusedTable(tables.find(el => el.tableId === item.tableId));
      return;
    }
    this.doPressFloor()
    this.setState({
      focused: item
    })
    this.props.handlePressTable(tables.find(el => el.tableId === item.tableId));
  }

  doPressFocusedTable(item) {
    if (item.statusFlg === 'A') {
      this.props.handleActionTable(item, 'OPEN')
    } else if (item.statusFlg === 'C') {
      this.props.handleActionTable(item, 'EDIT')
    }
  }

  doTranslateTableColor(item) {
    if (!item) return theme.grey9;
    if (item.adhocItem) return theme.adhoc;
    if (item.sectionId === 'SYS') {
      if (item.statusFlg === 'D') {
        return theme.paid;
      } else {
        if (item.transType === 'A') {
          return theme.grey5;
        } else {
          return theme.deposit;
        }
      }
    } else {
      if (item.statusFlg === 'A') {
        return theme.vacant;
      } else if (item.statusFlg === 'B') {
        return theme.reserved;
      } else if (item.statusFlg === 'C') {
        return theme.occupied;
      } else if (item.statusFlg === 'D') {
        return theme.paid;
      } else if (item.statusFlg === 'E') {
        return theme.unavailable;
      }
    }
  }

  doTranslateFloorTableColor(item) {
    const { tables } = this.state;
    return this.doTranslateTableColor(tables.find(el => el.tableId === item.tableId))
  }

  doLayout(e) {
    this.setState({
      floorWidth: e.nativeEvent.layout.width / 16,
    })
  }

  render() {
    const { preference, sections, settings, tables, selectedSection } = this.state;
    // console.log(tables && tables[0])

    return (
      <View style={{ flex: 1, flexDirection: 'row', backgroundColor: theme.grey1, padding: 5 }}>
        <View style={{ flex: 1, margin: 5, backgroundColor: theme.grey2, borderRadius: 5 }}>
          {
            selectedSection?.sectionId === 'FLOOR' ?
              this.renderFloor()
              :
              <FlatList
                style={{ flex: 1, padding: 3 }}
                showsVerticalScrollIndicator={false}
                numColumns={preference.tcolsize}
                horizontal={false}
                data={selectedSection?.sectionId === 'SYS' ? [{ recKey: -999 }, ...tables] : tables}
                keyExtractor={(item, index) => item.recKey.toString()}
                renderItem={({ item, index }) => this.renderTable(item, index)}
                initialNumToRender={25}
              />
          }
        </View>
        <View style={{ margin: 5, width: 120, }}>
          <TouchableOpacity
            style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 80, borderRadius: 5, marginVertical: 5 }}
            onPress={() => this.doPressFloor()}>
            <MaterialCommunityIcons name="mapbox" size={30} color="white" style={{ height: 28 }} />
            <Text style={{ marginTop: 10, fontFamily: 'varela-round', color: 'white', fontSize: 10, textTransform: 'uppercase' }}>{intl.get('floorplan')}</Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: theme.paid, borderRadius: 5, marginVertical: 5 }}
            onPress={() => this.doPressSection({ sectionId: 'SYS' })}>
            <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', textTransform: 'uppercase' }}>{intl.get('takeaway')}</Text>
            {/* <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', fontSize: 10, textTransform: 'uppercase' }}>{intl.get('takeaway')}</Text> */}
          </TouchableOpacity>
          <TouchableOpacity
            style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: theme.reserved, borderRadius: 5, marginVertical: 5 }}
            onPress={() => this.doPressSection({ sectionId: 'RESERVED' })}>
            <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', textTransform: 'uppercase' }}>{intl.get('B')}</Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: theme.occupied, borderRadius: 5, marginVertical: 5 }}
            onPress={() => this.doPressSection({ sectionId: 'OCCUPIED' })}>
            <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', textTransform: 'uppercase' }}>{intl.get('C')}</Text>
          </TouchableOpacity>
          {
            sections &&
            <ScrollView style={{ flex: 1, }}
              showsVerticalScrollIndicator={false}>
              {
                sections.map((item, index) => (
                  <TouchableOpacity key={index}
                    style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: selectedSection?.sectionId === item.sectionId ? 'white' : theme.grey1, borderRadius: 5, marginVertical: 5 }}
                    onPress={() => this.doPressSection(item)}>
                    <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: selectedSection?.sectionId === item.sectionId ? 'black' : 'white' }}>{item.name}</Text>
                  </TouchableOpacity>
                ))
              }
            </ScrollView>
          }
        </View>
      </View >
    );
  }

  renderTable(item, index) {
    const { selectedSection, preference, focused } = this.state;
    if (selectedSection?.sectionId === 'SYS' && index === 0) {
      return (
        <TouchableOpacity style={{ flex: 1 / preference.tcolsize, aspectRatio: 1, }}
          onPress={() => this.props.handleActionTable(null, 'CREATE')}>
          <View style={{ flex: 1, margin: 3, backgroundColor: theme.grey4, justifyContent: 'center', alignItems: 'center' }}>
            <AntDesign name="pluscircleo" size={36} color={theme.grey1} />
          </View>
        </TouchableOpacity>
      )
    }
    return (
      <TouchableOpacity style={{ flex: 1 / preference.tcolsize, aspectRatio: 1, }}
        disabled={!item.tableId}
        onPress={() => this.doPressTable(item)}>
        <View style={{ flex: 1, margin: 3, backgroundColor: this.doTranslateTableColor(item), justifyContent: 'center', alignItems: 'center' }}>
          {
            item.seatTime &&
            <Text style={{ fontFamily: 'varela-round', position: 'absolute', top: 5, left: 5, color: 'black', fontSize: theme.size * 4 }}>{moment(item.seatTime).format('HH:mm')}</Text>
          }
          {
            (item.sectionId !== 'SYS' && item.pax > 0) &&
            <Text style={{ fontFamily: 'varela-round', position: 'absolute', top: 5, right: 5, color: 'white', fontSize: theme.size * 4 }}>{item.pax} <Feather color='white' name='users' size={theme.size * 4} /></Text>
          }
          <Text style={{ fontFamily: 'varela-round', color: 'rgba(0,0,0,0.8)', fontSize: 22, letterSpacing: 1 }}>{item.tableId}</Text>
          {
            item.vipName &&
            <Text style={[styles.tableLowerBand]}>{item.vipName}</Text>
          }
          {
            item.grandTotal > 0 &&
            <Text style={{ fontFamily: 'varela-round', position: 'absolute', bottom: 5, right: 5, color: 'black', fontSize: theme.size * 4 }}>${item.grandTotal}</Text>
          }
        </View>
        {
          focused?.tableId === item.tableId &&
          <View style={{ position: 'absolute', top: 3, left: 3, right: 3, bottom: 3, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}>
            {
              (item.statusFlg === 'A' || item.statusFlg === 'C') &&
              <Text style={{ fontFamily: 'varela-round', color: 'black', fontSize: 10, textTransform: 'uppercase' }}>{intl.get('press')}</Text>
            }
            <Text style={{ fontFamily: 'varela-round', color: 'rgba(0,0,0,0.8)', fontSize: 22, letterSpacing: 1 }}>{item.tableId}</Text>
            {
              item.statusFlg === 'A' ?
                <Text style={{ fontFamily: 'varela-round', color: 'black', fontSize: 10, textTransform: 'uppercase' }}>{intl.get('open')}</Text>
                : item.statusFlg === 'C' ?
                  <Text style={{ fontFamily: 'varela-round', color: 'black', fontSize: 10, textTransform: 'uppercase' }}>{intl.get('edit')}</Text>
                  : null
            }
          </View>
        }
      </TouchableOpacity>
    )
  }

  renderFloor() {
    const { home, tablemas, tables, floorWidth, focused } = this.state;
    return (
      <ScrollView style={{ flex: 1 }}
        showsVerticalScrollIndicator={false}>
        <View style={{ flex: 1, aspectRatio: 1, backgroundColor: theme.grey1, overflow: 'hidden', borderRadius: 5 }}>
          <View style={{ position: 'absolute', left: 0, right: 0, top: 0, bottom: 0 }}>
            <View style={{ flex: 1, justifyContent: 'space-between' }}>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ height: 6, backgroundColor: theme.grey2 }}></View>
            </View>
          </View>
          <View style={{ position: 'absolute', left: 0, right: 0, top: 0, bottom: 0 }}>
            <View style={{ flex: 1, flexDirection: 'row', justifyContent: 'space-between' }}>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
              <View style={{ width: 6, backgroundColor: theme.grey2 }}></View>
            </View>
          </View>
          <View style={{ flex: 1, margin: 3, }} onLayout={(e) => this.doLayout(e)}>
            {
              floorWidth && tablemas.map(item =>
                <View key={item.recKey}
                  style={{ position: 'absolute', left: item.xaxis * floorWidth, top: Number(item.yaxis * floorWidth), width: item.width * floorWidth, height: Number(item.height * floorWidth) }}>
                  {
                    item.shape === 'B' ?
                      <TouchableOpacity style={{ flex: 1, alignItems: 'center', justifyContent: 'center', borderRadius: item.shape === 'C' ? floorWidth * item.height : 0 }}
                        onPress={() => this.doPressFloorTable(item)}>
                        <View style={{ height: Math.sqrt(0.5) * (item.width * floorWidth - 6), width: Math.sqrt(0.5) * (item.width * floorWidth - 6), backgroundColor: focused?.tableId === item.tableId ? 'white' : this.doTranslateFloorTableColor(item), transform: [{ rotateZ: '45deg' }], justifyContent: 'center', alignItems: 'center' }}>
                          <Text style={{ fontFamily: 'varela-round', color: theme.black8, fontSize: 20, transform: [{ rotateZ: '-45deg' }] }}>{item.tableId}</Text>
                        </View>
                      </TouchableOpacity>
                      :
                      <TouchableOpacity style={{ flex: 1, margin: 3, backgroundColor: focused?.tableId === item.tableId ? 'white' : this.doTranslateFloorTableColor(item), alignItems: 'center', justifyContent: 'center', borderRadius: item.shape === 'C' ? floorWidth * item.height : 0 }}
                        onPress={() => this.doPressFloorTable(item)}>
                        <Text style={{ fontFamily: 'varela-round', color: theme.black8, fontSize: 20 }}>{item.tableId}</Text>
                      </TouchableOpacity>
                  }
                  {/* {
                    // top and bottom chairs
                    (item.width > item.height && item.shape === 'A') &&
                    <>
                      <View style={{ position: 'absolute', top: -3, left: 0, right: 0, height: 6, flexDirection: 'row', }}>
                        {
                          [...Array(item.width)].map((el, index) =>
                            <View key={index} style={{ flex: 1 / item.width, marginHorizontal: 10, backgroundColor: theme.grey4, borderTopLeftRadius: 3, borderTopRightRadius: 3, }}>

                            </View>)
                        }
                      </View>
                      <View style={{ position: 'absolute', bottom: -3, left: 0, right: 0, height: 6, flexDirection: 'row', }}>
                        {
                          [...Array(item.width)].map((el, index) =>
                            <View key={index} style={{ flex: 1 / item.width, marginHorizontal: 10, backgroundColor: theme.grey4, borderBottomLeftRadius: 3, borderBottomRightRadius: 3, }}>

                            </View>)
                        }
                      </View>
                    </>
                  } */}
                  {/* {
                    // left and right chairs
                    (item.width < item.height && item.shape === 'A') &&
                    <>
                      <View style={{ position: 'absolute', left: -3, top: 0, bottom: 0, width: 6, }}>
                        {
                          [...Array(item.height)].map((el, index) =>
                            <View key={index} style={{ flex: 1 / item.height, marginVertical: 10, backgroundColor: theme.grey4, borderTopLeftRadius: 3, borderBottomLeftRadius: 3, }}>

                            </View>)
                        }
                      </View>
                      <View style={{ position: 'absolute', right: -3, top: 0, bottom: 0, width: 6, }}>
                        {
                          [...Array(item.height)].map((el, index) =>
                            <View key={index} style={{ flex: 1 / item.height, marginVertical: 10, backgroundColor: theme.grey4, borderTopRightRadius: 3, borderBottomRightRadius: 3, }}>

                            </View>)
                        }
                      </View>
                    </>
                  } */}
                </View>
              )
            }
          </View>


        </View>
        <View style={{ margin: 3, marginTop: -3, flexWrap: 'wrap', flexDirection: 'row' }}>
          {
            floorWidth && tables.filter(el => !tablemas.some(obj => obj.tableId === el.tableId)).map((item, index) =>
              <View key={index} style={{ width: floorWidth * 4, aspectRatio: 4 }}>
                <TouchableOpacity style={{ flex: 1, margin: 3, backgroundColor: focused?.tableId === item.tableId ? 'white' : this.doTranslateFloorTableColor(item), justifyContent: 'center', alignItems: 'center' }}
                  onPress={() => this.doPressFloorTable(item)}>
                  <Text style={{ fontFamily: 'varela-round', color: theme.black8, fontSize: 20 }}>{item.tableId}</Text>
                </TouchableOpacity>
              </View>
            )
          }
        </View>
      </ScrollView>
    )
  }
}

const styles = StyleSheet.create({
  textInput: {
    color: 'white',
    height: 60,
    borderRadius: 30,
    width: '90%',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: 120,
    textAlign: 'center',
    fontFamily: 'varela-round',
    fontSize: 24,
  },
  onetwo: {
    position: 'absolute', top: '0', left: '0', height: '12.5%', width: '12.5%',
  },
  tableLowerBand: {
    fontFamily: 'varela-round',
    width: '100%',
    paddingVertical: 5,
    backgroundColor: theme.black8,
    justifyContent: 'center',
    alignItems: 'center',
    color: 'white',
    fontSize: 12,
    textAlign: 'center',
    textTransform: 'uppercase',
  },
});
