import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, FlatList,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';

export default class OverlayMergeTable extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.home,
      serviceEntry: props.serviceEntry,
      selectedTable: props.selectedTable,

      overlaySearchText: '',
      tables: '',
      availableTables: '',
      mergeTables: [],
    };
    this.handleOverlaySearchText = this.handleOverlaySearchText.bind(this);
    this.handleSelectTable = this.handleSelectTable.bind(this);
  }

  componentDidMount() {
    const { serviceEntry, home } = this.state;
    let url = serviceEntry + 'api/tables';
    const params = new URLSearchParams();
    params.append('shopId', home.shopId);
    params.append('statusFlg', 'A');
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        // console.log('tablesForFilter', response);
        // to state
        this.setState({
          tables: response.filter(el => el.sectionId !== 'SYS'),
          availableTables: response.filter(el => el.sectionId !== 'SYS'),
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleOverlaySearchText(text) {
    const { tables, availableTables } = this.state;
    let availableTablesUpdate = [];
    this.setState({ overlaySearchText: text });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);
    console.log('available', this.state.availableTables);

    availableTablesUpdate = tables;
    if (!text) {
      this.setState({
        availableTables: availableTablesUpdate
      });
      return;
    }
    availableTablesUpdate = availableTablesUpdate.filter(item => {
      return item.tableId.toUpperCase().includes(caseText) || item.sectionId.toUpperCase().includes(caseText);
    });
    this.setState({
      availableTables: availableTablesUpdate
    });
  }

  handleSelectTable(item) {
    const { mergeTables } = this.state;
    let mergeTablesUpdate = mergeTables;
    if (!mergeTables.some(el => el.tableId === item.tableId)) {
      mergeTablesUpdate.push(item);
    } else {
      mergeTablesUpdate = mergeTablesUpdate.filter(el => el.tableId !== item.tableId);
    }
    this.setState({
      mergeTables: mergeTablesUpdate,
    });
  }

  handlePressSelection(item) {
    const { mergeTables } = this.state;
    const mergeTablesUpdate = mergeTables;
    mergeTablesUpdate = mergeTablesUpdate.filter(el => el.tableId !== item.tableId);
    this.setState({
      mergeTables: mergeTablesUpdate,
    });
  }

  render() {
    const { selectedTable, overlaySearchText, availableTables, tables, mergeTables } = this.state;
    console.log('in overlaymergetable, state', this.state);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>TABLE {selectedTable.tableId}</Text>
            <Text style={styles.overlaySubtitleText}>Merge Table</Text>
          </View>
          <View style={styles.selectionContainer}>
            {mergeTables.map(item =>
              <TouchableOpacity
                key={item.recKey}
                style={[styles.modalAvailableMod]}
                onPress={() => this.handleSelectTable(item)}
              >
                <Text style={[styles.modalAvailableModText]}>{item.tableId}</Text>
              </TouchableOpacity>
            )}
          </View>
          <View style={styles.searchBoxWrapper}>
            <TouchableOpacity style={styles.overlayBackButton}
              onPress={() => this.props.handleUpdateFromMergeTable([])}>
              <AntDesign name='close' size={24} color='white' />
            </TouchableOpacity>
            <View style={styles.overlaySearchBox}>
              <TextInput style={styles.overlaySearchBoxInput}
                placeholder='ENTER SEARCH TEXT'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(searchText) => this.handleOverlaySearchText(searchText)}
                value={overlaySearchText}
              />
              <TouchableOpacity style={styles.overlaySearchBoxButton}
                onPress={() => {
                  this.setState({ overlaySearchText: '' }, () => this.handleOverlaySearchText(''))
                  Keyboard.dismiss()
                }}>
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
          </View>
          <View style={styles.searchBoxInfo}>
            <Text style={styles.searchBoxInfoText}>{availableTables.length} ITEMS</Text>
          </View>
          <FlatList
            style={[{ flex: 1 }]}
            showsVerticalScrollIndicator={false}
            data={availableTables}
            keyExtractor={item => item.recKey.toString()}
            renderItem={({ item }) => (
              <TouchableOpacity
                style={styles.overlayGrid}
                onPress={() => this.handleSelectTable(item)}>
                <View style={styles.overlayGridFirst}>
                  <View style={styles.overlayGridFirstLeft}>
                    <Text style={styles.overlayGridFirstLeftText}>{item.sectionId}</Text>
                  </View>
                  <View style={styles.overlayGridFirstRight}>
                    <Text style={styles.overlayGridFirstRightText}>VACANT</Text>
                  </View>
                </View>
                <View style={styles.overlayGridSecond}>
                  <Text style={styles.overlayGridSecondText}>
                    {item.tableId}
                  </Text>
                </View>
                <View style={styles.overlayGridThird}>
                  <View style={styles.overlayGridThirdLeft}>
                  </View>
                  <View style={styles.overlayGridThirdRight}>
                    <Text style={styles.overlayGridThirdRightText}></Text>
                  </View>
                </View>
              </TouchableOpacity>
            )}
          />
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.vacant }]}
            onPress={() => this.props.handleUpdateFromMergeTable(mergeTables)}>
            <Text style={styles.overlayOptionText}>{intl.get('submit')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

