import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions, Platform,
  FlatList, ScrollView,
} from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { AntDesign } from '@expo/vector-icons';
import OverlayAlert from './OverlayAlert';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import OverlayServiceEntry from './OverlayServiceEntry';
import theme from '../assets/theme';
import intl from 'react-intl-universal';

export default class Register extends Component {
  constructor(props) {
    super(props);
    this.state = {
      savedEntries: [],
      serviceEntry: '',
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
      overlayProcedureMessageVisible: false,
    };
    this.handleUpdateFromAlert = this.handleUpdateFromAlert.bind(this);
    this.handleUpdateFromServiceEntry = this.handleUpdateFromServiceEntry.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  async componentDidMount() {
    const serviceEntry = await AsyncStorage.getItem('serviceEntry');
    let savedEntries = await AsyncStorage.getItem('savedEntries');
    if (savedEntries) {
      savedEntries = JSON.parse(savedEntries);
    } else {
      savedEntries = [];
    }
    this.setState({
      serviceEntry,
      savedEntries,
    })
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromAlert(text) {
    const { selectedItem } = this.state;
    this.setState({
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    });
    if (!text) {
      return;
    }
    if (text === 'doDeleteEntry') {
      this.doDeleteEntry(selectedItem);
    } else if (text === 'doSetEntry') {
      this.doSetEntry(selectedItem);
    }
  }

  handleUpdateFromServiceEntry(text) {
    this.setState({ overlayServiceEntryVisible: false });
    if (!text) {
      return;
    }
    if (text.includes(':8085')) {
      // assume local
      let found = {
        epbId: 'IP:PORT',
        name: 'IP:PORT',
        bistroServiceUrl: 'http://' + text + '/',
      }
      this.doSaveEntry(found);
    } else {
      // EPB ID
      fetch('http://203.208.248.199:9090/beacon/api/locate?epbId=' + text, {
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
      })
        .then(response => {
          if (!response.ok) {
            throw response;
          } else {
            return response.json();
          }
        })
        .then(response => {
          console.log('response', response);
          this.doSaveEntry(response);
        })
        .catch(error => {
          console.log('error', error);
          this.setState({
            overlayProcedureMessageVisible: 'INVALID EPB ID',
          })
        });
    }
  }

  doSaveEntry(item) {
    const { savedEntries } = this.state;
    let savedEntriesUpdate = savedEntries;
    if (!item.bistroServiceUrl) {
      this.setState({
        overlayProcedureMessageVisible: 'MISSING BISTRO SERVICE URL',
      })
      return;
    }
    if (savedEntriesUpdate.some(el => (el.epbId === item.epbId))) {
      savedEntriesUpdate = savedEntriesUpdate.filter(el => el.epbId !== item.epbId)
    }
    savedEntriesUpdate.unshift(item);
    this.setState({
      savedEntries: savedEntriesUpdate,
      serviceEntry: item.bistroServiceUrl,
    })
    AsyncStorage.setItem('savedEntries', JSON.stringify(savedEntriesUpdate))
    AsyncStorage.setItem('serviceEntry', item.bistroServiceUrl)
    AsyncStorage.setItem('serviceEntryObj', JSON.stringify(item))
  }

  doPressAdd() {
    this.setState({
      overlayServiceEntryVisible: true,
    })
  }

  doPressEntry(item) {
    this.setState({
      overlayAlertInputValue: {
        title: intl.get('action'),
        subtitle: item.name,
        text: '',
        ok: false,
        cancel: true,
        options: [
          { text: 'SET ENTRY', handleText: 'doSetEntry' },
          { text: 'DELETE ENTRY', handleText: 'doDeleteEntry' }
        ],
      },
      overlayAlertVisible: true,
      selectedItem: item,
    });
  }

  doDeleteEntry(item) {
    const { savedEntries } = this.state;
    let savedEntriesUpdate = savedEntries;
    savedEntriesUpdate = savedEntriesUpdate.filter(el => el.epbId !== item.epbId)
    console.log(savedEntriesUpdate)
    this.setState({
      savedEntries: savedEntriesUpdate,
    })
    AsyncStorage.setItem('savedEntries', JSON.stringify(savedEntriesUpdate))
  }

  doSetEntry(item) {
    this.setState({
      serviceEntry: item.bistroServiceUrl,
    })
    AsyncStorage.setItem('serviceEntry', item.bistroServiceUrl)
    AsyncStorage.setItem('serviceEntryObj', JSON.stringify(item))
  }

  render() {
    const { navigation } = this.props;
    const { preference, savedEntries, serviceEntry,
      overlayServiceEntryVisible,
      overlayProcedureMessageVisible, overlayAlertVisible, overlayAlertInputValue, } = this.state;
    console.log('render register', savedEntries);

    return (
      <View style={styles.container}>
        {
          overlayAlertVisible &&
          <OverlayAlert
            input={overlayAlertInputValue}
            handleUpdateFromAlert={this.handleUpdateFromAlert} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        {
          overlayServiceEntryVisible &&
          <OverlayServiceEntry
            handleUpdateFromServiceEntry={this.handleUpdateFromServiceEntry} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
              <View style={styles.selectionContainer}>
              </View>
            </View>
          </View>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingRight: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.gap}></View>
                <ScrollView
                  style={{ flex: 1, }}
                  showsVerticalScrollIndicator={false}>

                  {
                    savedEntries.map((item, index) =>
                      <TouchableOpacity
                        key={index.toString()}
                        style={styles.grid}
                        onPress={() => this.doPressEntry(item)}>
                        <View style={styles.gridFirst}>
                          <Text style={styles.gridFirstLeftText}>{item.epbId || '-'}</Text>
                          <Text style={styles.gridFirstRightText}>{item.bistroServiceUrl || '-'}</Text>
                        </View>
                        <View style={styles.gridSecond}>
                          <Text style={styles.gridSecondText}>{item.name || 'IP:PORT'}</Text>
                          <Text style={[styles.gridSecondText, { color: theme.reserved }]}>{preference?.tcolsize}</Text>
                        </View>
                        <View style={[styles.gridFirst, { marginBottom: 5 }]}>
                          <Text style={styles.gridFirstLeftText}>{item.restServiceUrl || '-'}</Text>
                        </View>
                        <View style={styles.gridThird}>
                          <Text style={[styles.gridThirdRightText, { color: theme.paid }]}></Text>
                          {
                            item.bistroServiceUrl === serviceEntry ?
                              <Text style={[styles.gridThirdRightText, { color: theme.reserved }]}>{intl.get('active')}</Text>
                              :
                              <Text style={[styles.gridThirdRightText, { color: theme.paid }]}>{intl.get('inactive')}</Text>
                          }
                        </View>
                      </TouchableOpacity>
                    )
                  }




                </ScrollView>
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingLeft: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.gap}></View>
                <ScrollView
                  style={{ flex: 1, }}
                  showsVerticalScrollIndicator={false}>
                </ScrollView>
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => this.doPressAdd()}>
                <AntDesign
                  name='pluscircleo'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('add')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

