import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TouchableOpacity, ScrollView,
} from 'react-native';
import theme from '../assets/theme';

export default class OverlayViewSplitted extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    const { receipts } = this.props;
    console.log('in overlayalert', this.props);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>VIEW</Text>
            <Text style={styles.overlaySubtitleText}>Splitted Receipts</Text>
            <Text style={styles.overlayText}></Text>
          </View>
          <ScrollView style={styles.overlayScroll}>
            {
              receipts.map(item => (
                <View style={styles.inputRow}
                  key={item.recKey}
                >
                  <View style={styles.inputRowLeft}>
                    <Text style={styles.inputRowTitleText}>RECEIPT</Text>
                    <Text style={styles.inputRowSubtitleText}>{item.docId}</Text>
                  </View>
                  <View style={styles.inputRowRight}>
                    <Text style={styles.textInput}>{item.grandTotal ? item.grandTotal.toFixed(2) : null}</Text>
                    <Text style={styles.percentageText}></Text>
                  </View>
                </View>
              ))
            }
          </ScrollView>
          <TouchableOpacity style={styles.overlayOption}
            onPress={() => this.props.handleUpdateFromViewSplitted('')}
          >
            <Text style={styles.overlayOptionText}>OK</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  overlayScroll: {
    height: 280, // 4 times of (60 + 10) inputRow class
  },
  inputRow: {
    height: 60,
    // backgroundColor: 'white',
    marginVertical: 5,
    marginHorizontal: 40,
    flexDirection: 'row',
  },
  inputRowLeft: {
    flex: 1,
    justifyContent: 'center',
  },
  inputRowRight: {
    flex: 1,
    borderRadius: 5,
    flexDirection: 'row',
    backgroundColor: theme.grey1,
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  inputRowTitleText: {
    fontFamily: 'varela-round',
    fontSize: 12,
    color: theme.paid,
  },
  inputRowSubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
  },
  textInput: {
    width: '75%',
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    textAlign: 'right',
  },
  percentageText: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    opacity: 0.6,
    paddingHorizontal: 10,
  },
});