import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions,
  FlatList, ScrollView,
} from 'react-native';
import { AntDesign } from '@expo/vector-icons';
import { debug } from '../assets/utility';
import theme from '../assets/theme';
import intl from 'react-intl-universal';
import OverlayAlert from './OverlayAlert';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import OverlayRequestAccess from './OverlayRequestAccess';

export default class Activate extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      masters: props.route.params.masters,

      privileges: props.route.params.masters.privileges,
      settings: props.route.params.masters.settings,
      menus: [...new Map(props.route.params.masters.menus.map(v => [v.menuId, v])).values()],
      availableMenus: [...new Map(props.route.params.masters.menus.map(v => [v.menuId, v])).values()],

      updateds: [],
      searchText: '',
      displayMode: 'ALL',

      overlayRequestPrivilegeVisible: false,
      overlayProcedureMessageVisible: false,
      overlayAlertVisible: false,
      overlayAlertInputValue: '',

      loading: false,
    };
    this.handleSelectMenu = this.handleSelectMenu.bind(this);
    this.handleSelectUpdated = this.handleSelectUpdated.bind(this);
    this.handlePressDisplayMode = this.handlePressDisplayMode.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleUpdateMenu = this.handleUpdateMenu.bind(this);
    this.handleSearchText = this.handleSearchText.bind(this);
    this.handleUpdateFromRequestAccess = this.handleUpdateFromRequestAccess.bind(this);
    this.handleUpdateFromAlert = this.handleUpdateFromAlert.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  componentDidMount() {
  }

  handleSelectMenu(item) {
    const { menus, updateds } = this.state;
    if (updateds.some(el => el.recKey === item.recKey)) {
      return;
    } else {
      let updatedsUpdate = updateds;
      updatedsUpdate.push(item);
      this.setState({
        updateds: updatedsUpdate,
      });
    }
  }

  handleSelectUpdated(item) {
    const { menus, updateds } = this.state;
    let updatedsUpdate = updateds;
    updatedsUpdate = updatedsUpdate.filter(el => el.recKey !== item.recKey);
    this.setState({
      updateds: updatedsUpdate,
    })
  }

  handleSubmit() {
    const { updateds } = this.state;
    let options = [];
    options.push({ text: 'Yes', handleText: 'handleUpdateMenu' });
    this.setState({
      overlayAlertInputValue: {
        title: intl.get('action'),
        subtitle: 'Update Menu',
        text: 'Update ' + updateds.length + ' menus?',
        ok: false,
        cancel: true,
        options: options,
      },
      overlayAlertVisible: true,
    });
  }

  handleUpdateMenu(superUserId) {
    const { navigation } = this.props;
    const { home, settings, privileges, serviceEntry, updateds } = this.state;
    if (!(home.adminFlg === 'Y' || privileges.some(el => el.priId === 'UPDATEMENU')) && !superUserId) {
      this.setState({
        overlayRequestAccessVisible: true,
      });
      return;
    }

    let updatedsUpdate = updateds.map(el => ({
      recKey: el.recKey,
      newStatusFlg: el.statusFlg === 'A' ? 'B' : 'A',
    }));

    const restmenuRecKeys = updatedsUpdate.reduce((init, el) => { return init + el.recKey + ',' + el.newStatusFlg + ','; }, '');

    let url = serviceEntry + 'api/update-restmenu';
    const body = {
      userId: home.userId,
      orgId: home.orgId,
      shopId: home.shopId,
      restmenuRecKeys: restmenuRecKeys,
    };

    this.setState({
      loading: true,
    });

    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post update-restmenu successful');
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Update Menu',
            home.userName,
            response.message,
            '',
            '',
            updateds.map(el => ({
              menuId: el.menuId,
              newStatusFlg: el.statusFlg === 'A' ? 'B' : 'A',
            })).reduce((init, el) => { return init + el.menuId + ',' + el.newStatusFlg + ','; }, ''),
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
            loading: false,
          })
          return;
        }
        navigation.navigate('Login', { sync: new Date() });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });

  }

  handleSearchText(text) {
    const { menus, availableMenus, searchText } = this.state;
    let availableMenusUpdate = [];
    this.setState({
      searchText: text,
      displayMode: 'ALL',
    });
    let caseText = text.toUpperCase();

    availableMenusUpdate = menus;

    if (!text) {
      this.setState({
        availableMenus: availableMenusUpdate
      });
      return;
    }
    availableMenusUpdate = availableMenusUpdate.filter(item => {
      return item.menuName.toUpperCase().includes(caseText) || item.menuId.toUpperCase().includes(caseText);
    });
    this.setState({
      availableMenus: availableMenusUpdate
    });
  }

  handlePressDisplayMode() {
    const { displayMode, menus } = this.state;
    if (displayMode === 'INACTIVE ONLY') {
      this.setState({
        availableMenus: menus,
        searchText: '',
        displayMode: 'ALL',
      });
    } else if (displayMode === 'ALL') {
      this.setState({
        availableMenus: menus.filter(el => el.statusFlg === 'B'),
        searchText: '',
        displayMode: 'INACTIVE ONLY',
      });
    }
  }

  handleUpdateFromRequestAccess(text) {
    const { home, serviceEntry, settings } = this.state;
    if (!text) {
      this.setState({
        overlayRequestAccessVisible: false,
      });
      return;
    }
    let url = serviceEntry + 'api/super-user';
    const body = {
      locId: home.locId,
      accessCode: text,
      priId: 'UPDATEMENU',
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post super-user successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Request Access UPDATEMENU',
            home.userName,
            response.message,
            '',
            '',
            response.userId,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          overlayRequestAccessVisible: false,
        });
        this.handleUpdateMenu(response.userId);
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromAlert(text) {
    this.setState({
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    });
    if (!text) {
      return;
    }
    if (text === 'handleUpdateMenu') {
      this.handleUpdateMenu();
    }
  }

  render() {
    const { navigation } = this.props;
    const { home, availableMenus, updateds, searchText, displayMode,
      overlayRequestAccessVisible, overlayProcedureMessageVisible, overlayAlertVisible, overlayAlertInputValue,
      loading, } = this.state;
    console.log('in activate');

    return (
      <View style={styles.container}>
        {
          overlayAlertVisible &&
          <OverlayAlert
            input={overlayAlertInputValue}
            handleUpdateFromAlert={this.handleUpdateFromAlert} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        {
          overlayRequestAccessVisible &&
          <OverlayRequestAccess
            home={home}
            requestedPrivilege='UPDATEMENU'
            handleUpdateFromRequestAccess={this.handleUpdateFromRequestAccess} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
              <View style={styles.selectionContainer}>
              </View>
            </View>
          </View>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingRight: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.searchBox}>
                  <TextInput style={styles.searchBoxInputValue}
                    placeholder='ENTER SEARCH TEXT'
                    placeholderTextColor='rgba(255,255,255,0.2)'
                    value={searchText}
                    onChangeText={(searchText) => this.handleSearchText(searchText)}
                  />
                  <TouchableOpacity style={styles.searchBoxButton}
                    onPress={() => {
                      this.setState({ searchText: '' }, () => this.handleSearchText(''))
                      Keyboard.dismiss()
                    }}>
                    <AntDesign name='close' size={24} color={theme.grey4} />
                  </TouchableOpacity>
                </View>
                <View style={styles.searchBoxInfo}>
                  <TouchableOpacity style={styles.searchBoxInfoLeft}
                    onPress={() => this.handlePressDisplayMode()}
                  >
                    <Text style={styles.searchBoxInfoText}>{displayMode}</Text>
                  </TouchableOpacity>
                  <View style={styles.searchBoxInfoRight}>
                    <Text style={styles.searchBoxInfoText}>{availableMenus.length} ITEMS</Text>
                  </View>
                </View>
                <FlatList
                  style={{ flex: 1 }}
                  data={availableMenus}
                  showsVerticalScrollIndicator={false}
                  keyExtractor={(item, index) => index.toString()}
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      style={styles.grid}
                      onPress={() => this.handleSelectMenu(item)}>
                      <View style={styles.gridFirst}>
                        <View style={styles.gridFirstLeft}>
                          <Text style={styles.gridFirstLeftText}>{item.menuId}</Text>
                        </View>
                        <View style={styles.gridFirstRight}>
                          {
                            item.statusFlg === 'A' ?
                              <Text style={[styles.gridFirstRightText, { color: theme.reserved }]}>ACTIVE</Text>
                              :
                              <Text style={[styles.gridFirstRightText, { color: theme.paid }]}>INACTIVE</Text>
                          }
                        </View>
                      </View>
                      <View style={styles.gridSecond}>
                        <Text style={styles.gridSecondText}>
                          {item.menuName}
                        </Text>
                      </View>
                      <View style={styles.gridThird}>
                        <View style={styles.gridThirdLeft}>
                          <Text style={[styles.gridThirdRightText, { color: theme.reserved }]}>KOT {item.kotType || '-'}</Text>
                        </View>
                        <View style={styles.gridThirdRight}>
                          <Text style={styles.gridThirdRightText}>${item.listPrice.toFixed(2)}</Text>
                        </View>
                      </View>
                    </TouchableOpacity>
                  )}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingLeft: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={[styles.searchBox, { backgroundColor: theme.grey3 }]}>
                </View>
                <View style={styles.searchBoxInfo}>
                  <View style={[styles.titleSecondContainer, { flex: 3 }]}>
                    <Text style={styles.titleText}>{updateds.length} Changes</Text>
                  </View>
                  <View style={[styles.titleFourthContainer, { flex: 1 }]}>
                    <Text style={styles.titleText}>New Status</Text>
                  </View>
                </View>
                <ScrollView style={{ flex: 1 }}>
                  {
                    updateds ?
                      updateds.map((item, index) => (
                        <TouchableOpacity
                          key={index}
                          style={styles.grid}
                          onPress={() => this.handleSelectUpdated(item)}>
                          <View style={styles.gridFirst}>
                            <View style={styles.gridFirstLeft}>
                              <Text style={styles.gridFirstLeftText}>{item.menuId}</Text>
                            </View>
                            <View style={styles.gridFirstRight}>
                              {
                                item.statusFlg === 'B' ?
                                  <Text style={[styles.gridFirstRightText, { color: theme.reserved }]}>ACTIVE</Text>
                                  :
                                  <Text style={[styles.gridFirstRightText, { color: theme.paid }]}>INACTIVE</Text>
                              }
                            </View>
                          </View>
                          <View style={styles.gridSecond}>
                            <Text style={styles.gridSecondText}>
                              {item.menuName}
                            </Text>
                          </View>
                          <View style={styles.gridThird}>
                            <View style={styles.gridThirdLeft}>
                            </View>
                            <View style={styles.gridThirdRight}>
                              <Text style={styles.gridThirdRightText}>${item.listPrice.toFixed(2)}</Text>
                            </View>
                          </View>
                        </TouchableOpacity>
                      ))
                      : null
                  }
                </ScrollView>
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={[styles.button, (loading || updateds.length === 0) && { opacity: 0.2 }]}
                disabled={loading || updateds.length === 0}
                onPress={() => this.handleSubmit()}>
                <AntDesign
                  name='bulb1'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('update')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

