import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity, FlatList,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';

export default class OverlayRequestAccess extends Component {

  constructor(props) {
    super(props);
    this.state = {
      overlayText: '',
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.scanTextInput.focus();
  }

  handleSubmit(overlayText) {
    this.setState({
      overlayText: ''
    }, () => this.scanTextInput.focus());
    this.props.handleUpdateFromRequestAccess(overlayText);
  }

  doPressKey(item) {
    const { overlayText } = this.state;
    let overlayTextUpdate = overlayText;
    if (item === 'CLEAR') {
      this.setState({ overlayText: '' });
    } else if (item === 'ENTER') {
      this.handleSubmit(overlayText);
    } else {
      this.setState({ overlayText: overlayTextUpdate + item })
    }
  }

  render() {
    const { home, requestedPrivilege } = this.props;
    const { overlayText } = this.state;
    console.log('in overlayrequestaccess');

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Request Access</Text>
            <Text style={styles.overlayText}>Grant temporary {requestedPrivilege} privilege to {home.userId}?</Text>
          </View>
          <View style={styles.searchBoxWrapper}>
            <View style={styles.overlaySearchBox}>
              <TextInput style={styles.overlaySearchBoxInput}
                ref={ref => { this.scanTextInput = ref; }}
                secureTextEntry={true}
                autoFocus={true}
                placeholder='ENTER ACCESS CODE'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(text) => this.setState({ overlayText: text })}
                onSubmitEditing={() => this.handleSubmit(overlayText)}
                value={overlayText}
              />
              <TouchableOpacity style={styles.overlaySearchBoxButton}
                onPress={() => {
                  this.setState({ overlayText: '' })
                  this.scanTextInput.focus()
                }}>
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
          </View>
          <FlatList
            style={{ width: 135, marginBottom: 20, alignSelf: 'center' }}
            data={['1', '2', '3', '4', '5', '6', '7', '8', '9', 'CLEAR', '0', 'ENTER']}
            scrollEnabled={false}
            showsVerticalScrollIndicator={false}
            numColumns={3}
            keyExtractor={(item, index) => index.toString()}
            renderItem={({ item }) => (
              <View style={{ flex: 1 / 3, aspectRatio: 1 }}>
                <TouchableOpacity style={{ margin: 2.5, flex: 1, alignItems: 'center', justifyContent: 'center', backgroundColor: 'black', borderRadius: 5, }}
                  onPress={() => this.doPressKey(item)}>
                  {
                    item === 'CLEAR' ?
                      <AntDesign name='close' color='white' size={18} />
                      : item === 'ENTER' ?
                        <AntDesign name='enter' color='white' size={18} />
                        :
                        <Text style={{ color: 'white', fontFamily: 'varela-round', fontSize: 18, }}>{item}</Text>
                  }
                </TouchableOpacity>
              </View>
            )}
          />
          {/* <TouchableOpacity style={styles.overlayOption}
            onPress={() => this.handleSubmit(overlayText)}>
            <Text style={styles.overlayOptionText}>{intl.get('submit')}</Text>
          </TouchableOpacity> */}
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromRequestAccess('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 2,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  searchBoxWrapper: {
    height: 50,
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 20,
  },
  overlaySearchBox: {
    backgroundColor: theme.grey1,
    height: 50,
    borderRadius: 100,
    marginHorizontal: 5,
    flexDirection: 'row',
    flex: 1,
  },
  overlaySearchBoxInput: {
    flex: 1,
    fontFamily: 'varela-round',
    fontSize: 16,
    color: 'white',
    paddingHorizontal: 20,
    height: 50,
  },
  overlaySearchBoxButton: {
    height: 50,
    width: 50,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 50,
  },
  searchBoxInfo: {
    height: 20,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  searchBoxInfoText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
});