import { Component } from 'react';
import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, Platform } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import * as Font from 'expo-font';
import intl from 'react-intl-universal';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { BarCodeScanner } from 'expo-barcode-scanner';

import Login from './src/Login';
import Main from './src/Main';
import Register from './src/Register';
import Preference from './src/Preference';
import Pos from './src/Pos';
import Split from './src/Split';
import Adhoc from './src/Adhoc';
import Dayclose from './src/Dayclose';
import Printer from './src/Printer';
import Report from './src/Report';
import Reprint from './src/Reprint';
import Resequence from './src/Resequence';
import Transfer from './src/Transfer';
import Void from './src/Void';
import Collect from './src/Collect';
import Table from './src/Table';
import Menu from './src/Menu';
import Scan from './src/Scan';
import Trace from './src/Trace';
import Activate from './src/Activate';
import Limit from './src/Limit';
import Fire from './src/Fire';
import Combo from './src/Combo';

const Stack = createNativeStackNavigator();

export default class App extends Component {

  state = {
    loading: true,
  }

  async componentDidMount() {

    // locale data
    const locales = {
      "zhs": require('./assets/zhs.json'),
      "zht": require('./assets/zht.json'),
      "en": require('./assets/en.json'),
      "ja": require('./assets/ja.json'),
    };

    const currentLocale = await AsyncStorage.getItem('language');

    await intl.init({
      currentLocale: currentLocale || 'en',
      locales,
    });

    // ttf for native, woff for web
    await Font.loadAsync({
      // 'varela-round': require('./assets/VarelaRound-Regular.ttf'),
      'varela-round': require('./assets/VarelaRound-Regular.woff'),
    })

    if (Platform.OS !== 'web') {
      BarCodeScanner.getPermissionsAsync()
        .then(response => {
          if (response.status !== 'granted') return BarCodeScanner.requestPermissionsAsync();
          return Promise.resolve(response);
        })
    }

    this.setState({ loading: false })
  }

  render() {
    const { loading } = this.state;

    if (loading) return <View></View>;

    return (
      <NavigationContainer>
        <StatusBar hidden />
        <Stack.Navigator
          screenOptions={{
            headerShown: false,
            animation: 'none',
            gestureEnabled: false,
          }}>
          <Stack.Screen name="Login" component={Login} options={Login.navigationOptions} />
          <Stack.Screen name="Register" component={Register} options={Register.navigationOptions} />
          <Stack.Screen name="Preference" component={Preference} options={Preference.navigationOptions} />
          <Stack.Screen name="Main" component={Main} options={Main.navigationOptions} />
          <Stack.Screen name="Pos" component={Pos} options={Pos.navigationOptions} />
          <Stack.Screen name="Split" component={Split} options={Split.navigationOptions} />
          <Stack.Screen name="Void" component={Void} options={Void.navigationOptions} />
          <Stack.Screen name="Collect" component={Collect} options={Collect.navigationOptions} />
          <Stack.Screen name="Transfer" component={Transfer} options={Transfer.navigationOptions} />
          <Stack.Screen name="Resequence" component={Resequence} options={Resequence.navigationOptions} />
          <Stack.Screen name="Reprint" component={Reprint} options={Reprint.navigationOptions} />
          <Stack.Screen name="Report" component={Report} options={Report.navigationOptions} />
          <Stack.Screen name="Printer" component={Printer} options={Printer.navigationOptions} />
          <Stack.Screen name="Dayclose" component={Dayclose} options={Dayclose.navigationOptions} />
          <Stack.Screen name="Adhoc" component={Adhoc} options={Adhoc.navigationOptions} />
          <Stack.Screen name="Scan" component={Scan} options={Scan.navigationOptions} />
          <Stack.Screen name="Trace" component={Trace} options={Trace.navigationOptions} />
          <Stack.Screen name="Activate" component={Activate} options={Activate.navigationOptions} />
          <Stack.Screen name="Limit" component={Limit} options={Limit.navigationOptions} />
          <Stack.Screen name="Table" component={Table} options={Table.navigationOptions} />
          <Stack.Screen name="Fire" component={Fire} options={Fire.navigationOptions} />
          <Stack.Screen name="Menu" component={Menu} options={{
            presentation: 'transparentModal',
          }} />
          <Stack.Screen name="Combo" component={Combo} options={{
            presentation: 'transparentModal',
          }} />
        </Stack.Navigator>
      </NavigationContainer>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
