import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, FlatList,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';

export default class OverlaySplitTable extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.home,
      serviceEntry: props.serviceEntry,
      selectedTable: props.selectedTable,
      tableItems: props.tableItems,

      overlaySearchText: '',
      availableTableItems: props.tableItems.filter(el => !el.refRecKey),
      selectedTableItems: [],
    };
    this.handleOverlaySearchText = this.handleOverlaySearchText.bind(this);
    this.handleSelectTableItem = this.handleSelectTableItem.bind(this);
  }

  componentDidMount() {

  }

  handleOverlaySearchText(text) {
    const { tableItems } = this.state;
    let availableTableItemsUpdate = [];
    this.setState({ overlaySearchText: text });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);
    console.log('available', this.state.availableTableItems);

    availableTableItemsUpdate = tableItems.filter(el => !el.refRecKey);
    if (!text) {
      this.setState({
        availableTableItems: availableTableItemsUpdate
      });
      return;
    }
    availableTableItemsUpdate = availableTableItemsUpdate.filter(item => {
      return item.menuId.toUpperCase().includes(caseText) || item.menuName.toUpperCase().includes(caseText);
    });
    this.setState({
      availableTableItems: availableTableItemsUpdate
    });
  }

  handleSelectTableItem(item) {
    const { selectedTableItems } = this.state;
    let selectedTableItemsUpdate = selectedTableItems;
    if (!selectedTableItemsUpdate.some(el => el.recKey === item.recKey)) {
      selectedTableItemsUpdate.push(item);
    } else {
      selectedTableItemsUpdate = selectedTableItemsUpdate.filter(el => el.recKey !== item.recKey);
    }
    this.setState({
      selectedTableItems: selectedTableItemsUpdate,
    });
  }

  handlePressSelection(item) {
    const { selectedTableItems } = this.state;
    const selectedTableItemsUpdate = selectedTableItems;
    selectedTableItemsUpdate = selectedTableItemsUpdate.filter(el => el.recKey !== item.recKey);
    this.setState({
      selectedTableItems: selectedTableItemsUpdate,
    });
  }

  render() {
    const { selectedTable, overlaySearchText, availableTableItems, selectedTableItems } = this.state;
    console.log('in overlaysplittable, state', this.state);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>TABLE {selectedTable.tableId}</Text>
            <Text style={styles.overlaySubtitleText}>Split Table</Text>
          </View>
          <View style={styles.selectionContainer}>
            {selectedTableItems.map(item =>
              <TouchableOpacity
                key={item.recKey}
                style={[styles.modalAvailableMod]}
                onPress={() => this.handleSelectTableItem(item)}
              >
                <Text style={[styles.modalAvailableModText]}>{item.lineNo}</Text>
              </TouchableOpacity>
            )}
          </View>
          <View style={styles.searchBoxWrapper}>
            <TouchableOpacity style={styles.overlayBackButton}
              onPress={() => this.props.handleUpdateFromSplitTable('')}>
              <AntDesign name='close' size={24} color='white' />
            </TouchableOpacity>
            <View style={styles.overlaySearchBox}>
              <TextInput style={styles.overlaySearchBoxInput}
                placeholder='ENTER SEARCH TEXT'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(searchText) => this.handleOverlaySearchText(searchText)}
                value={overlaySearchText}
              >
              </TextInput>
              <TouchableOpacity style={styles.overlaySearchBoxButton}
                onPress={() => {
                  this.setState({ overlaySearchText: '' }, () => this.handleOverlaySearchText(''))
                  Keyboard.dismiss()
                }}
              >
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
          </View>
          <View style={styles.searchBoxInfo}>
            <Text style={styles.searchBoxInfoText}>{availableTableItems.length} ITEMS</Text>
          </View>
          <FlatList
            style={[{ flex: 1 }]}
            showsVerticalScrollIndicator={false}
            data={availableTableItems}
            keyExtractor={item => item.recKey.toString()}
            renderItem={({ item }) => (
              <TouchableOpacity
                style={styles.overlayGrid}
                onPress={() => this.handleSelectTableItem(item)}>
                <View style={styles.overlayGridFirst}>
                  <View style={styles.overlayGridFirstLeft}>
                    <Text style={styles.overlayGridFirstLeftText}>{item.menuId}</Text>
                  </View>
                  <View style={styles.overlayGridFirstRight}>
                    <Text style={styles.overlayGridFirstRightText}>{item.lineNo}</Text>
                  </View>
                </View>
                <View style={styles.overlayGridSecond}>
                  <Text style={styles.overlayGridSecondText}>
                    {item.menuName}
                  </Text>
                </View>
                <View style={styles.overlayGridThird}>
                  <View style={styles.overlayGridThirdLeft}>
                  </View>
                  <View style={styles.overlayGridThirdRight}>
                    <Text style={styles.overlayGridThirdRightText}></Text>
                  </View>
                </View>
              </TouchableOpacity>
            )}
          />
          <TouchableOpacity style={styles.overlayOption}
            onPress={() => this.props.handleUpdateFromSplitTable(selectedTableItems)}>
            <Text style={styles.overlayOptionText}>{intl.get('submit')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

