import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, Image, TextInput, Alert, SectionList, ScrollView, FlatList, Platform } from 'react-native';
import theme from '../assets/theme';
import { debug } from '../assets/utility';
import intl from 'react-intl-universal';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import OverlayAlert from './OverlayAlert';

export default class Combo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      preference: props.route.params.preference,
      masters: props.route.params.masters,
      settings: props.route.params.masters.settings,
      modBundles: props.route.params.masters.modBundles,
      mods: props.route.params.masters.mods,
      selectedTable: props.route.params.selectedTable,
      selectedMenu: props.route.params.selectedMenu, // NEW
      selectedTableItem: props.route.params.selectedTableItem, // EDIT
      tableItems: props.route.params.tableItems,

      comboGroups: props.route.params.masters.comboGroups.filter(el => el.refMenuId === props.route.params.selectedMenu.menuId),
      comboItems: props.route.params.masters.comboItems.filter(el => el.refMenuId === props.route.params.selectedMenu.menuId),
      selectedComboItems: [],
      selectedComboItem: null,

      loading: false,
      menuName: props.route.params.selectedTableItem ? props.route.params.selectedTableItem?.menuName : props.route.params.selectedMenu.menuName,
      qtyInput: props.route.params.selectedTableItem?.orderQty?.toString() || '1',
      orderType: props.route.params.selectedTableItem ? (props.route.params.selectedTableItem?.orderType || 'A') : props.route.params.selectedTable?.sectionId === 'SYS' ? 'B' : 'A',

      // modInput: props.route.params.selectedTableItem ? (props.route.params.selectedTableItem?.menuMod || '') : (props.route.params.selectedTable?.sectionId === 'SYS' ? `*${intl.get('takeaway')}  ` : ''),
      // modRecKeys: props.route.params.selectedTableItem?.modRecKeys?.split(',') || [],
    };

    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
    this.handleUpdateFromAlert = this.handleUpdateFromAlert.bind(this);
  }

  componentDidMount() {
    console.log('mount combo', this.state.comboItems, this.state.selectedComboItems)
    this.doPrepareComboItems();
  }

  doPrepareComboItems() {
    const { comboItems, tableItems, selectedTableItem } = this.state;
    let tableItemsUpdate = selectedTableItem ? tableItems?.filter(el => el.refRecKey === selectedTableItem.recKey) : [];
    let comboItemsUpdate = comboItems.map(el => ({
      ...el,
      modInput: tableItems?.find(obj => obj.comboitemRecKey === el.recKey)?.menuMod || '',
      modRecKeys: tableItems?.find(obj => obj.comboitemRecKey === el.recKey)?.modRecKeys?.split(',') || [],
    }));
    let selectedComboItemsUpdate = comboItemsUpdate.filter(el => tableItemsUpdate.some(obj => obj.comboitemRecKey === el.recKey));
    this.setState({
      comboItems: comboItemsUpdate,
      selectedComboItems: selectedComboItemsUpdate,
    })
  }

  doComboItemModInput(text) {
    const { comboItems, selectedComboItem } = this.state;
    let comboItemsUpdate = comboItems;
    comboItemsUpdate.find(el => el.recKey === selectedComboItem.recKey).modInput = text;
    this.setState({
      comboItems: comboItemsUpdate,
    })
  }

  doPressComboItem(item) {
    const { selectedComboItems } = this.state;
    let selectedComboItemsUpdate = selectedComboItems;
    if (selectedComboItems.some(el => el.recKey === item.recKey)) {
      selectedComboItemsUpdate = selectedComboItems.filter(el => el.recKey !== item.recKey);
      this.setState({
        selectedComboItems: selectedComboItemsUpdate,
        selectedComboItem: null,
        selectedModBundle: null,
      })
    } else {
      selectedComboItemsUpdate.push(item);
      this.setState({
        selectedComboItems: selectedComboItemsUpdate,
        selectedComboItem: item,
        selectedModBundle: null,
      })
    }
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromAlert(text) {
    const { selectedTable, selectedTableItem } = this.state;
    this.setState({
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    });
    if (!text) {
      return;
    }
  }

  doValidateQty(text) {
    if (!text || isNaN(text)) {
      this.setState({
        qtyInput: '0',
      })
    } else {
      this.setState({
        qtyInput: text,
      })
    }
  }

  doChangeQty(type) {
    const { qtyInput } = this.state;
    let qtyInputUpdate = Number(qtyInput);
    if (type === 'PLUS') {
      this.setState({
        qtyInput: (qtyInputUpdate + 1).toString(),
      });
    }
    else if (type === 'MINUS') {
      if (qtyInputUpdate <= 1) return;
      this.setState({
        qtyInput: (qtyInputUpdate - 1).toString(),
      });
    }
  }

  doToggleOrderType() {
    const { orderType, comboItems } = this.state;
    let comboItemsUpdate = comboItems;

    if (orderType === 'A') {
      comboItemsUpdate.forEach(el => {
        el.modInput = el.modInput.replace(`*${intl.get('takeaway')}  `, '');
        el.modInput = `*${intl.get('takeaway')}  ` + el.modInput;
      })
      this.setState({
        orderType: 'B',
        comboItems: comboItemsUpdate,
      });
    } else {
      comboItemsUpdate.forEach(el => {
        el.modInput = el.modInput.replace(`*${intl.get('takeaway')}  `, '');
      })
      this.setState({
        orderType: 'A',
        comboItems: comboItemsUpdate,
      });
    }
  }

  doPressModBundle(item) {
    this.setState({
      selectedModBundle: item,
    })
  }

  doPressMod(item) {
    const { selectedModBundle, selectedComboItem, comboItems } = this.state;
    let modInputUpdate = comboItems.find(el => el.recKey === selectedComboItem.recKey).modInput;
    let modRecKeysUpdate = comboItems.find(el => el.recKey === selectedComboItem.recKey).modRecKeys;

    if (selectedModBundle.modmas.modType === 'A') {
      // any
      if (comboItems.find(el => el.recKey === selectedComboItem.recKey).modRecKeys.some(el => el === item.recKey.toString())) {
        modInputUpdate = modInputUpdate.replace('*' + item.name + '  ', '');
        modRecKeysUpdate = modRecKeysUpdate.filter(obj => obj !== item.recKey.toString());
      } else {
        modInputUpdate += '*' + item.name + '  ';
        modRecKeysUpdate.push(item.recKey.toString());
      }
    } else {
      // either
      selectedModBundle.moditems.forEach(el => {
        modInputUpdate = modInputUpdate.replace('*' + el.name + '  ', '');
        modRecKeysUpdate = modRecKeysUpdate.filter(obj => obj !== el.recKey.toString());
      })
      if (!comboItems.find(el => el.recKey === selectedComboItem.recKey).modRecKeys.some(el => el === item.recKey.toString())) {
        modInputUpdate += '*' + item.name + '  ';
        modRecKeysUpdate.push(item.recKey.toString());
      }
    }

    let comboItemsUpdate = comboItems;
    comboItemsUpdate.find(el => el.recKey === selectedComboItem.recKey).modInput = modInputUpdate;
    comboItemsUpdate.find(el => el.recKey === selectedComboItem.recKey).modRecKeys = modRecKeysUpdate;
    this.setState({
      comboItems: comboItemsUpdate,
    })
  }

  doSubmit() {
    const { navigation } = this.props;
    const { serviceEntry, home, selectedTable, selectedMenu, selectedTableItem, mods, masters, settings, modBundles,
      comboGroups, comboItems, selectedComboItems,
      menuName, qtyInput, orderType, } = this.state;

    // no table
    if (!selectedTable) {
      this.setState({
        overlayProcedureMessageVisible: 'MISSING TABLE',
        loading: false,
      })
      return;
    }
    // table status
    if (selectedTable.sectionId !== 'SYS' && selectedTable.statusFlg !== 'C') {
      this.setState({
        overlayProcedureMessageVisible: 'CHECK TABLE STATUS',
        loading: false,
      })
      return;
    }
    // combo group min max
    if (comboGroups.some(el => (selectedComboItems.filter(obj => obj.groupNo === el.groupNo).length > el.maxSelectItem || selectedComboItems.filter(obj => obj.groupNo === el.groupNo).length < el.minSelectItem))) {
      this.setState({
        overlayProcedureMessageVisible: 'CHECK MIN MAX',
        loading: false,
      })
      return;
    }
    // combo item compulsory modifier
    if (selectedComboItems.some(arr => modBundles.filter(el => el.modmas.mustFlg === 'Y' && (el.modmas.globalFlg === 'Y' || mods.some(obj => obj.menuId === arr.menuId && obj.modId === el.modmas.modId))).some(el => !el.moditems.some(obj => arr.modRecKeys.some(el => el === obj.recKey.toString()))))) {
      this.setState({
        overlayProcedureMessageVisible: 'CHECK COMPULSORY MODIFIERS',
        loading: false,
      })
      return;
    }

    if (selectedTableItem) {
      // edit item
      let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/update-combo';
      const body = {
        opentableRecKey: selectedTable.recKey,
        opentableItemRecKey: selectedTableItem.recKey,
        userId: home.userId,
        restmenuRecKey: selectedMenu.recKey,
        orderQty: qtyInput,
        orderType: orderType,
        menuName: menuName,
        itemPayloads: selectedComboItems.map(el => ({
          combogroupRecKey: comboGroups.find(obj => obj.groupNo === el.groupNo).recKey,
          comboitemRecKey: el.recKey,
          restmenuMod: comboItems.find(obj => obj.recKey === el.recKey).modInput,
          modRecKeys: comboItems.find(obj => obj.recKey === el.recKey).modRecKeys.join(','),
          // need one more hole for modRecKeys
          orderQty: qtyInput,
          orderType: orderType,
        })),
      };
      fetch(url, {
        method: 'POST',
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
        body: JSON.stringify(body),
      })
        .then(response => response.json())
        .then(response => {
          settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
            debug(
              serviceEntry,
              'Update Item',
              home.userName,
              response.message,
              selectedTable.tableId,
              menuName,
            );
          console.log('post update-item successful', response);
          if (response.message) {
            this.setState({
              overlayProcedureMessageVisible: response.message.toUpperCase(),
              loading: false,
            })
            return;
          }
          navigation.goBack();
        })
    } else {
      // add item
      let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/insert-combo';
      const body = {
        opentableRecKey: selectedTable.recKey,
        userId: home.userId,
        restmenuRecKey: selectedMenu.recKey,
        orderQty: qtyInput,
        orderType: orderType,
        menuName: menuName,
        itemPayloads: selectedComboItems.map(el => ({
          combogroupRecKey: comboGroups.find(obj => obj.groupNo === el.groupNo).recKey,
          comboitemRecKey: el.recKey,
          restmenuMod: comboItems.find(obj => obj.recKey === el.recKey).modInput,
          modRecKeys: comboItems.find(obj => obj.recKey === el.recKey).modRecKeys.join(','),
          // need one more hole for modRecKeys
          orderQty: qtyInput,
          orderType: orderType,
        })),
      };
      fetch(url, {
        method: 'POST',
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
        body: JSON.stringify(body),
      })
        .then(response => response.json())
        .then(response => {
          settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
            debug(
              serviceEntry,
              'Insert Item',
              home.userName,
              response.message,
              selectedTable.tableId,
              menuName,
            );
          console.log('post insert-item successful', response);
          if (response.message) {
            this.setState({
              overlayProcedureMessageVisible: response.message.toUpperCase(),
              loading: false,
            })
            return;
          }
          navigation.goBack();
        })
    }
  }

  render() {
    const { navigation, route } = this.props;
    const { loading, preference,
      home, selectedMenu, selectedTableItem, masters, mods, modBundles, selectedModBundle,
      menuName, qtyInput, orderType,
      comboGroups, comboItems, selectedComboItems, selectedComboItem,
      overlayProcedureMessageVisible, overlayAlertVisible, overlayAlertInputValue, } = this.state;

    return (
      <View style={{ flex: 1, flexDirection: 'row', backgroundColor: 'rgba(0,0,0,0.9)' }}>
        {
          overlayAlertVisible &&
          <OverlayAlert
            input={overlayAlertInputValue}
            handleUpdateFromAlert={this.handleUpdateFromAlert} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={{ width: 10, }}></View>
        <View style={{ flex: 1, }}>
          {
            selectedComboItem &&
            <View style={{ flex: 1, marginVertical: 10, marginLeft: 0, backgroundColor: theme.grey2, borderRadius: 5, }}>
              <View style={{ flex: 1, margin: 5, backgroundColor: theme.grey4 }}>
                <View style={{ alignItems: 'center', marginVertical: 10, }}>
                  <Text style={{ fontFamily: 'varela-round', color: theme.paid, fontSize: 16, }}>{selectedComboItem.menuId}</Text>
                  <Text style={{
                    fontFamily: 'varela-round',
                    color: 'white',
                    fontSize: 20,
                    textAlign: 'center',
                    paddingHorizontal: 20,
                  }} numberOfLines={2}>{selectedComboItem.menuName}</Text>
                </View>
                <TextInput style={styles.modifierTextInput}
                  multiline={true}
                  placeholder='CHOOSE FROM PREDEFINED MODIFIERS OR ENTER TEXT'
                  placeholderTextColor='rgba(255,255,255,0.2)'
                  value={comboItems.find(el => el.recKey === selectedComboItem.recKey).modInput}
                  onChangeText={text => this.doComboItemModInput(text)}
                />
                <View style={{ height: 20, justifyContent: 'center', alignItems: 'center', backgroundColor: theme.grey2, }}>
                  <Text style={{ textTransform: 'uppercase', fontFamily: 'varela-round', fontSize: 12, color: theme.grey7, }}>MODIFIER</Text>
                </View>
                <ScrollView style={{ flex: 1 }}
                  showsVerticalScrollIndicator={false}>
                  <View style={styles.modContainer}>
                    {
                      mods?.filter(el => el.menuId === selectedMenu.menuId || el.menuId === selectedComboItem?.menuId)?.map(item => modBundles.find(el => el.modmas.modId === item.modId))?.map(item =>
                        <TouchableOpacity key={item.modmas.recKey.toString()} style={[styles.modButton, item.modmas.mustFlg === 'Y' && { backgroundColor: theme.paid }]}
                          onPress={() => this.doPressModBundle(item)}>
                          <Text style={styles.modText}>{item.modmas.name}</Text>
                        </TouchableOpacity>
                      )
                    }
                    {
                      modBundles?.filter(el => el.modmas.globalFlg === 'Y').map(item =>
                        <TouchableOpacity key={item.modmas.recKey.toString()} style={[styles.modButton, item.modmas.mustFlg === 'Y' && { backgroundColor: theme.paid }]}
                          onPress={() => this.doPressModBundle(item)}>
                          <Text style={styles.modText}>{item.modmas.name}</Text>
                        </TouchableOpacity>
                      )
                    }
                  </View>
                </ScrollView>
                <View style={{ height: 20, justifyContent: 'center', alignItems: 'center', backgroundColor: theme.grey2, }}>
                  <Text style={{ textTransform: 'uppercase', fontFamily: 'varela-round', fontSize: 12, color: theme.grey7, }}>MODIFIER OPTIONS</Text>
                </View>
                <View style={{ flex: 1, backgroundColor: theme.grey4 }}>
                  <ScrollView
                    contentContainerStyle={{ flexGrow: 1, alignItems: 'flex-end', justifyContent: 'center' }}
                    showsVerticalScrollIndicator={false}>
                    {
                      selectedModBundle?.moditems.map(item =>
                        <TouchableOpacity key={item.recKey.toString()} style={[styles.modButton, { margin: 10 }, comboItems.find(el => el.recKey === selectedComboItem.recKey)?.modRecKeys.some(el => el === item.recKey.toString()) && { backgroundColor: theme.occupied }]}
                          onPress={() => this.doPressMod(item)}>
                          <Text style={styles.modText}>{item.name}</Text>
                          {
                            item.listPrice !== 0 &&
                            <Text style={{ position: 'absolute', top: -15, right: 10, fontFamily: 'varela-round', color: theme.reserved, fontSize: 20, }}>{item.listPrice < 0 ? '' : '+'}{item.listPrice?.toFixed(2)}</Text>
                          }
                        </TouchableOpacity>
                      )
                    }
                  </ScrollView>
                </View>
              </View>
            </View>
          }
        </View>
        <View style={{ flex: 2 }}>
          <View style={{ flex: 1, flexDirection: 'row', padding: 5 }}>
            <View style={{ flex: 1, margin: 5, backgroundColor: theme.grey2, borderRadius: 5, }}>
              <View style={{ flex: 1, margin: 5, marginBottom: 0, backgroundColor: theme.grey4 }}>
                <View style={{ alignItems: 'center', marginTop: 10, }}>
                  <Text style={{ fontFamily: 'varela-round', color: theme.paid, fontSize: 16, }}>{selectedMenu.menuId}</Text>
                  <TextInput style={{ height: 50, width: '90%', fontFamily: 'varela-round', color: 'white', fontSize: 20, textAlign: 'center', paddingHorizontal: 20, }}
                    editable={(home.adminFlg === 'Y' || masters.privileges.some(el => el.priId === 'EDITITEM'))}
                    multiline
                    blurOnSubmit
                    value={menuName}
                    onChangeText={text => this.setState({ menuName: text })}
                  />
                </View>
                <View style={styles.menuQtyRow}>
                  <TouchableOpacity style={styles.menuQtyRowFirst}
                    onPress={() => this.doChangeQty('MINUS')}>
                    <AntDesign name='minus' size={32} color='rgba(0,0,0,0.85)' />
                  </TouchableOpacity>
                  <View style={styles.menuQtyRowSecond}>
                    <TextInput style={styles.qtyTextInput}
                      value={qtyInput}
                      keyboardType='numeric'
                      onChangeText={text => this.doValidateQty(text)}
                    />
                  </View>
                  <TouchableOpacity style={styles.menuQtyRowThird}
                    onPress={() => this.doChangeQty('PLUS')}>
                    <AntDesign name='plus' size={32} color='rgba(0,0,0,0.85)' />
                  </TouchableOpacity>
                </View>
                <ScrollView
                  style={{ flex: 1, backgroundColor: theme.grey2, paddingBottom: 5 }}
                  showsVerticalScrollIndicator={false}>
                  {
                    comboGroups.map(el =>
                      <View key={el.recKey.toString()}>
                        <View style={{ height: 20, justifyContent: 'center', alignItems: 'center', }}>
                          <Text style={{ textTransform: 'uppercase', fontFamily: 'varela-round', fontSize: 12, color: 'white', }}>{el.groupName} <Text style={{ fontFamily: 'varela-round', fontSize: 12, color: theme.paid, }}>MIN</Text> {el.minSelectItem} <Text style={{ fontFamily: 'varela-round', fontSize: 12, color: theme.paid, }}>MAX</Text> {el.maxSelectItem}</Text>
                        </View>
                        <FlatList
                          style={{ margin: -2.5 }}
                          showsVerticalScrollIndicator={false}
                          data={comboItems.filter(obj => obj.groupNo === el.groupNo)}
                          numColumns={preference.pcolsize}
                          keyExtractor={(item, index) => item.recKey.toString()}
                          renderItem={({ item }) => this.renderComboItem(item)}
                        />
                      </View>
                    )
                  }
                </ScrollView>
              </View>
            </View>
            <View style={{ margin: 5, width: 120, }}>
              <ScrollView style={{ flex: 1 }}
                showsVerticalScrollIndicator={false}
                alwaysBounceVertical={false}>
                <View style={{ paddingHorizontal: 5, height: 80, marginVertical: 5 }}>
                </View>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: orderType === 'A' ? theme.grey3 : theme.paid, borderRadius: 5, marginVertical: 5 }}
                  onPress={() => this.doToggleOrderType()}>
                  <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', textTransform: 'uppercase' }}>{orderType === 'A' ? intl.get('dinein') : intl.get('takeaway')}</Text>
                  <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', fontSize: 10 }}>ORDER TYPE</Text>
                </TouchableOpacity>
                <View style={{ paddingHorizontal: 5, height: 50, marginVertical: 5 }}>
                </View>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: 'white', borderRadius: 5, marginVertical: 5 }}
                  disabled={loading}
                  onPress={() => this.setState({ loading: true }, () => this.doSubmit())}>
                  <AntDesign name="check" size={28} color={theme.grey4} />
                </TouchableOpacity>
                <View style={{ paddingHorizontal: 5, height: 50, marginVertical: 5 }}>
                </View>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: theme.grey3, borderRadius: 5, marginVertical: 5 }}
                  onPress={() => this.doChangeQty('PLUS')}>
                  <AntDesign name='plus' size={26} color='white' />
                </TouchableOpacity>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: theme.grey3, borderRadius: 5, marginVertical: 5 }}
                  onPress={() => this.doChangeQty('MINUS')}>
                  <AntDesign name="minus" size={32} color='white' />
                </TouchableOpacity>
                <View style={{ paddingHorizontal: 5, height: 50, marginVertical: 5 }}>
                </View>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: 'white', borderRadius: 5, marginVertical: 5 }}
                  disabled={loading}
                  onPress={() => navigation.goBack()}>
                  <AntDesign name="close" size={26} color={theme.grey4} />
                </TouchableOpacity>
              </ScrollView>
            </View>
          </View>
        </View>
      </View>
    );
  }

  renderComboItem(item) {
    const { preference, selectedComboItems } = this.state;
    const found = selectedComboItems.some(el => el.recKey === item.recKey);

    return (
      <View style={{ flex: 1 / preference.pcolsize }}>
        <TouchableOpacity style={{ height: 70, padding: 10, margin: 2.5, backgroundColor: found ? theme.occupied : theme.grey4, }}
          onPress={() => this.doPressComboItem(item)}>
          <View style={{ flex: 1 }}>
            <Text style={{ fontFamily: 'varela-round', color: 'white', }} numberOfLines={2}>{item.menuName}</Text>
          </View>
          <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
            <Text style={{ fontFamily: 'varela-round', color: 'white', fontSize: 12, }}></Text>
            <Text style={[{ fontFamily: 'varela-round', fontSize: 12, color: Number(item.addonPrice) === 0 ? 'transparent' : found ? 'white' : theme.reserved }]}>+{item.addonPrice?.toFixed(2)}</Text>
          </View>
        </TouchableOpacity>
      </View>
    )
  }

}

const styles = StyleSheet.create({
  modifierTextInput: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    paddingHorizontal: 20,
    height: 120,
    backgroundColor: theme.grey3,
  },
  menuQtyRow: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginRight: 10,
  },
  menuQtyRowSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginLeft: 10,
  },
  qtyTextInput: {
    fontFamily: 'varela-round',
    fontSize: 32,
    width: '100%',
    color: 'white',
    textAlign: 'center',
  },
  modContainer: {
    padding: 5,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    backgroundColor: theme.grey4,
  },
  modButton: {
    paddingHorizontal: 20,
    minHeight: 32,
    justifyContent: 'center',
    borderRadius: 16,
    backgroundColor: theme.grey2,
    margin: 5,
  },
  modText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 20,
  },
});
