import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions,
  FlatList, ScrollView,
} from 'react-native';
import moment from 'moment';
import { AntDesign } from '@expo/vector-icons';
import theme from '../assets/theme';
import { debug } from '../assets/utility';
import OverlayProcedureMessage from './OverlayProcedureMessage';

export default class Adhoc extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      selectedTable: props.route.params.selectedTable,
      menus: props.route.params.masters.menus,
      settings: props.route.params.masters.settings,

      selectedAdhocItem: '',
      selectedMenu: '',
      adhocItems: [],

      searchText: '',
      availableMenus: props.route.params.masters.menus.filter(menu => {
        return menu.menuType === 'X';
      }),
    };
    this.handleSelectTableItem = this.handleSelectTableItem.bind(this);
    this.handleSelectAdhoc = this.handleSelectAdhoc.bind(this);
    this.handlePressSettleAsMenu = this.handlePressSettleAsMenu.bind(this);
    this.handlePressSettleAsRetail = this.handlePressSettleAsRetail.bind(this);
    this.handleSearchText = this.handleSearchText.bind(this);

    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  componentDidMount() {
    this.doGet();
  }

  doGet() {
    const { home, serviceEntry, selectedTable } = this.state;
    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/opentable-adhocs';
    fetch(url)
      .then(response => response.json())
      .then(response => {
        console.log('get opentable-adhocs successful', response);
        // to state
        this.setState({
          adhocItems: response,
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleSelectTableItem(item) {
    this.setState({
      selectedAdhocItem: item,
      selectedMenu: '',
    });
  }

  handleSelectAdhoc(item) {
    const { selectedAdhocItem } = this.state;
    if (!selectedAdhocItem) return;
    this.setState({
      selectedMenu: item,
    });
  }

  handlePressSettleAsMenu() {
    const { navigation } = this.props;
    const { home, serviceEntry, settings, selectedTable, selectedAdhocItem, selectedMenu } = this.state;

    if (!selectedAdhocItem) return;

    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/insert-item';
    const body = {
      userId: home.userId,
      opentableRecKey: selectedTable.recKey,
      restmenuRecKey: selectedMenu.recKey,
      adhocRecKey: selectedAdhocItem.recKey,
      orderQty: '1',
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post insert-item successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Adhoc Settle Menu',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            selectedAdhocItem.recKey,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        this.doGet();
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handlePressSettleAsRetail() {
    const { navigation } = this.props;
    const { home, serviceEntry, settings, selectedTable, selectedAdhocItem } = this.state;

    if (!selectedAdhocItem) return;

    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/insert-item';
    const body = {
      userId: home.userId,
      opentableRecKey: selectedTable.recKey,
      // restmenuRecKey: menu.recKey,
      adhocRecKey: selectedAdhocItem.recKey,
      orderQty: selectedAdhocItem.orderQty,
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post insert-item successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Adhoc Settle Retail',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            selectedAdhocItem.recKey,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        this.doGet();
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleSearchText(text) {
    const { menus } = this.state;
    let availableMenusUpdate = [];
    this.setState({ searchText: text });
    let caseText = text.toUpperCase();

    availableMenusUpdate = menus.filter(menu => {
      return menu.menuType === 'X';
    });
    if (!text) {
      this.setState({
        availableMenus: availableMenusUpdate
      });
      return;
    }
    availableMenusUpdate = availableMenusUpdate.filter(item => {
      return item.menuName.toUpperCase().includes(caseText) || item.menuId.toUpperCase().includes(caseText);
    });
    this.setState({
      availableMenus: availableMenusUpdate
    });
  }

  render() {
    const { navigation } = this.props;
    const { selectedTable, adhocItems, selectedAdhocItem, selectedMenu, searchText, availableMenus,
      overlayProcedureMessageVisible } = this.state;
    console.log('in adhoc',);
    return (
      <View style={styles.container}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.wrapper}>
            <View style={styles.overlayHeader}>
              <Text style={styles.overlayTitleText}>TABLE {selectedTable.tableId}</Text>
              <Text style={styles.overlaySubtitleText}>Ad Hoc</Text>
              <Text style={styles.overlayTitleText}>{adhocItems.length} ITEMS</Text>
            </View>
            <View style={styles.buttonsContainer}>
              <TouchableOpacity style={[styles.button, { opacity: 0.2 }]}
                disabled={true}
                onPress={() => this.handlePressSettleAsRetail()}>
                <Text style={styles.buttonText}>Settle</Text>
                <Text style={styles.buttonText}>As</Text>
                <Text style={styles.buttonText}>Retail</Text>
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button, !selectedMenu && { opacity: 0.2 }]}
                disabled={!selectedMenu}
                onPress={() => this.handlePressSettleAsMenu()}>
                <Text style={styles.buttonText}>Settle</Text>
                <Text style={styles.buttonText}>As</Text>
                <Text style={styles.buttonText}>Menu</Text>
              </TouchableOpacity>
            </View>
            <ScrollView style={[styles.itemsContainer]}>
              <View style={{ paddingTop: 5 }}></View>
              {
                adhocItems.map(item => (
                  <TouchableOpacity key={item.recKey}
                    style={[styles.tableItemContainer, (selectedAdhocItem.recKey === item.recKey) && { backgroundColor: theme.occupied }]}
                    onPress={() => this.handleSelectTableItem(item)}>
                    <View style={styles.tableItemFirstContainer}><Text style={styles.tableItemText}>{item.orderQty}</Text></View>
                    <View style={styles.tableItemSecondContainer}>
                      <Text style={styles.tableItemText}>
                        {item.name}
                      </Text>
                      <Text style={styles.tableItemText}>
                        {moment(item.createDate).format("HH:mm")}
                      </Text>
                    </View>
                    <View style={styles.tableItemThirdContainer}>
                      <Text style={styles.tableItemText}>
                      </Text>
                    </View>
                  </TouchableOpacity>
                ))
              }
            </ScrollView>
            <TouchableOpacity style={styles.actionButton}
              onPress={() => navigation.goBack()}>
              <Text style={[styles.buttonText, { color: theme.paid }]}>QUIT</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.rightWrapper}>
            <View style={styles.searchBox}>
              <TextInput style={styles.searchBoxInputValue}
                placeholder='ENTER SEARCH TEXT'
                placeholderTextColor='rgba(255,255,255,0.2)'
                value={searchText}
                onChangeText={(searchText) => this.handleSearchText(searchText)}
              />
              <TouchableOpacity style={styles.searchBoxButton}
                onPress={() => {
                  this.setState({ searchText: '' }, () => this.handleSearchText(''))
                  Keyboard.dismiss()
                }}>
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
            <View style={styles.searchBoxInfo}>
              <Text style={styles.searchBoxInfoText}>{availableMenus.length} ITEMS</Text>
            </View>
            <FlatList
              style={{ flex: 1 }}
              data={availableMenus}
              keyExtractor={item => item.recKey.toString()}
              renderItem={({ item }) => (
                <TouchableOpacity
                  key={item.recKey}
                  style={[styles.grid, (selectedMenu.recKey === item.recKey) && { backgroundColor: theme.occupied }]}
                  onPress={() => this.handleSelectAdhoc(item)}>
                  <View style={styles.gridFirst}>
                    <View style={styles.gridFirstLeft}>
                      <Text style={styles.gridFirstLeftText}>{item.menuId}</Text>
                    </View>
                    <View style={styles.gridFirstRight}>
                    </View>
                  </View>
                  <View style={styles.gridSecond}>
                    <Text style={styles.gridSecondText}>
                      {item.menuName}
                    </Text>
                  </View>
                  <View style={styles.gridThird}>
                  </View>
                </TouchableOpacity>
              )}
            />
          </View>
        </View>
      </View>
    );

  }
}

