import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions,
  FlatList, ScrollView,
} from 'react-native';
import { AntDesign, MaterialCommunityIcons, Feather } from '@expo/vector-icons';
import { debug } from '../assets/utility';
import theme from '../assets/theme';
import intl from 'react-intl-universal';
import OverlayAlert from './OverlayAlert';
import OverlayProcedureMessage from './OverlayProcedureMessage';

export default class Fire extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      masters: props.route.params.masters,
      selectedTable: props.route.params.selectedTable,
      tableItems: props.route.params.tableItems,

      kitchenmsgs: props.route.params.masters.kitchenmsgs,
      kottypes: props.route.params.masters.kottypes,
      privileges: props.route.params.masters.privileges,
      settings: props.route.params.masters.settings,

      remark: '',
      selectedTableItems: [],
      selectedKottypes: [],
      selectedKitchenmsg: null,

      overlayRequestPrivilegeVisible: false,
      overlayProcedureMessageVisible: false,
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    };

    this.handleUpdateFromAlert = this.handleUpdateFromAlert.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  componentDidMount() {
  }

  doReset() {
    Keyboard.dismiss();
    this.setState({
      selectedKitchenmsg: null,
      selectedKottypes: [],
      selectedTableItems: [],
      remark: '',
    })
  }

  doPressFire() {
    const { selectedKottypes, selectedTableItems, remark } = this.state;
    // let options = [];
    // options.push({ text: 'Yes', handleText: 'doSubmit' });
    // this.setState({
    //   overlayAlertInputValue: {
    //     title: intl.get('action'),
    //     subtitle: 'Fire',
    //     text: `${intl.get('fire')} "${remark}" (${selectedTableItems.map(el => el.menuId).join(',')}) (${selectedKottypes.map(el => el.kotType).join(',')})`,
    //     ok: false,
    //     cancel: true,
    //     options: options,
    //   },
    //   overlayAlertVisible: true,
    // });
    this.doSubmit();
  }

  doPressKitchenmsg(item) {
    const { selectedKitchenmsg, kottypes } = this.state;
    if (selectedKitchenmsg?.recKey === item?.recKey) {
      this.setState({
        selectedKitchenmsg: null,
        remark: '',
        selectedKottypes: [],
      })
    } else {
      this.setState({
        selectedKitchenmsg: item,
        remark: item.name,
        selectedKottypes: item.kotTypes?.split(',').map(el => kottypes.find(obj => obj.kotType === el)) || [],
      })
    }
  }

  doPressTableItem(item) {
    const { selectedTableItems, } = this.state;
    let selectedTableItemsUpdate = selectedTableItems;
    if (selectedTableItems.some(el => el.recKey === item.recKey)) {
      selectedTableItemsUpdate = selectedTableItems.filter(el => el.recKey !== item.recKey);
    } else {
      selectedTableItemsUpdate.push(item);
    }
    this.setState({
      selectedTableItems: selectedTableItemsUpdate,
    })
  }

  doPressKottype(item) {
    const { selectedKottypes } = this.state;
    let selectedKottypesUpdate = selectedKottypes;
    if (selectedKottypes.some(el => el.recKey === item.recKey)) {
      selectedKottypesUpdate = selectedKottypes.filter(el => el.recKey !== item.recKey);
    } else {
      selectedKottypesUpdate.push(item);
    }
    this.setState({
      selectedKottypes: selectedKottypesUpdate,
    })
  }

  doPressAllKottype() {
    const { selectedKottypes, kottypes } = this.state;
    if (!kottypes.some(el => !selectedKottypes.some(obj => obj.recKey === el.recKey))) {
      this.setState({
        selectedKottypes: [],
      })
    } else {
      this.setState({
        selectedKottypes: kottypes,
      })
    }
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromAlert(text) {
    this.setState({
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    });
    if (!text) {
      return;
    }
    if (text === 'doSubmit') {
      this.doSubmit();
    }
  }

  doSubmit() {
    const { navigation } = this.props;
    const { serviceEntry, home, selectedTable, masters, settings,
      selectedKitchenmsg, selectedTableItems, selectedKottypes, remark } = this.state;
    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/fire-kot';
    const body = {
      userId: home.userId,
      orgId: home.orgId,
      locId: home.locId,
      opentableRecKey: selectedTable.recKey,
      remark: remark,
      kotTypes: selectedKottypes.map(el => el.kotType).join(','),
      opentableItemRecKeys: selectedTableItems.map(el => el.recKey).join(','),
      msgId: selectedKitchenmsg?.msgId,
    };
    console.log(body)
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post fire-kot successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Fire Course',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            remark + ' (' + selectedTableItems.map(el => el.menuId).join(',') + ')' + ' (' + selectedKottypes.map(el => el.kotType).join(',') + ')',
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        navigation.goBack();
      })
  }

  render() {
    const { navigation } = this.props;
    const { home, remark, kottypes, kitchenmsgs, tableItems,
      selectedKitchenmsg, selectedTableItems, selectedKottypes,
      overlayProcedureMessageVisible, overlayAlertVisible, overlayAlertInputValue,
    } = this.state;

    return (
      <View style={styles.container}>
        {
          overlayAlertVisible &&
          <OverlayAlert
            input={overlayAlertInputValue}
            handleUpdateFromAlert={this.handleUpdateFromAlert} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.navigateButton}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.navigateButtonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
              <View style={styles.selectionContainer}>
              </View>
            </View>
          </View>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingRight: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.searchBox}>
                  <TextInput style={styles.searchBoxInputValue}
                    placeholder='ENTER MESSAGE'
                    placeholderTextColor='rgba(255,255,255,0.2)'
                    value={remark}
                    onChangeText={(text) => this.setState({ remark: text })}
                  />
                  <TouchableOpacity style={styles.searchBoxButton}
                    onPress={() => this.doReset()}>
                    <AntDesign name='close' size={24} color={theme.grey4} />
                  </TouchableOpacity>
                </View>
                <View style={styles.searchBoxInfo}>
                  <View style={styles.titleSecondContainer}>
                    <Text style={styles.titleText}>Desc</Text>
                  </View>
                </View>
                <FlatList
                  style={{ flex: 1, marginHorizontal: 2.5 }}
                  showsVerticalScrollIndicator={false}
                  data={kitchenmsgs}
                  numColumns={2}
                  keyExtractor={item => item.recKey.toString()}
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      style={[styles.tableItemContainer, { marginHorizontal: 2.5 }, selectedKitchenmsg?.recKey === item?.recKey && { backgroundColor: theme.occupied }]}
                      onPress={() => this.doPressKitchenmsg(item)}>
                      <View style={styles.tableItemSecondContainer}>
                        <Text style={styles.tableItemText}>
                          {item.name}
                        </Text>
                      </View>
                    </TouchableOpacity>
                  )}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingLeft: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.searchBoxInfo}>
                  <View style={styles.titleFirstContainer}>
                    <Text style={styles.titleText}>Qty</Text>
                  </View>
                  <View style={styles.titleSecondContainer}>
                    <Text style={styles.titleText}>Desc</Text>
                  </View>
                  <View style={styles.titleThirdContainer}>
                    <Text style={styles.titleText}>Price</Text>
                  </View>
                  <View style={styles.titleFourthContainer}>
                    <Text style={styles.titleText}>Total</Text>
                  </View>
                </View>
                <FlatList
                  style={{ flex: 1 }}
                  showsVerticalScrollIndicator={false}
                  data={tableItems}
                  keyExtractor={item => item.recKey.toString()}
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      style={[styles.tableItemContainer, selectedTableItems.some(el => el.recKey === item.recKey) && { backgroundColor: theme.occupied }, item.refRecKey && { opacity: 0.2 }]}
                      disabled={item.refRecKey ? true : false}
                      onPress={() => this.doPressTableItem(item)}>
                      <View style={[styles.tableItemFirstContainer, item.fireFlg === 'Y' && { borderRightColor: theme.reserved }]}><Text style={styles.tableItemText}>{item.orderQty}</Text></View>
                      <View style={styles.tableItemSecondContainer}>
                        <Text style={styles.tableItemText}>
                          {item.refRecKey ? '- ' : null}{item.menuName}
                        </Text>
                        {item.menuMod ? <Text style={styles.tableItemModText}>{item.menuMod}</Text> : null}
                      </View>
                      <View style={styles.tableItemThirdContainer}>
                        <Text style={styles.tableItemText}>
                          {item.netPrice && item.netPrice.toFixed(2)}
                        </Text>
                      </View>
                      <View style={styles.tableItemFourthContainer}>
                        <Text style={styles.tableItemText}>
                          {item.lineTotal && item.lineTotal.toFixed(2)}
                        </Text>
                      </View>
                    </TouchableOpacity>
                  )}
                />
              </View>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.searchBoxInfo}>
                  <View style={{ paddingLeft: 10, alignItems: 'center', justifyContent: 'center', }}>
                    <Text style={styles.titleText}>Destination</Text>
                  </View>
                  <View style={styles.titleFourthContainer}>
                    {
                      selectedKottypes.length > 1 ?
                        <Text style={[styles.titleText, { color: 'white' }]}>Various</Text>
                        :
                        selectedKottypes.length === 1 ?
                          <Text style={[styles.titleText, { color: 'white' }]}>{kottypes?.find(el => el.kotType === selectedKottypes[0].kotType)?.kotName}</Text>
                          :
                          <Text style={[styles.titleText, { color: 'white' }]}>-</Text>
                    }
                  </View>
                </View>
                <FlatList
                  style={{ flex: 1 }}
                  showsVerticalScrollIndicator={false}
                  data={kottypes}
                  keyExtractor={item => item.recKey.toString()}
                  ListHeaderComponent={
                    <TouchableOpacity
                      style={[styles.tableItemContainer, !kottypes.some(el => !selectedKottypes.some(obj => obj.recKey === el.recKey)) && { backgroundColor: theme.occupied }]}
                      onPress={() => this.doPressAllKottype()}>
                      <View style={styles.tableItemSecondContainer}>
                        <Text style={styles.tableItemText}>
                          ALL KOT PRINTERS
                        </Text>
                      </View>
                    </TouchableOpacity>
                  }
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      style={[styles.tableItemContainer, selectedKottypes.some(el => el.recKey === item.recKey) && { backgroundColor: theme.occupied }]}
                      onPress={() => this.doPressKottype(item)}>
                      <View style={[styles.tableItemSecondContainer, { flexDirection: 'row', justifyContent: 'space-between', paddingRight: 10, alignItems: 'center' }]}>
                        <Text style={styles.tableItemText}>
                          {item.kotName}
                        </Text>
                        <Text style={styles.tableItemText}>
                          {item.kotType}
                        </Text>
                      </View>
                    </TouchableOpacity>
                  )}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={[styles.navigateButton, ((selectedTableItems.length === 0 && selectedKottypes.length === 0) || !remark) && { opacity: 0.2 }]}
                disabled={(selectedTableItems.length === 0 && selectedKottypes.length === 0) || !remark}
                onPress={() => this.doPressFire()}>
                <AntDesign
                  name='customerservice'
                  color='white'
                  size={36}
                />
                <Text style={styles.navigateButtonText}>{intl.get('fire')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

