import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, FlatList,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';

export default class OverlayUpdateTips extends Component {

  constructor(props) {
    super(props);
    this.state = {
      overlayText: '',
      overlaySearchText: '',
      paymentMethods: props.paymentMethods,
      availablePaymentMethods: props.paymentMethods,
      selectedPaymentMethod: null,
    };
    this.handleOverlaySearchText = this.handleOverlaySearchText.bind(this);
  }

  handleOverlaySearchText(text) {
    const { paymentMethods } = this.state;
    let availablePaymentMethodsUpdate = [];
    this.setState({ overlaySearchText: text });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);

    availablePaymentMethodsUpdate = paymentMethods;
    if (!text) {
      this.setState({
        availablePaymentMethods: availablePaymentMethodsUpdate
      });
      return;
    }
    availablePaymentMethodsUpdate = availablePaymentMethodsUpdate.filter(item => {
      return item.pmId.toUpperCase().includes(caseText) || item.name.toUpperCase().includes(caseText);
    });
    this.setState({
      availablePaymentMethods: availablePaymentMethodsUpdate
    });
  }

  render() {
    const { overlayText, selectedPaymentMethod, availablePaymentMethods, overlaySearchText } = this.state;
    console.log('in overlayupdatetips', availablePaymentMethods);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Update Tips</Text>
          </View>
          <View style={styles.searchBoxWrapper}>
            <View style={styles.overlaySearchBox}>
              <TextInput style={styles.overlaySearchBoxInput}
                autoFocus={true}
                keyboardType='numeric'
                placeholder='ENTER TIPS'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(text) => this.setState({ overlayText: text })}
                value={overlayText}
              />
              <TouchableOpacity style={styles.overlaySearchBoxButton}
                onPress={() => {
                  this.setState({ overlayText: '' })
                  Keyboard.dismiss()
                }}>
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
          </View>
          <View style={{ paddingBottom: 10 }}></View>
          <View style={styles.selectWrapper}>
            <View style={styles.searchBoxWrapper}>
              <View style={[styles.overlaySearchBox, { marginHorizontal: 0 }]}>
                <TextInput style={styles.overlaySearchBoxInput}
                  placeholder='ENTER SEARCH TEXT'
                  placeholderTextColor='rgba(255,255,255,0.2)'
                  onChangeText={(searchText) => this.handleOverlaySearchText(searchText)}
                  value={overlaySearchText}
                />
                <TouchableOpacity style={styles.overlaySearchBoxButton}
                  onPress={() => {
                    this.setState({ overlaySearchText: '' }, () => this.handleOverlaySearchText(''))
                    Keyboard.dismiss()
                  }}>
                  <AntDesign name='close' size={24} color={theme.grey4} />
                </TouchableOpacity>
              </View>
            </View>
            <View style={styles.searchBoxInfo}>
              <Text style={styles.searchBoxInfoText}>{availablePaymentMethods.length} ITEMS</Text>
            </View>
            <FlatList
              style={[{ flex: 1 }]}
              data={availablePaymentMethods}
              keyExtractor={(item, index) => index.toString()}
              renderItem={({ item }) => (
                <TouchableOpacity
                  style={[styles.overlayGrid, selectedPaymentMethod?.pmId === item.pmId && { backgroundColor: theme.occupied }]}
                  onPress={() => this.setState({ selectedPaymentMethod: item, })}>
                  <View style={styles.overlayGridFirst}>
                    <View style={styles.overlayGridFirstLeft}>
                      <Text style={styles.overlayGridFirstLeftText}>{item.pmId}</Text>
                    </View>
                    <View style={styles.overlayGridFirstRight}>
                      <Text style={styles.overlayGridFirstRightText}></Text>
                    </View>
                  </View>
                  <View style={styles.overlayGridSecond}>
                    <Text style={styles.overlayGridSecondText}>
                      {item.name}
                    </Text>
                  </View>
                  <View style={styles.overlayGridThird}>
                    <View style={styles.overlayGridThirdLeft}>
                    </View>
                    <View style={styles.overlayGridThirdRight}>
                      <Text style={styles.overlayGridThirdRightText}></Text>
                    </View>
                  </View>
                </TouchableOpacity>
              )}
            />
          </View>
          <TouchableOpacity style={styles.overlayOption}
            onPress={() => this.props.handleUpdateFromUpdateTips(overlayText, selectedPaymentMethod)}>
            <Text style={styles.overlayOptionText}>{intl.get('submit')}</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromUpdateTips()}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    height: '80%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
  },
  searchBoxWrapper: {
    height: 50,
    flexDirection: 'row',
    alignItems: 'center',
  },
  overlaySearchBox: {
    backgroundColor: theme.grey1,
    height: 50,
    borderRadius: 100,
    marginHorizontal: 5,
    flexDirection: 'row',
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlaySearchBoxInput: {
    flex: 1,
    fontFamily: 'varela-round',
    fontSize: 16,
    color: 'white',
    paddingHorizontal: 20,
    height: 50,
  },
  overlaySearchBoxButton: {
    height: 50,
    width: 50,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 50,
  },
  overlayBackButton: {
    // backgroundColor: 'white',
    width: 50,
    height: 50,
    borderRadius: 50,
    marginHorizontal: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayGrid: {
    flex: 1,
    margin: 5,
    marginTop: 0,
    marginBottom: 10,
    backgroundColor: theme.grey4,
    flexDirection: 'column',
    padding: 10,
  },
  overlayGridFirst: {
    flexDirection: 'row'
  },
  overlayGridSecond: {
    marginVertical: 5,
  },
  overlayGridThird: {
    flexDirection: 'row'
  },
  overlayGridFirstLeft: {
    flex: 1,
  },
  overlayGridFirstRight: {
    flex: 1,
    alignItems: 'flex-end',
  },
  overlayGridThirdLeft: {
    flex: 1,
  },
  overlayGridThirdRight: {
    flex: 1,
    alignItems: 'flex-end',
  },
  overlayGridFirstLeftText: {
    color: 'white',
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  overlayGridFirstRightText: {
    color: theme.vacant,
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  overlayGridSecondText: {
    color: 'white',
    fontSize: 16,
    fontFamily: 'varela-round',
    opacity: 0.8,
  },
  overlayGridThirdRightText: {
    color: 'white',
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  searchBoxInfo: {
    height: 20,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  searchBoxInfoText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
  selectText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 16,
  },
  selectWrapper: {
    flex: 1,
    backgroundColor: theme.grey1,
    borderRadius: 5,
    marginHorizontal: 5,
    marginBottom: 5,
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
});