import { Component } from 'react';
import { AntDesign, Ionicons, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Image, TextInput, Alert, Platform, Dimensions } from 'react-native';
import * as Linking from 'expo-linking';
import Constants from 'expo-constants';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Text, debug } from '../assets/utility';
import intl from 'react-intl-universal';
import theme from '../assets/theme';
import Logo from '../assets/logo-small-light.png';
import moment from 'moment';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import OverlayLanguage from './OverlayLanguage';

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      preference: null,
      language: '-',
      serviceEntry: '',
      serviceEntryObj: null,
      accessCode: '',
      password: '',
      userId: '',
      home: null,

      dev: false,
      // dev: true,
    };

    this.handleUpdateFromLanguage = this.handleUpdateFromLanguage.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  async componentDidMount() {
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
    const serviceEntry = await AsyncStorage.getItem('serviceEntry');
    const language = await AsyncStorage.getItem('language');
    let preference = await AsyncStorage.getItem('preference');
    if (preference) {
      preference = JSON.parse(preference);
    } else {
      preference = {
        tcolsize: Platform.OS === 'web' ? 5 : 4,
        mcolsize: Platform.OS === 'web' ? 3 : 2,
        pcolsize: Platform.OS === 'web' ? 5 : 3,
        mquick: 'Y',
      };
      AsyncStorage.setItem('preference', JSON.stringify(preference))
    }

    console.log('mounting service', serviceEntry)
    let serviceEntryObj = await AsyncStorage.getItem('serviceEntryObj');
    if (serviceEntryObj) {
      serviceEntryObj = JSON.parse(serviceEntryObj);
    } else {
      serviceEntryObj = null;
    }
    this.setState({
      preference,
      serviceEntry,
      language: language || 'en',
      serviceEntryObj,
    })
    // dev only
    if (this.state.dev) {
      this.setState({
        accessCode: 'qwe'
      }, () => this.doSubmitAccessCode())
    }
  }

  async didFocus() {
    const { route } = this.props;
    let preference = await AsyncStorage.getItem('preference');
    const serviceEntry = await AsyncStorage.getItem('serviceEntry');
    const serviceEntryObj = await AsyncStorage.getItem('serviceEntryObj');
    if (preference) {
      this.setState({ preference: JSON.parse(preference) })
    }
    if (serviceEntryObj) {
      this.setState({ serviceEntryObj: JSON.parse(serviceEntryObj) })
    }
    this.setState({ serviceEntry })
    if (route.params?.sync && (moment(route.params.sync).diff(moment(), 'seconds') > -1)) {
      this.doSubmitAccessCode();
    } else {
      if (!this.state.dev) {
        this.setState({
          accessCode: ''
        })
      }
    }
  }

  componentWillUnmount() {
    this.sub();
  }

  handleUpdateFromLanguage(item) {
    this.setState({
      overlayLanguageVisible: false,
    })
    if (!item) {
      return;
    }
    intl.options.currentLocale = item;
    AsyncStorage.setItem('language', item);
    this.setState({
      language: item,
    })
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  doPressDownload() {
    Alert.alert(
      `Download latest bistro app?`,
      '',
      [
        {
          text: 'Cancel',
        },
        {
          text: 'Ok',
          onPress: () => this.doDownload()
        },
      ],
    );
  }

  doDownload() {
    if (Platform.OS === 'ios') {
      Linking.openURL(`itms-apps://itunes.apple.com/app/id6443468946`);
    }
  }

  doSubmitPassword() {
    const { serviceEntry, userId, password } = this.state;
    let link = serviceEntry + 'api/login-shop';
    let body = {
      userId: userId,
      password: password,
    };
    this.doLogIn(link, body);
  }

  doSubmitAccessCode() {
    const { serviceEntry, accessCode } = this.state;
    let link = serviceEntry + 'api/login-shop';
    let body = {
      accessCode: accessCode,
    };
    this.doLogIn(link, body);
  }

  async doLogIn(link, body) {
    const { navigation } = this.props;
    const { serviceEntry } = this.state;

    fetch(link, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post login', response);
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          throw response;
        }
        this.setState({
          home: response,
        });
        return Promise.all([
          AsyncStorage.setItem('home', JSON.stringify(response)),
          response,
        ]);
      })
      .then(([response, home]) => {// do parallel chain

        let promiseArray = [];

        let url = serviceEntry + 'api/sections';
        let params = new URLSearchParams();
        params.append('shopId', home.shopId);
        url += ('?' + params);
        let promiseObject = { url: url, nameString: 'sections' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/tablemas';
        params = new URLSearchParams();
        params.append('shopId', home.shopId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'tablemas' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/foldergrps';
        params = new URLSearchParams();
        params.append('orgId', home.orgId);
        params.append('shopId', home.shopId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'foldergrps' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/folders';
        params = new URLSearchParams();
        params.append('shopId', home.shopId);
        params.append('orgId', home.orgId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'folders' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/menus';
        params = new URLSearchParams();
        params.append('orgId', home.orgId);
        params.append('shopId', home.shopId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'menus' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/mods';
        params = new URLSearchParams();
        params.append('orgId', home.orgId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'mods' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/mod-bundles';
        params = new URLSearchParams();
        params.append('orgId', home.orgId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'modBundles' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/combo-items';
        params = new URLSearchParams();
        params.append('orgId', home.orgId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'comboItems' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/combo-groups';
        params = new URLSearchParams();
        params.append('orgId', home.orgId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'comboGroups' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/payment-methods';
        params = new URLSearchParams();
        params.append('shopId', home.shopId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'paymentMethods' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/vip-sources';
        promiseObject = { url: url, nameString: 'vipSources' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/discounts';
        params = new URLSearchParams();
        params.append('shopId', home.shopId);
        params.append('orgId', home.orgId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'discounts' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/returns';
        promiseObject = { url: url, nameString: 'returns' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/settings';
        params = new URLSearchParams();
        params.append('appCode', 'RESTMENU');
        params.append('orgId', home.orgId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'settings' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/privileges';
        params = new URLSearchParams();
        params.append('userId', home.userId);
        params.append('locId', home.locId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'privileges' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/cashier-printers';
        params = new URLSearchParams();
        params.append('shopId', home.shopId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'cashierPrinters' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/kottype';
        params = new URLSearchParams();
        params.append('shopId', home.shopId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'kottypes' };
        promiseArray.push(promiseObject);

        url = serviceEntry + 'api/kitchenmsgs';
        params = new URLSearchParams();
        params.append('orgId', home.orgId);
        params.append('shopId', home.shopId);
        url += ('?' + params);
        promiseObject = { url: url, nameString: 'kitchenmsgs' };
        promiseArray.push(promiseObject);

        return Promise.all([
          ...promiseArray.map(el =>
            fetch(el.url)
              .then(response => response.json())
              .then(response => {
                console.log('fetching', el.nameString, response);
                return ({ [el.nameString]: response });
              })
          )
        ]);
      })
      .then(response => {
        const masters = response.reduce((o, item) => ({ ...o, [Object.keys(item)[0]]: Object.values(item)[0] }), {});
        console.log('masters', masters);
        navigation.navigate('Main', {
          masters,
          home: this.state.home,
          serviceEntryObj: this.state.serviceEntryObj,
          serviceEntry: this.state.serviceEntry,
          preference: this.state.preference
        })
      })
      .catch(error => {
        console.log('error', error);
        this.setState({
          accessCode: '',
        })
      });
  }

  doPressKey(item) {
    const { accessCode } = this.state;
    let accessCodeUpdate = accessCode + item;
    this.setState({ accessCode: accessCodeUpdate });
  }

  render() {
    const { navigation, route } = this.props;
    const { serviceEntryObj, serviceEntry, language, userId, password, accessCode,
      overlayProcedureMessageVisible, overlayLanguageVisible } = this.state;

    return (
      <View style={{ flex: 1, flexDirection: 'row' }}>
        {
          overlayLanguageVisible &&
          <OverlayLanguage
            handleUpdateFromLanguage={this.handleUpdateFromLanguage} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={{ flex: 1, backgroundColor: 'white', }}>
          <View style={{ flex: 2, }}>
            <View style={{ flex: 1, flexDirection: 'row', justifyContent: 'space-between' }}>
              <View>
                <TouchableOpacity style={{ margin: 20, height: 36, width: 36, borderRadius: 18, justifyContent: 'center', alignItems: 'center', backgroundColor: theme.paid }}
                  onPress={() => navigation.navigate('Register')}>
                  <Ionicons name='ios-globe' size={24} color={theme.grey1} style={{ height: 24, width: 24, marginTop: -2 }} />
                </TouchableOpacity>
                <TouchableOpacity style={{ marginHorizontal: 20, height: 36, width: 36, borderRadius: 18, justifyContent: 'center', alignItems: 'center', backgroundColor: theme.reserved }}
                  onPress={() => navigation.navigate('Preference')}>
                  <MaterialIcons name="miscellaneous-services" size={24} color={theme.grey1} />
                </TouchableOpacity>
                {
                  Platform.OS === 'ios' &&
                  <TouchableOpacity style={{ margin: 20, height: 36, width: 36, borderRadius: 18, justifyContent: 'center', alignItems: 'center', backgroundColor: theme.occupied }}
                    onPress={() => this.doPressDownload()}>
                    <AntDesign name="clouddownload" size={24} color={theme.grey1} />
                  </TouchableOpacity>
                }
              </View>

            </View>
            <View style={{ margin: 20, alignItems: 'flex-end', justifyContent: 'flex-end', }}>
              <Text style={{ color: theme.paid }}>{serviceEntryObj?.name || 'Entry'}</Text>
              <Text style={{ fontSize: 10, marginBottom: 5, color: theme.occupied }} numberOfLines={1}>{serviceEntry}</Text>
              <Text style={{ fontSize: 18 }}>AN EXTENSION OF THE EPB SYSTEM</Text>
              <Text style={{ fontSize: 18 }}>VERSION {Constants.manifest.version?.split('.')[0]}</Text>
            </View>
          </View>
          <Image
            style={{ flex: 1, width: '100%', }}
            source={Logo}
            resizeMode='contain'
          />
        </View>
        <View style={{ flex: 1, backgroundColor: theme.grey1, justifyContent: 'center', alignItems: 'center' }}>
          <TouchableOpacity style={{ position: 'absolute', right: 20, top: 20, minHeight: 40, alignItems: 'flex-end' }}
            onPress={() => this.setState({ overlayLanguageVisible: true })}>
            <Text style={{ color: 'white', textTransform: 'uppercase' }}>{language === 'ja' ? '日本語' : language === 'zhs' ? '简体中文' : language === 'zht' ? '繁體中文' : 'EN'}</Text>
            <Text style={{ color: 'white', fontSize: 10 }}>LANGUAGE 语言 ランゲージ</Text>
          </TouchableOpacity>
          <View style={styles.textInputContainer}>
            <TextInput style={styles.textInput}
              placeholder={intl.get('accesscode')}
              placeholderTextColor='white'
              placeholderStyle={{ textTransform: 'uppercase' }}
              onChangeText={text => this.setState({ accessCode: text })}
              value={accessCode}
              secureTextEntry
              autoComplete='off'
              autoCorrect={false}
              onSubmitEditing={() => this.doSubmitAccessCode()}
            />
          </View>

          <View>
            <View style={styles.buttonsContainer}>
              <TouchableOpacity style={styles.button}
                onPress={() => this.doPressKey('1')}>
                <Text style={styles.buttonText}>1</Text>
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button]}
                onPress={() => this.doPressKey('2')}>
                <Text style={styles.buttonText}>2</Text>
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button]}
                onPress={() => this.doPressKey('3')}>
                <Text style={styles.buttonText}>3</Text>
              </TouchableOpacity>
            </View>
            <View style={styles.buttonsContainer}>
              <TouchableOpacity style={styles.button}
                onPress={() => this.doPressKey('4')}>
                <Text style={styles.buttonText}>4</Text>
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button]}
                onPress={() => this.doPressKey('5')}>
                <Text style={styles.buttonText}>5</Text>
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button]}
                onPress={() => this.doPressKey('6')}>
                <Text style={styles.buttonText}>6</Text>
              </TouchableOpacity>
            </View>
            <View style={styles.buttonsContainer}>
              <TouchableOpacity style={styles.button}
                onPress={() => this.doPressKey('7')}>
                <Text style={styles.buttonText}>7</Text>
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button]}
                onPress={() => this.doPressKey('8')}>
                <Text style={styles.buttonText}>8</Text>
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button]}
                onPress={() => this.doPressKey('9')}>
                <Text style={styles.buttonText}>9</Text>
              </TouchableOpacity>
            </View>
            <View style={styles.buttonsContainer}>
              <TouchableOpacity style={[styles.button, { backgroundColor: theme.grey1 }]}
                onPress={() => this.setState({ accessCode: '' })}>
                <AntDesign name='close' color='white' size={24} />
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button]}
                onPress={() => this.doPressKey('0')}>
                <Text style={styles.buttonText}>0</Text>
              </TouchableOpacity>
              <TouchableOpacity style={[styles.button, { backgroundColor: theme.grey1 }]}
                onPress={() => this.doSubmitAccessCode()}>
                <AntDesign name='enter' color='white' size={24} />
              </TouchableOpacity>
            </View>
          </View>

        </View>
      </View >
    );
  }
}

const keyHeight = Dimensions.get('window').height / 8;

const styles = StyleSheet.create({
  textInputContainer: {
    width: '80%',
    marginTop: 20,
    marginBottom: 40,
    height: 60,
    borderRadius: 100,
    backgroundColor: 'black',
  },
  textInput: {
    flex: 1,
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
    paddingHorizontal: 20,
    borderRadius: 100,
  },
  buttonsContainer: {
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
  },
  button: {
    height: keyHeight - 10,
    width: keyHeight - 10,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'black',
    borderRadius: 5,
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 24,
    textTransform: 'uppercase',
  },
});
