import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions, Platform,
  FlatList, ScrollView,
} from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { AntDesign } from '@expo/vector-icons';
import OverlayAlert from './OverlayAlert';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import theme from '../assets/theme';
import intl from 'react-intl-universal';

export default class Preference extends Component {

  constructor(props) {
    super(props);
    this.state = {
      // tcolsize: '',
      // mcolsize: '',
      // mquick: '',
      preference: null,

      overlayProcedureMessageVisible: false,
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    };
    this.handleUpdateFromAlert = this.handleUpdateFromAlert.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  async componentDidMount() {
    let preference = await AsyncStorage.getItem('preference');
    console.log('here', preference)
    if (preference) {
      preference = JSON.parse(preference);
      this.setState({
        preference
      })
    }
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromAlert(text) {
    this.setState({
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    });
    if (!text) {
      return;
    }
    if (text === 'doReset') {
      this.doReset();
    }
  }

  doPressReset() {
    let options = [];
    options.push({ text: intl.get('yes'), handleText: 'doReset' });
    this.setState({
      overlayAlertInputValue: {
        title: intl.get('action'),
        subtitle: `${intl.get('reset')} ${intl.get('preference')}`,
        text: 'Reset all to factory preset?',
        ok: false,
        cancel: true,
        options: options,
      },
      overlayAlertVisible: true,
    })
  }

  doReset() {
    let preference = {
      tcolsize: Platform.OS === 'web' ? 5 : 4,
      mcolsize: Platform.OS === 'web' ? 3 : 2,
      pcolsize: Platform.OS === 'web' ? 5 : 3,
      mquick: 'Y',
    };
    this.setState({ preference })
    AsyncStorage.setItem('preference', JSON.stringify(preference))
  }

  doTcolsize() {
    const { preference } = this.state;
    let preferenceUpdate = preference;
    if (preferenceUpdate.tcolsize < 8) {
      preferenceUpdate.tcolsize = preferenceUpdate.tcolsize + 1;
    } else {
      preferenceUpdate.tcolsize = 3;
    }
    this.setState({
      preference: preferenceUpdate,
    })
    AsyncStorage.setItem('preference', JSON.stringify(preferenceUpdate))
  }

  doMcolsize() {
    const { preference } = this.state;
    let preferenceUpdate = preference;
    if (preferenceUpdate.mcolsize < 6) {
      preferenceUpdate.mcolsize = preferenceUpdate.mcolsize + 1;
    } else {
      preferenceUpdate.mcolsize = 1;
    }
    this.setState({
      preference: preferenceUpdate,
    })
    AsyncStorage.setItem('preference', JSON.stringify(preferenceUpdate))
  }

  doPcolsize() {
    const { preference } = this.state;
    let preferenceUpdate = preference;
    if (preferenceUpdate.pcolsize < 7) {
      preferenceUpdate.pcolsize = preferenceUpdate.pcolsize + 1;
    } else {
      preferenceUpdate.pcolsize = 2;
    }
    this.setState({
      preference: preferenceUpdate,
    })
    AsyncStorage.setItem('preference', JSON.stringify(preferenceUpdate))
  }

  doMquick() {
    const { preference } = this.state;
    let preferenceUpdate = preference;
    if (preferenceUpdate.mquick === 'Y') {
      preferenceUpdate.mquick = 'N';
    } else {
      preferenceUpdate.mquick = 'Y';
    }
    this.setState({
      preference: preferenceUpdate,
    })
    AsyncStorage.setItem('preference', JSON.stringify(preferenceUpdate))
  }

  render() {
    const { navigation } = this.props;
    const { preference,
      overlayProcedureMessageVisible, overlayAlertVisible, overlayAlertInputValue, } = this.state;
    console.log('render preference');

    return (
      <View style={styles.container}>
        {
          overlayAlertVisible &&
          <OverlayAlert
            input={overlayAlertInputValue}
            handleUpdateFromAlert={this.handleUpdateFromAlert} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
              <View style={styles.selectionContainer}>
              </View>
            </View>
          </View>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingRight: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.gap}></View>
                <ScrollView
                  style={{ flex: 1, }}
                  showsVerticalScrollIndicator={false}>

                  <TouchableOpacity
                    style={styles.grid}
                    onPress={() => this.doTcolsize()}>
                    <View style={styles.gridFirst}>
                      <Text style={styles.gridFirstLeftText}>TCOLSIZE</Text>
                      <Text style={[styles.gridFirstRightText]}>3-8</Text>
                    </View>
                    <View style={styles.gridSecond}>
                      <Text style={styles.gridSecondText}>Number of Columns for Table</Text>
                      <Text style={[styles.gridSecondText, { color: theme.reserved }]}>{preference?.tcolsize}</Text>
                    </View>
                    <View style={styles.gridThird}>
                      <Text style={[styles.gridThirdRightText, { color: theme.paid }]}></Text>
                      {
                        ((Platform.OS === 'web' && preference?.tcolsize === 5) || (Platform.OS !== 'web' && preference?.tcolsize === 4)) ?
                          <Text style={[styles.gridThirdRightText, { color: theme.vacant }]}>{intl.get('default')}</Text>
                          :
                          <Text style={[styles.gridThirdRightText, { color: theme.paid }]}>{intl.get('preference')}</Text>
                      }
                    </View>
                  </TouchableOpacity>

                  <TouchableOpacity
                    style={styles.grid}
                    onPress={() => this.doPcolsize()}>
                    <View style={styles.gridFirst}>
                      <Text style={styles.gridFirstLeftText}>PCOLSIZE</Text>
                      <Text style={[styles.gridFirstRightText]}>2-7</Text>
                    </View>
                    <View style={styles.gridSecond}>
                      <Text style={styles.gridSecondText}>Number of Columns for POS</Text>
                      <Text style={[styles.gridSecondText, { color: theme.reserved }]}>{preference?.pcolsize}</Text>
                    </View>
                    <View style={styles.gridThird}>
                      <Text style={[styles.gridThirdRightText, { color: theme.paid }]}></Text>
                      {
                        ((Platform.OS === 'web' && preference?.pcolsize === 5) || (Platform.OS !== 'web' && preference?.pcolsize === 3)) ?
                          <Text style={[styles.gridThirdRightText, { color: theme.vacant }]}>{intl.get('default')}</Text>
                          :
                          <Text style={[styles.gridThirdRightText, { color: theme.paid }]}>{intl.get('preference')}</Text>
                      }
                    </View>
                  </TouchableOpacity>


                </ScrollView>
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingLeft: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.gap}></View>
                <ScrollView
                  style={{ flex: 1, }}
                  showsVerticalScrollIndicator={false}>

                  <TouchableOpacity
                    style={styles.grid}
                    onPress={() => this.doMcolsize()}>
                    <View style={styles.gridFirst}>
                      <Text style={styles.gridFirstLeftText}>MCOLSIZE</Text>
                      <Text style={[styles.gridFirstRightText]}>1-6</Text>
                    </View>
                    <View style={styles.gridSecond}>
                      <Text style={styles.gridSecondText}>Number of Columns for Menu</Text>
                      <Text style={[styles.gridSecondText, { color: theme.reserved }]}>{preference?.mcolsize}</Text>
                    </View>
                    <View style={styles.gridThird}>
                      <Text style={[styles.gridThirdRightText, { color: theme.paid }]}></Text>
                      {
                        ((Platform.OS === 'web' && preference?.mcolsize === 3) || (Platform.OS !== 'web' && preference?.mcolsize === 2)) ?
                          <Text style={[styles.gridThirdRightText, { color: theme.vacant }]}>{intl.get('default')}</Text>
                          :
                          <Text style={[styles.gridThirdRightText, { color: theme.paid }]}>{intl.get('preference')}</Text>
                      }
                    </View>
                  </TouchableOpacity>
                  <TouchableOpacity
                    style={styles.grid}
                    onPress={() => this.doMquick()}>
                    <View style={styles.gridFirst}>
                      <Text style={styles.gridFirstLeftText}>MQUICK</Text>
                      <Text style={[styles.gridFirstRightText]}>Y/N</Text>
                    </View>
                    <View style={styles.gridSecond}>
                      <Text style={styles.gridSecondText}>Quick Select for Menu</Text>
                      <Text style={[styles.gridSecondText, { color: theme.reserved }]}>{preference?.mquick}</Text>
                    </View>
                    <View style={styles.gridThird}>
                      <Text style={[styles.gridThirdRightText, { color: theme.paid }]}></Text>
                      {
                        preference?.mquick === 'Y' ?
                          <Text style={[styles.gridThirdRightText, { color: theme.vacant }]}>{intl.get('default')}</Text>
                          :
                          <Text style={[styles.gridThirdRightText, { color: theme.paid }]}>{intl.get('preference')}</Text>
                      }
                    </View>
                  </TouchableOpacity>


                </ScrollView>
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => this.doPressReset()}>
                <AntDesign
                  name='warning'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('reset')} {intl.get('all')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

