import React, { Component } from 'react';
import {
  StyleSheet, Text, View, TouchableOpacity,
  Dimensions, TextInput,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';

export default class OverlaySplitBill extends Component {

  constructor(props) {
    super(props);
    this.state = {
      overlayNumber: 2,
    };
    this.handleQtyChange = this.handleQtyChange.bind(this);
  }

  handleQtyChange(type) {
    if (type === 'PLUS') {
      this.setState((prevState, props) => ({
        overlayNumber: prevState.overlayNumber + 1
      }));
    }
    else if (type === 'MINUS') {
      if (this.state.overlayNumber === 2) return;
      this.setState((prevState, props) => ({
        overlayNumber: prevState.overlayNumber - 1
      }));
    }
  }

  handleNumberChange(text) {
    if (isNaN(text)) {
      this.setState({ overlayNumber: 2 })
    } else {
      this.setState({ overlayNumber: Number(text) })
    }
  }

  render() {
    const { overlayNumber } = this.state;
    console.log('in overlaysplitbill');

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Split Bill</Text>
            <Text style={styles.overlayText}>Enter the number of bills to split into</Text>
          </View>
          <View style={styles.adjustContainer}>
            <TouchableOpacity style={styles.adjustFirst}
              onPress={() => this.handleQtyChange('MINUS')}>
              <AntDesign name='minus' size={32} color='rgba(0,0,0,0.85)' />
            </TouchableOpacity>
            <View style={styles.adjustSecond}>
              <TextInput style={styles.overlayNumber}
                selectTextOnFocus
                keyboardType='numeric'
                value={overlayNumber?.toString()}
                onChangeText={(searchText) => this.handleNumberChange(searchText)} />
            </View>
            <TouchableOpacity style={styles.adjustThird}
              onPress={() => this.handleQtyChange('PLUS')}>
              <AntDesign name='plus' size={32} color='rgba(0,0,0,0.85)' />
            </TouchableOpacity>
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={[styles.button, overlayNumber === 2 && { backgroundColor: theme.occupied }]}
              onPress={() => this.setState({ overlayNumber: 2 })}>
              <Text style={styles.buttonText}>2</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, overlayNumber === 3 && { backgroundColor: theme.reserved }]}
              onPress={() => this.setState({ overlayNumber: 3 })}>
              <Text style={styles.buttonText}>3</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, overlayNumber === 4 && { backgroundColor: theme.paid }]}
              onPress={() => this.setState({ overlayNumber: 4 })}>
              <Text style={styles.buttonText}>4</Text>
            </TouchableOpacity>
          </View>
          <TouchableOpacity style={styles.overlayOption}
            onPress={() => this.props.handleUpdateFromSplitBill(overlayNumber, 'ITEM')}>
            <Text style={styles.overlayOptionText}>Split By Item</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.overlayOption}
            onPress={() => this.props.handleUpdateFromSplitBill(overlayNumber, 'AMOUNT')}>
            <Text style={styles.overlayOptionText}>Split By Amount</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromSplitBill('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const keyHeight = Dimensions.get('window').height / 8;

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayNumber: {
    width: '100%',
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 32,
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  buttonsContainer: {
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
    marginBottom: 30,
  },
  button: {
    height: keyHeight - 10,
    width: keyHeight - 10,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: theme.grey1,
    borderRadius: 5,
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 24,
    textTransform: 'uppercase',
  },
  adjustContainer: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  adjustFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
  },
  adjustSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    marginHorizontal: 20,
  },
  adjustThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
  },
});