import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, Image, TextInput, Alert, ScrollView, Platform } from 'react-native';
import theme from '../assets/theme';
import { debug } from '../assets/utility';
import intl from 'react-intl-universal';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import OverlayAlert from './OverlayAlert';

export default class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      masters: props.route.params.masters,
      settings: props.route.params.masters.settings,
      modBundles: props.route.params.masters.modBundles,
      mods: props.route.params.masters.mods,
      selectedTable: props.route.params.selectedTable,
      selectedMenu: props.route.params.selectedMenu, // NEW
      selectedTableItem: props.route.params.selectedTableItem, // EDIT

      loading: false,
      menuName: props.route.params.selectedTableItem ? props.route.params.selectedTableItem?.menuName : props.route.params.selectedMenu.menuName,
      qtyInput: props.route.params.selectedTableItem?.orderQty?.toString() || '1',
      orderType: props.route.params.selectedMenu.serviceFlg === 'N' ? 'B' : (props.route.params.selectedTableItem ? (props.route.params.selectedTableItem?.orderType || 'A') : props.route.params.selectedTable?.sectionId === 'SYS' ? 'B' : 'A'),
      lineBreak: props.route.params.selectedTableItem ? props.route.params.selectedTableItem.lineBreak : 'N',

      modInput: props.route.params.selectedTableItem ? (props.route.params.selectedTableItem?.menuMod || '') : (props.route.params.selectedMenu.serviceFlg === 'N' ? `*${intl.get('takeaway')}  ` : ((props.route.params.selectedTable?.sectionId === 'SYS' && props.route.params.masters.settings.find(el => el.setId === 'POSMODE')?.setString === 'A') ? `*${intl.get('takeaway')}  ` : '')),
      modRecKeys: props.route.params.selectedTableItem?.modRecKeys?.split(',') || [],
    };

    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
    this.handleUpdateFromAlert = this.handleUpdateFromAlert.bind(this);
  }

  componentDidMount() {
    console.log('mount menu', this.state.selectedMenu, this.state.selectedTableItem)
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromAlert(text) {
    const { selectedTable, selectedTableItem } = this.state;
    this.setState({
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    });
    if (!text) {
      return;
    }
  }

  doValidateQty(text) {
    if (!text || isNaN(text)) {
      this.setState({
        qtyInput: '0',
      })
    } else {
      this.setState({
        qtyInput: text,
      })
    }
  }

  doChangeQty(type) {
    const { qtyInput } = this.state;
    let qtyInputUpdate = Number(qtyInput);
    if (type === 'PLUS') {
      this.setState({
        qtyInput: (qtyInputUpdate + 1).toString(),
      });
    }
    else if (type === 'MINUS') {
      if (qtyInputUpdate <= 1) return;
      this.setState({
        qtyInput: (qtyInputUpdate - 1).toString(),
      });
    }
  }

  doToggleOrderType() {
    const { orderType, modInput } = this.state;
    let modInputUpdate = modInput;
    if (orderType === 'A') {
      modInputUpdate = modInputUpdate.replace(`*${intl.get('takeaway')}  `, '');
      modInputUpdate = `*${intl.get('takeaway')}  ` + modInputUpdate;
      this.setState({
        orderType: 'B',
        modInput: modInputUpdate,
      });
    } else {
      modInputUpdate = modInputUpdate.replace(`*${intl.get('takeaway')}  `, '');
      this.setState({
        orderType: 'A',
        modInput: modInputUpdate,
      })
    }
  }

  doToggleLineBreak() {
    const { lineBreak } = this.state;
    this.setState({
      lineBreak: lineBreak === 'Y' ? 'N' : 'Y',
    });
  }

  doPressModBundle(item) {
    this.setState({
      selectedModBundle: item,
    })
  }

  doPressMod(item) {
    const { modInput, selectedModBundle, modRecKeys } = this.state;
    let modInputUpdate = modInput;
    let modRecKeysUpdate = modRecKeys;

    if (selectedModBundle.modmas.modType === 'A') {
      // any
      if (modRecKeys.some(el => el === item.recKey.toString())) {
        modInputUpdate = modInputUpdate.replace('*' + item.name + '  ', '');
        modRecKeysUpdate = modRecKeysUpdate.filter(obj => obj !== item.recKey.toString());
      } else {
        modInputUpdate += '*' + item.name + '  ';
        modRecKeysUpdate.push(item.recKey.toString());
      }
    } else {
      // either
      selectedModBundle.moditems.forEach(el => {
        modInputUpdate = modInputUpdate.replace('*' + el.name + '  ', '');
        modRecKeysUpdate = modRecKeysUpdate.filter(obj => obj !== el.recKey.toString());
      })
      if (!modRecKeys.some(el => el === item.recKey.toString())) {
        modInputUpdate += '*' + item.name + '  ';
        modRecKeysUpdate.push(item.recKey.toString());
      }
    }
    this.setState({
      modInput: modInputUpdate,
      modRecKeys: modRecKeysUpdate,
    })
  }

  doSubmit() {
    const { navigation } = this.props;
    const { serviceEntry, home, selectedTable, selectedMenu, selectedTableItem, mods, masters, settings, modBundles,
      menuName, modInput, qtyInput, orderType, lineBreak, modRecKeys } = this.state;

    // need preference to auto go through
    if (!selectedTable) {
      this.setState({
        overlayProcedureMessageVisible: 'MISSING TABLE',
        loading: false,
      })
      return;
    }
    if (selectedTable.sectionId !== 'SYS' && selectedTable.statusFlg !== 'C') {
      this.setState({
        overlayProcedureMessageVisible: 'CHECK TABLE STATUS',
        loading: false,
      })
      return;
    }
    if (modBundles.filter(el => el.modmas.mustFlg === 'Y' && (el.modmas.globalFlg === 'Y' || mods.some(obj => obj.menuId === selectedMenu.menuId && obj.modId === el.modmas.modId))).some(el => !el.moditems.some(obj => modRecKeys.some(el => el === obj.recKey.toString())))) {
      this.setState({
        overlayProcedureMessageVisible: 'CHECK COMPULSORY MODIFIERS',
        loading: false,
      })
      return;
    }

    if (selectedTableItem) {
      // edit item
      let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/update-item';
      const body = {
        userId: home.userId,
        opentableRecKey: selectedTable.recKey,
        opentableItemRecKey: selectedTableItem.recKey,
        restmenuMod: modInput,
        orderQty: qtyInput,
        orderType: orderType,
        modRecKeys: modRecKeys.join(','),
        menuName: menuName,
        lineBreak: lineBreak,
      };
      fetch(url, {
        method: 'POST',
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
        body: JSON.stringify(body),
      })
        .then(response => response.json())
        .then(response => {
          settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
            debug(
              serviceEntry,
              'Update Item',
              home.userName,
              response.message,
              selectedTable.tableId,
              menuName,
            );
          console.log('post update-item successful', response);
          if (response.message) {
            this.setState({
              overlayProcedureMessageVisible: response.message.toUpperCase(),
              loading: false,
            })
            return;
          }
          navigation.goBack();
        })
    } else {
      // add item
      let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/insert-item';
      const body = {
        userId: home.userId,
        opentableRecKey: selectedTable.recKey,
        restmenuRecKey: selectedMenu.recKey,
        restmenuMod: modInput,
        orderQty: qtyInput,
        orderType: orderType,
        modRecKeys: modRecKeys.join(','),
        menuName: menuName,
        lineBreak: lineBreak,
      };
      fetch(url, {
        method: 'POST',
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
        body: JSON.stringify(body),
      })
        .then(response => response.json())
        .then(response => {
          settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
            debug(
              serviceEntry,
              'Insert Item',
              home.userName,
              response.message,
              selectedTable.tableId,
              menuName,
              qtyInput,
            );
          console.log('post insert-item successful', response);
          if (response.message) {
            this.setState({
              overlayProcedureMessageVisible: response.message.toUpperCase(),
              loading: false,
            })
            return;
          }
          navigation.goBack();
        })
    }
  }

  render() {
    const { navigation, route } = this.props;
    const { loading,
      home, settings, selectedMenu, selectedTable, selectedTableItem, masters, mods, modBundles, selectedModBundle,
      menuName, modInput, qtyInput, orderType, lineBreak, modRecKeys,
      overlayProcedureMessageVisible, overlayAlertVisible, overlayAlertInputValue, } = this.state;

    return (
      <View style={{ flex: 1, flexDirection: 'row', backgroundColor: 'rgba(0,0,0,0.9)' }}>
        {
          overlayAlertVisible &&
          <OverlayAlert
            input={overlayAlertInputValue}
            handleUpdateFromAlert={this.handleUpdateFromAlert} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={{ width: 10, }}></View>
        <View style={{ flex: 1, }}>
          <ScrollView contentContainerStyle={{ flexGrow: 1, alignItems: 'flex-end', justifyContent: 'center' }}
            showsVerticalScrollIndicator={false}>
            {
              selectedModBundle?.moditems.map(item =>
                <TouchableOpacity key={item.recKey.toString()} style={[styles.modButton, { margin: 10 }, modRecKeys.some(el => el === item.recKey.toString()) && { backgroundColor: theme.occupied }]}
                  onPress={() => this.doPressMod(item)}>
                  <Text style={styles.modText}>{item.name}</Text>
                  {
                    item.listPrice !== 0 &&
                    <Text style={{ position: 'absolute', top: -15, right: 10, fontFamily: 'varela-round', color: theme.reserved, fontSize: 20, }}>{item.listPrice < 0 ? '' : '+'}{item.listPrice?.toFixed(2)}</Text>
                  }
                </TouchableOpacity>
              )
            }
          </ScrollView>
        </View>
        <View style={{ flex: 2 }}>
          <View style={{ flex: 1, flexDirection: 'row', padding: 5 }}>
            <View style={{ flex: 1, margin: 5, backgroundColor: theme.grey2, borderRadius: 5, }}>
              <View style={{ flex: 1, margin: 5, marginBottom: 0, backgroundColor: theme.grey4 }}>
                <View style={{ alignItems: 'center', marginTop: 10, }}>
                  <View style={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
                    <Text style={{ fontFamily: 'varela-round', color: theme.paid, fontSize: 16, }}>{selectedMenu.menuId} </Text>
                    <Text style={{ fontFamily: 'varela-round', color: theme.reserved, fontSize: 16, }}> ${selectedTableItem?.listPrice?.toFixed(2) || selectedMenu.listPrice?.toFixed(2)}</Text>
                  </View>
                  <TextInput style={{ height: 50, width: '90%', fontFamily: 'varela-round', color: 'white', fontSize: 20, textAlign: 'center', paddingHorizontal: 20, }}
                    editable={(home.adminFlg === 'Y' || masters.privileges.some(el => el.priId === 'EDITITEM') || selectedMenu.listPrice === 0)}
                    multiline
                    blurOnSubmit
                    value={menuName}
                    onChangeText={text => this.setState({ menuName: text })}
                  />
                </View>
                <View style={styles.menuQtyRow}>
                  <TouchableOpacity style={styles.menuQtyRowFirst}
                    onPress={() => this.doChangeQty('MINUS')}>
                    <AntDesign name='minus' size={32} color='rgba(0,0,0,0.85)' />
                  </TouchableOpacity>
                  <View style={styles.menuQtyRowSecond}>
                    <TextInput style={styles.qtyTextInput}
                      value={qtyInput}
                      keyboardType='numeric'
                      onChangeText={text => this.doValidateQty(text)}
                    />
                  </View>
                  <TouchableOpacity style={styles.menuQtyRowThird}
                    onPress={() => this.doChangeQty('PLUS')}>
                    <AntDesign name='plus' size={32} color='rgba(0,0,0,0.85)' />
                  </TouchableOpacity>
                </View>
                <TextInput style={styles.modifierTextInput}
                  multiline={true}
                  placeholder='CHOOSE FROM PREDEFINED MODIFIERS OR ENTER TEXT'
                  placeholderTextColor='rgba(255,255,255,0.2)'
                  value={modInput}
                  onChangeText={text => this.setState({ modInput: text })}
                />
                <ScrollView style={{ flex: 1 }}
                  showsVerticalScrollIndicator={false}>
                  {
                    mods?.filter(el => el.menuId === selectedMenu.menuId).length > 0 &&
                    <>
                      <View style={{ height: 20, justifyContent: 'center', alignItems: 'center', backgroundColor: theme.grey2, }}>
                        <Text style={{ textTransform: 'uppercase', fontFamily: 'varela-round', fontSize: 12, color: theme.grey7, }}>MENU SPECIFIC MODIFIER</Text>
                      </View>
                      <View style={styles.modContainer}>
                        {
                          mods?.filter(el => el.menuId === selectedMenu.menuId)?.map(item => modBundles.find(el => el.modmas.modId === item.modId))?.map(item =>
                            <TouchableOpacity key={item.modmas.recKey.toString()} style={[styles.modButton, item.modmas.mustFlg === 'Y' && { backgroundColor: theme.paid }]}
                              onPress={() => this.doPressModBundle(item)}>
                              <Text style={styles.modText}>{item.modmas.name}</Text>
                            </TouchableOpacity>
                          )
                        }
                      </View>
                    </>
                  }
                  <View style={{ height: 20, justifyContent: 'center', alignItems: 'center', backgroundColor: theme.grey2, }}>
                    <Text style={{ textTransform: 'uppercase', fontFamily: 'varela-round', fontSize: 12, color: theme.grey7, }}>GLOBAL MODIFIER</Text>
                  </View>
                  <View style={styles.modContainer}>
                    {
                      modBundles?.filter(el => el.modmas.globalFlg === 'Y').map(item =>
                        <TouchableOpacity key={item.modmas.recKey.toString()} style={[styles.modButton, item.modmas.mustFlg === 'Y' && { backgroundColor: theme.paid }]}
                          onPress={() => this.doPressModBundle(item)}>
                          <Text style={styles.modText}>{item.modmas.name}</Text>
                        </TouchableOpacity>
                      )
                    }
                  </View>
                </ScrollView>
              </View>

              <View style={{ height: 100, margin: 5, marginTop: 0, flexDirection: 'row', backgroundColor: 'white' }}>
                <TouchableOpacity
                  style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}
                  disabled={loading}
                  onPress={() => navigation.goBack()}>
                  <AntDesign name='close' size={48} color={theme.grey4} />
                  <Text style={{ fontFamily: 'varela-round', fontSize: 12, color: theme.grey4, textTransform: 'uppercase' }}>
                    {intl.get('close')}
                  </Text>
                </TouchableOpacity>
                <TouchableOpacity
                  style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}
                  disabled={loading}
                  onPress={() => this.setState({ loading: true }, () => this.doSubmit())}>
                  <AntDesign name='check' size={48} color={theme.grey4} />
                  <Text style={{ fontFamily: 'varela-round', fontSize: 12, color: theme.grey4 }}>
                    OK
                  </Text>
                </TouchableOpacity>
              </View>
            </View>
            <View style={{ margin: 5, width: 120, }}>
              <ScrollView style={{ flex: 1 }}
                showsVerticalScrollIndicator={false}
                alwaysBounceVertical={false}>
                <View style={{ paddingHorizontal: 5, height: 80, marginVertical: 5 }}>
                </View>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: orderType === 'A' ? theme.grey3 : theme.paid, borderRadius: 5, marginVertical: 5 }}
                  disabled={selectedMenu.serviceFlg === 'N'}
                  onPress={() => this.doToggleOrderType()}>
                  <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', textTransform: 'uppercase' }}>{orderType === 'A' ? intl.get('dinein') : intl.get('takeaway')}</Text>
                  <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', fontSize: 10 }}>ORDER TYPE</Text>
                </TouchableOpacity>
                {
                  settings.find(el => el.setId === 'MARKFIRE')?.setString === 'Y' ?
                    <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: lineBreak === 'N' ? theme.grey3 : theme.occupied, borderRadius: 5, marginVertical: 5 }}
                      disabled={selectedTableItem?.confirmFlg === 'Y'}
                      onPress={() => this.doToggleLineBreak()}>
                      <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white', textTransform: 'uppercase', fontSize: 10 }}>{lineBreak === 'N' ? intl.get('no') : intl.get('add')}</Text>
                      <Text style={{ fontFamily: 'varela-round', textAlign: 'center', color: 'white' }}>LINE BREAK</Text>
                    </TouchableOpacity>
                    :
                    <View style={{ paddingHorizontal: 5, height: 50, marginVertical: 5 }}>
                    </View>
                }
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: 'white', borderRadius: 5, marginVertical: 5 }}
                  disabled={loading}
                  onPress={() => this.setState({ loading: true }, () => this.doSubmit())}>
                  <AntDesign name="check" size={28} color={theme.grey4} />
                </TouchableOpacity>
                <View style={{ paddingHorizontal: 5, height: 50, marginVertical: 5 }}>
                </View>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: theme.grey3, borderRadius: 5, marginVertical: 5 }}
                  onPress={() => this.doChangeQty('PLUS')}>
                  <AntDesign name='plus' size={26} color='white' />
                </TouchableOpacity>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: theme.grey3, borderRadius: 5, marginVertical: 5 }}
                  onPress={() => this.doChangeQty('MINUS')}>
                  <AntDesign name="minus" size={32} color='white' />
                </TouchableOpacity>
                <View style={{ paddingHorizontal: 5, height: 50, marginVertical: 5 }}>
                </View>
                <TouchableOpacity style={{ alignItems: 'center', paddingHorizontal: 5, justifyContent: 'center', height: 50, backgroundColor: 'white', borderRadius: 5, marginVertical: 5 }}
                  disabled={loading}
                  onPress={() => navigation.goBack()}>
                  <AntDesign name="close" size={26} color={theme.grey4} />
                </TouchableOpacity>
              </ScrollView>
            </View>
          </View>
        </View>
      </View>
    );
  }

}

const styles = StyleSheet.create({
  modifierTextInput: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    paddingHorizontal: 20,
    height: 120,
    backgroundColor: theme.grey3,
  },
  menuQtyRow: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginRight: 10,
  },
  menuQtyRowSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginLeft: 10,
  },
  qtyTextInput: {
    fontFamily: 'varela-round',
    fontSize: 32,
    width: '100%',
    color: 'white',
    textAlign: 'center',
  },
  modContainer: {
    padding: 5,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    backgroundColor: theme.grey4,
  },
  modButton: {
    paddingHorizontal: 20,
    minHeight: 32,
    justifyContent: 'center',
    borderRadius: 16,
    backgroundColor: theme.grey2,
    margin: 5,
  },
  modText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 20,
  },
});
