import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
} from 'react-native';
import moment from 'moment';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';

export default class OverlayEnterDate extends Component {

  constructor(props) {
    super(props);

    this.state = {
      dateString: props.dateString ? moment(props.dateString).format('YYYY-MM-DD') : moment(new Date()).format('YYYY-MM-DD'),
      valid: true,
    }
    this.handleValidate = this.handleValidate.bind(this);
    this.handleQtyChange = this.handleQtyChange.bind(this);
  }

  handleValidate(dateString) {
    this.setState({
      dateString,
      valid: moment(dateString, 'YYYY-MM-DD', true).isValid(),
    });
  }

  handleQtyChange(type) {
    const { dateString } = this.state;
    if (type === 'PLUS') {
      let dateStringUpdate = moment(dateString).add(1, 'days').format('YYYY-MM-DD');

      this.setState({
        dateString: dateStringUpdate,
        valid: moment(dateStringUpdate, 'YYYY-MM-DD', true).isValid(),
      });
    }
    else if (type === 'MINUS') {
      let dateStringUpdate = moment(dateString).subtract(1, 'days').format('YYYY-MM-DD');
      this.setState({
        dateString: dateStringUpdate,
        valid: moment(dateStringUpdate, 'YYYY-MM-DD', true).isValid(),
      });
    }
  }

  render() {
    const { dateString, valid } = this.state;
    console.log('in overlayenterdate', this.props);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Enter Date</Text>
          </View>
          <View style={styles.inputRow}>
            <TouchableOpacity style={styles.inputRowFirst}
              onPress={() => this.handleQtyChange('MINUS')}>
              <AntDesign name='minus' size={32} color='rgba(0,0,0,0.85)' />
            </TouchableOpacity>
            <View style={styles.inputRowSecond}>
              <TextInput style={[styles.textInput, !valid && { color: theme.paid }]}
                keyboardType='numeric'
                value={dateString}
                onChangeText={(dateString) => this.handleValidate(dateString)}
              />
            </View>
            <TouchableOpacity style={styles.inputRowThird}
              onPress={() => this.handleQtyChange('PLUS')}>
              <AntDesign name='plus' size={32} color='rgba(0,0,0,0.85)' />
            </TouchableOpacity>
          </View>
          <TouchableOpacity onPress={() => this.setState({ dateString: moment().format('YYYY-MM-DD'), valid: true })}>
            <Text style={styles.overlayText}>YYYY-MM-DD</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption, { marginTop: 20 }, !valid && { opacity: 0.2 }]}
            disabled={!valid}
            onPress={() => this.props.handleUpdateFromEnterDate(dateString, this.props.dateType)}>
            <Text style={styles.overlayOptionText}>OK</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromEnterDate('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View >
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  inputRow: {
    height: 60,
    width: '100%',
    marginVertical: 5,
    flexDirection: 'row',
    justifyContent: 'center',
  },
  inputRowFirst: {
    width: 100,
    height: 60,
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRowSecond: {
    flex: 1,
    minWidth: 80,
    width: 150,
    borderRadius: 5,
    backgroundColor: theme.grey1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRowThird: {
    width: 100,
    height: 60,
    alignItems: 'center',
    justifyContent: 'center',
  },
  textInput: {
    width: '75%',
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    textAlign: 'center',
  },
});