import React, { Component } from 'react';
import {
  StyleSheet, Text, View, TouchableOpacity,
  FlatList, TextInput,
} from 'react-native';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';
import theme from '../assets/theme';
import { debug } from '../assets/utility';
import OverlayChoosePrinter from './OverlayChoosePrinter';
import OverlayProcedureMessage from './OverlayProcedureMessage';

export default class Dayclose extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      dayclosePayments: props.route.params.dayclosePayments,
      daycloseSubPayments: props.route.params.daycloseSubPayments,
      cashierPrinters: props.route.params.masters.cashierPrinters,
      settings: props.route.params.masters.settings,

      overlayChoosePrinterVisible: false,

      total: props.route.params.dayclosePayments.reduce((init, obj) => { return init + obj.systemAmt }, 0).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }),
      focused: null,
    };
    this.handleUpdateFromChoosePrinter = this.handleUpdateFromChoosePrinter.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);

    this.handleFinishDayclose = this.handleFinishDayclose.bind(this);
  }

  componentDidMount() {

  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromChoosePrinter(printer, purpose) {
    if (!printer) {
      this.setState({
        overlayChoosePrinterVisible: false,
      });
      return;
    }
    this.setState({
      overlayChoosePrinterVisible: false,
    });
    this.handleFinishDayclose(printer);
  }

  handleFinishDayclose(printer) {
    const { navigation } = this.props;
    const { serviceEntry, home, settings, dayclosePayments, daycloseSubPayments, cashierPrinters } = this.state;
    if (dayclosePayments.length === 0) {
      return;
    }
    if (cashierPrinters.length > 1 && !printer && !home.defaultPrinter) {
      this.setState({
        overlayChoosePrinterVisible: true,
      });
      return;
    }
    let url = serviceEntry + 'api/finish-dayclose';
    const body = {
      userId: home.userId,
      orgId: home.orgId,
      locId: home.locId,
      shopId: home.shopId,
      daycloseRecKey: dayclosePayments[0].masRecKey,
      iomodelId: (home.defaultPrinter && home.defaultPrinter.iomodelId) || (printer && printer.iomodelId),
      verify: dayclosePayments.map(el => el.recKey.toString() + ',' + (this.state[el.recKey] || '0')).join(','),
      subVerify: daycloseSubPayments.map(el => el.recKey.toString() + ',' + (this.state[el.recKey] || '0')).join(','),
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post finish-dayclose successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Finish Dayclose',
            home.userName,
            response.message,
            '',
            '',
            '',
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        navigation.goBack();
      })
  }

  doChangeText(item, text) {
    const { dayclosePayments, daycloseSubPayments } = this.state;
    const found = dayclosePayments.find(el => el.pmId === item.pmId);
    this.setState({
      [item.recKey]: text,
      [found.recKey]: daycloseSubPayments.filter(el => el.pmId === item.pmId && el.recKey !== item.recKey).reduce((init, el) => {
        return init + (this.state[el.recKey] || 0) * el.pmValue
      }, Number(text) * item.pmValue)
    })
  }

  doPressKey(item) {
    const { focused, dayclosePayments, daycloseSubPayments } = this.state;
    const found = daycloseSubPayments.find(el => el.recKey === focused);
    if (item === 'CLEAR') {
      this.doChangeText(found, '0')
    } else if (Number(this.state[focused]) === 0) {
      this.setState({ [focused]: item })
      this.doChangeText(found, item)
    } else {
      this.doChangeText(found, (this.state[focused] || '') + item)
    }
  }

  render() {
    const { navigation } = this.props;
    const { settings, dayclosePayments, daycloseSubPayments, cashierPrinters, overlayChoosePrinterVisible,
      overlayProcedureMessageVisible,
      total, focused } = this.state;
    const settingDAYCLOSEVERIFY = settings.find(el => el.setId === 'DAYCLOSEVERIFY')?.setString;
    console.log('in dayclose', focused);

    return (
      <View style={styles.container}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        {
          overlayChoosePrinterVisible &&
          <OverlayChoosePrinter
            printPurpose='DAY-END CLOSE'
            cashierPrinters={cashierPrinters}
            handleUpdateFromChoosePrinter={this.handleUpdateFromChoosePrinter} />
        }
        <View style={styles.containerFirst}>
          <View style={{ flex: 1 }}>
            <TouchableOpacity style={styles.button}
              onPress={() => navigation.goBack()}>
              <AntDesign
                name='left'
                color='white'
                size={36}
              />
              <Text style={styles.buttonText}>BACK</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.containerSecond}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Day-end Closing</Text>
            <Text style={styles.overlayTitleText}>{dayclosePayments.length} PAYMENT METHODS</Text>
          </View>
          {
            settingDAYCLOSEVERIFY === 'N' &&
            <View style={styles.summary}>
              <Text style={styles.summaryText}>TOTAL</Text>
              <Text style={styles.summaryTotalText}>{total}</Text>
            </View>
          }
          <View style={styles.searchBoxInfo}>
            <View style={styles.titleFirstContainer}>
              <Text style={styles.titleText}>Line No.</Text>
            </View>
            <View style={styles.titleSecondContainer}>
              <Text style={styles.titleText}>Payment ID</Text>
            </View>
            <View style={styles.titleThirdContainer}>
              <Text style={styles.titleText}>Payment Name</Text>
            </View>
            <View style={styles.titleFourthContainer}>
              <Text style={styles.titleText}>Amount</Text>
            </View>
          </View>
          <FlatList
            style={{ flex: 1 }}
            data={dayclosePayments}
            showsVerticalScrollIndicator={false}
            keyExtractor={item => item.recKey.toString()}
            renderItem={({ item }) => (
              <>
                <View style={[styles.tableItemContainer, (Number(this.state[item.recKey] || 0).toFixed(2) !== (item.systemAmt + item.floatAmt).toFixed(2) && daycloseSubPayments.some(el => el.pmId === item.pmId) && settingDAYCLOSEVERIFY === 'Y') && { backgroundColor: theme.paid }]}>
                  <View style={styles.tableItemFirstContainer}>
                    <Text style={styles.tableItemText}>{item.lineNo}</Text>
                  </View>
                  <View style={styles.tableItemSecondContainer}>
                    <Text style={styles.tableItemText}>
                      {item.pmId}
                    </Text>
                  </View>
                  <View style={styles.tableItemThirdContainer}>
                    <Text style={styles.tableItemText}>
                      {item.name}
                    </Text>
                  </View>
                  <View style={styles.tableItemFourthContainer}>
                    {
                      (settingDAYCLOSEVERIFY === 'Y' && daycloseSubPayments.some(obj => obj.pmId === item.pmId)) ?
                        <Text style={[styles.tableItemText, { paddingRight: 10 }]}>
                          {(this.state[item.recKey] || 0).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                        </Text>
                        :
                        <Text style={[styles.tableItemText, { paddingRight: 10 }]}>
                          {item.systemAmt.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                        </Text>
                    }
                  </View>
                </View>
                {
                  daycloseSubPayments.filter(el => el.pmId === item.pmId).map(el =>
                    <View key={el.recKey.toString()}
                      style={[styles.tableItemContainer]}>
                      <View style={styles.tableItemFirstContainer}>
                        <Text style={styles.tableItemText}></Text>
                      </View>
                      <View style={styles.tableItemSecondContainer}>
                        <Text style={styles.tableItemText}>
                        </Text>
                      </View>
                      <View style={{ flex: 0.5, justifyContent: 'center' }}>
                        <Text style={styles.tableItemText}>
                          {el.subPmId}
                        </Text>
                      </View>
                      <View style={{ flex: 0.5, }}>
                        <View style={[styles.inputRowRight, { marginRight: 10 }, focused === el.recKey && { backgroundColor: theme.reserved }]}>
                          <TextInput style={[styles.textInput, focused === el.recKey && { color: 'black' }]}
                            keyboardType='numeric'
                            selectTextOnFocus={true}
                            value={this.state[el.recKey] || '0'}
                            onChangeText={text => this.doChangeText(el, text)}
                            onFocus={() => this.setState({ focused: el.recKey })}
                          />
                        </View>
                      </View>
                      <View style={styles.tableItemFourthContainer}>
                        <Text style={[styles.tableItemText, { paddingRight: 10 }]}>
                          {(el.pmValue * Number(this.state[el.recKey] || 0)).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                        </Text>
                      </View>
                    </View>
                  )
                }
              </>
            )}
          />
        </View>
        <View style={[styles.containerThird]}>
          <View style={{ flex: 1, }}>
            <TouchableOpacity style={styles.button}
              onPress={() => this.handleFinishDayclose()}>
              <AntDesign
                name='isv'
                color='white'
                size={36}
              />
              <Text style={styles.buttonText}>DAY CLOSE</Text>
            </TouchableOpacity>
          </View>
          <View>
            {
              focused &&
              <FlatList
                data={['1', '2', '3', '4', '5', '6', '7', '8', '9', 'CLEAR', '0']}
                scrollEnabled={false}
                showsVerticalScrollIndicator={false}
                numColumns={3}
                keyExtractor={(item, index) => index.toString()}
                renderItem={({ item }) => (
                  <View style={{ flex: 1 / 3, aspectRatio: 1 }}>
                    <TouchableOpacity style={{ margin: 2.5, flex: 1, alignItems: 'center', justifyContent: 'center', backgroundColor: 'black', borderRadius: 5, }}
                      onPress={() => this.doPressKey(item)}>
                      {
                        item === 'CLEAR' ?
                          <AntDesign name='close' color='white' size={18} />
                          :
                          <Text style={{ color: 'white', fontFamily: 'varela-round', fontSize: 18, }}>{item}</Text>
                      }
                    </TouchableOpacity>
                  </View>
                )}
              />
            }
          </View>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'row',
    backgroundColor: theme.grey1,
  },
  containerFirst: {
    flex: 1,
    flexDirection: 'row',
    padding: 10,
  },
  containerSecond: {
    flex: 6,
    marginVertical: 10,
    borderRadius: 5,
    backgroundColor: theme.grey3,
  },
  containerThird: {
    flex: 1,
    padding: 10,
  },
  searchBoxInfo: {
    height: 20,
    margin: 5,
    flexDirection: 'row',
  },
  titleFirstContainer: {
    width: 100,
    alignItems: 'center',
    justifyContent: 'center',
  },
  titleSecondContainer: {
    flex: 1,
    justifyContent: 'center',
  },
  titleThirdContainer: {
    flex: 1,
    justifyContent: 'center',
  },
  titleFourthContainer: {
    width: 150,
    alignItems: 'flex-end',
    justifyContent: 'center',
    paddingRight: 20,
  },
  titleText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
  tableItemText: {
    fontFamily: 'varela-round',
    color: 'white',
  },
  inputRowRight: {
    flex: 1,
    backgroundColor: theme.grey1,
    borderRadius: 5,
    // justifyContent: 'center',
    justifyContent: 'center',
    alignItems: 'flex-end',
  },
  textInput: {
    paddingHorizontal: 10,
    flex: 1,
    width: '100%',
    textAlign: 'right',
    color: 'white',
    fontFamily: 'varela-round',
  },
  tableItemContainer: {
    flex: 1,
    flexDirection: 'row',
    minHeight: 50,
    paddingVertical: 5,
    marginHorizontal: 5,
    marginBottom: 5,
    backgroundColor: theme.grey4,
  },
  tableItemFirstContainer: {
    width: 100,
    alignItems: 'center',
    justifyContent: 'center',
  },
  tableItemSecondContainer: {
    flex: 1,
    justifyContent: 'center',
  },
  tableItemThirdContainer: {
    flex: 1,
    justifyContent: 'center',
  },
  tableItemFourthContainer: {
    width: 150,
    alignItems: 'flex-end',
    justifyContent: 'center',
    paddingRight: 10,
  },
  button: {
    backgroundColor: 'transparent',
    borderRadius: 10,
    aspectRatio: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  summary: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  summaryTotalText: {
    fontFamily: 'varela-round',
    fontSize: 32,
    color: theme.reserved,
    textAlign: 'center',
  },
  summaryText: {
    fontFamily: 'varela-round',
    fontSize: 12,
    color: theme.paid,
    textAlign: 'center',
  },
});