import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, FlatList,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import OverlayRequestAccess from './OverlayRequestAccess';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import { debug } from '../assets/utility';
import intl from 'react-intl-universal';

export default class OverlayLineDiscount extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.home,
      serviceEntry: props.serviceEntry,
      settings: props.settings,
      discounts: props.discounts,
      privileges: props.privileges,

      availableDiscounts: props.discounts,

      overlaySearchText: '',
      selectedDiscount: '',
      remarks: '',
      overlayRequestAccessVisible: false,
      overlayProcedureMessageVisible: false,
    };
    this.handleOverlaySearchText = this.handleOverlaySearchText.bind(this);
    this.handleUpdateFromRequestAccess = this.handleUpdateFromRequestAccess.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleOverlaySearchText(text) {
    const { discounts, availableDiscounts } = this.state;
    let availableDiscountsUpdate = [];
    this.setState({ overlaySearchText: text });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);
    console.log('available', this.state.availableDiscounts);

    availableDiscountsUpdate = discounts;
    if (!text) {
      this.setState({
        availableDiscounts: availableDiscountsUpdate
      });
      return;
    }
    availableDiscountsUpdate = availableDiscountsUpdate.filter(item => {
      return item.discId.toUpperCase().includes(caseText) || item.discName.toUpperCase().includes(caseText);
    });
    this.setState({
      availableDiscounts: availableDiscountsUpdate
    });
  }

  handleUpdateFromRequestAccess(text) {
    const { home, serviceEntry, settings, selectedDiscount } = this.state;
    if (!text) {
      this.setState({
        overlayRequestAccessVisible: false,
      });
      return;
    }
    let url = serviceEntry + 'api/super-user';
    const body = {
      locId: home.locId,
      accessCode: text,
      priId: 'LINEDISC',
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post super-user successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Request Access LINEDISC',
            home.userName,
            response.message,
            '',
            '',
            response.userId,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          overlayRequestAccessVisible: false,
        });
        this.handleSubmit(selectedDiscount, response.userId);
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleSubmit(item, superUserId) {
    const { home, privileges, remarks } = this.state;
    let itemWithRemarks = item;
    itemWithRemarks.remarks = remarks;

    if (!(home.adminFlg === 'Y' || privileges.some(el => el.priId === 'LINEDISC')) && !superUserId) {
      this.setState({
        overlayRequestAccessVisible: true,
        selectedDiscount: item,
      });
      return;
    }

    this.props.handleUpdateFromLineDiscount(itemWithRemarks, superUserId);
  }

  render() {
    const { home, overlaySearchText, availableDiscounts, remarks,
      overlayRequestAccessVisible, overlayProcedureMessageVisible } = this.state;
    console.log('in overlaylinediscount, state', this.state);

    return (
      <View style={styles.overlay}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        {
          overlayRequestAccessVisible &&
          <OverlayRequestAccess
            home={home}
            requestedPrivilege='LINEDISC'
            handleUpdateFromRequestAccess={this.handleUpdateFromRequestAccess} />
        }
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Line Discount</Text>
          </View>
          <View style={[styles.searchBoxWrapper, { marginBottom: 10 }]}>
            <View style={styles.overlaySearchBox}>
              <TextInput style={styles.overlaySearchBoxInput}
                placeholder='ENTER REMARKS'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(remarks) => this.setState({ remarks })}
                value={remarks}
              />
            </View>
          </View>
          <View style={styles.searchBoxWrapper}>
            <TouchableOpacity style={styles.overlayBackButton}
              onPress={() => this.props.handleUpdateFromLineDiscount('')}>
              <AntDesign name='close' size={24} color='white' />
            </TouchableOpacity>
            <View style={styles.overlaySearchBox}>
              <TextInput style={styles.overlaySearchBoxInput}
                placeholder='ENTER SEARCH TEXT'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(searchText) => this.handleOverlaySearchText(searchText)}
                value={overlaySearchText}
              >
              </TextInput>
              <TouchableOpacity style={styles.overlaySearchBoxButton}
                onPress={() => {
                  this.setState({ overlaySearchText: '' }, () => this.handleOverlaySearchText(''))
                  Keyboard.dismiss()
                }}
              >
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
          </View>
          <View style={styles.searchBoxInfo}>
            <Text style={styles.searchBoxInfoText}>{availableDiscounts.length} ITEMS</Text>
          </View>
          <FlatList
            style={[{ flex: 1 }]}
            data={availableDiscounts}
            keyExtractor={item => item.recKey.toString()}
            renderItem={({ item }) => (
              <TouchableOpacity
                style={[styles.overlayGrid, item.dummy ? styles.hidden : null]}
                onPress={() => this.handleSubmit(item)}>
                <View style={styles.overlayGridFirst}>
                  <View style={styles.overlayGridFirstLeft}>
                    <Text style={styles.overlayGridFirstLeftText}>{item.discId}</Text>
                  </View>
                  <View style={styles.overlayGridFirstRight}>
                    <Text style={styles.overlayGridFirstRightText}></Text>
                  </View>
                </View>
                <View style={styles.overlayGridSecond}>
                  <Text style={styles.overlayGridSecondText}>
                    {item.discName}
                  </Text>
                </View>
                <View style={styles.overlayGridThird}>
                  <View style={styles.overlayGridThirdLeft}>
                  </View>
                  <View style={styles.overlayGridThirdRight}>
                    <Text style={styles.overlayGridThirdRightText}></Text>
                  </View>
                </View>
              </TouchableOpacity>
            )}
          />
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    height: '80%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
  },
  searchBoxWrapper: {
    height: 50,
    flexDirection: 'row',
    alignItems: 'center',
  },
  overlaySearchBox: {
    backgroundColor: theme.grey1,
    height: 50,
    borderRadius: 100,
    marginHorizontal: 5,
    flexDirection: 'row',
    flex: 1,
  },
  overlaySearchBoxInput: {
    flex: 1,
    fontFamily: 'varela-round',
    fontSize: 16,
    color: 'white',
    paddingHorizontal: 20,
    height: 50,
  },
  overlaySearchBoxButton: {
    height: 50,
    width: 50,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 50,
  },
  overlayBackButton: {
    // backgroundColor: 'white',
    width: 50,
    height: 50,
    borderRadius: 50,
    marginHorizontal: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayGrid: {
    flex: 1,
    margin: 5,
    marginTop: 0,
    marginBottom: 10,
    backgroundColor: theme.grey4,
    flexDirection: 'column',
    padding: 10,
  },
  overlayGridFirst: {
    flexDirection: 'row'
  },
  overlayGridSecond: {
    marginVertical: 5,
  },
  overlayGridThird: {
    flexDirection: 'row'
  },
  overlayGridFirstLeft: {
    flex: 1,
  },
  overlayGridFirstRight: {
    flex: 1,
    alignItems: 'flex-end',
  },
  overlayGridThirdLeft: {
    flex: 1,
  },
  overlayGridThirdRight: {
    flex: 1,
    alignItems: 'flex-end',
  },
  overlayGridFirstLeftText: {
    color: 'white',
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  overlayGridFirstRightText: {
    color: theme.vacant,
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  overlayGridSecondText: {
    color: 'white',
    fontSize: 16,
    fontFamily: 'varela-round',
    opacity: 0.8,
  },
  overlayGridThirdRightText: {
    color: 'white',
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  searchBoxInfo: {
    height: 20,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  searchBoxInfoText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
});