import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions, FlatList,
} from 'react-native';
import theme from '../assets/theme';
import { debug } from '../assets/utility';
import { AntDesign, MaterialCommunityIcons } from '@expo/vector-icons';
import intl from 'react-intl-universal';
import OverlayProcedureMessage from './OverlayProcedureMessage';

export default class Table extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      selectedTable: props.route.params.selectedTable,
      mode: props.route.params.mode,
      masters: props.route.params.masters,
      settings: props.route.params.masters.settings,
      vipSources: props.route.params.masters.vipSources,
      availableVipSources: props.route.params.masters.vipSources,

      overlayPax: props.route.params.mode === 'NEW' ? (props.route.params.masters.tablemas?.find(el => el.tableId === props.route.params.selectedTable.tableId)?.capacity || 2) : (props.route.params.selectedTable.pax || 0),
      overlayVip: props.route.params.mode === 'NEW' ? null
        : {
          vipId: props.route.params.selectedTable.vipId || '',
          vipName: props.route.params.selectedTable.vipName || '',
          vipDisc: props.route.params.selectedTable.vipDisc || '',
          vipPhone: props.route.params.selectedTable.vipPhone || '',
          cumPts: props.route.params.selectedTable.cumPts || '',
          vipPtsMoney: props.route.params.selectedTable.vipPtsMoney || '',
          classId: props.route.params.selectedTable.classId || '',
        },
      overlayValidateVipId: '',
      overlayValidateCardNumber: '',
      overlayValidateNric: '',
      overlayValidateVipName: '',
      overlayValidateVipPhone: '',
      overlayValidateVipPhoneCountryCode: '', // 65
      overlayValidateEmailAddress: '',

      vipResults: '',
      selectedVipResult: '',

      overlaySource: props.route.params.mode === 'NEW' ? ''
        : {
          sourceId: props.route.params.selectedTable.sourceId || '',
        },
      overlaySourceSearch: '',

      overlayDeliveryFlg: props.route.params.mode === 'NEW' ? 'N' : (props.route.params.selectedTable.homeDelivery || ''),
      overlayPostalCode: props.route.params.mode === 'NEW' ? '' : (props.route.params.selectedTable.dpostalcode || ''),
      overlayAddr1: props.route.params.mode === 'NEW' ? '' : (props.route.params.selectedTable.daddress1 || ''),
      overlayAddr2: props.route.params.mode === 'NEW' ? '' : (props.route.params.selectedTable.daddress2 || ''),
      overlayAddr3: props.route.params.mode === 'NEW' ? '' : (props.route.params.selectedTable.daddress3 || ''),
      overlayAddr4: props.route.params.mode === 'NEW' ? '' : (props.route.params.selectedTable.daddress4 || ''),

      activeTab: props.route.params.tab || 'PAX',
      warning: '',
    };
    this.handleQtyChange = this.handleQtyChange.bind(this);
    this.handlePressTab = this.handlePressTab.bind(this);
    this.handleSearchVip = this.handleSearchVip.bind(this);
    this.handleEnterVip = this.handleEnterVip.bind(this);
    this.handleClearVip = this.handleClearVip.bind(this);
    this.handlePressNext = this.handlePressNext.bind(this);
    this.handlePressPrev = this.handlePressPrev.bind(this);
    this.handleOverlaySearchText = this.handleOverlaySearchText.bind(this);
    this.handleAddressChange = this.handleAddressChange.bind(this);
    this.handleCheckDeliveryFlg = this.handleCheckDeliveryFlg.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleSubmit() {
    const { navigation, } = this.props;
    const { mode, home, serviceEntry, selectedTable, settings, overlayPax, overlayVip, overlaySource, overlayValidateVipName, overlayDeliveryFlg,
      overlayPostalCode, overlayAddr1, overlayAddr2, overlayAddr3, overlayAddr4 } = this.state;
    if (!overlayPax && overlayPax !== 0) {
      return;
    }
    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + (mode === 'NEW' ? '/change-table-status' : '/update-table-info');
    const body = {
      userId: home.userId,
      locId: home.locId,
      tableStatus: 'C',
      pax: overlayPax,
      // vipId: overlayVip?.vipId,
      vipName: overlayVip?.vipId ? overlayVip?.vipName : overlayValidateVipName,
      phone: overlayVip?.vipPhone,
      reservationTime: '',
      sourceId: overlaySource.sourceId,
      deliveryFlg: overlayDeliveryFlg,
      postalCode: overlayPostalCode,
      addr1: overlayAddr1,
      addr2: overlayAddr2,
      addr3: overlayAddr3,
      addr4: overlayAddr4,
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post table-edit-open successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            mode === 'NEW' ? 'Open Table' : 'Edit Table',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            overlayVip?.vipId ? overlayVip?.vipName : overlayValidateVipName,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        if (mode === 'NEW') {
          this.props.route.params.handleUpdateFromTable();
        } else {
          navigation.goBack();
        }
      })
  }

  handleAddressChange(type, text) {
    if (type === 'POSTALCODE') {
      this.setState({
        overlayPostalCode: text
      }, () => this.handleCheckDeliveryFlg())
    } else if (type === 'ADDR1') {
      this.setState({
        overlayAddr1: text
      }, () => this.handleCheckDeliveryFlg())
    } else if (type === 'ADDR2') {
      this.setState({
        overlayAddr2: text
      }, () => this.handleCheckDeliveryFlg())
    } else if (type === 'ADDR3') {
      this.setState({
        overlayAddr3: text
      }, () => this.handleCheckDeliveryFlg())
    } else if (type === 'ADDR4') {
      this.setState({
        overlayAddr4: text
      }, () => this.handleCheckDeliveryFlg())
    }
  }

  handleCheckDeliveryFlg() {
    const { overlayPostalCode, overlayAddr1, overlayAddr2, overlayAddr3, overlayAddr4 } = this.state;
    if (overlayPostalCode || overlayAddr1 || overlayAddr2 || overlayAddr3 || overlayAddr4) {
      this.setState({
        overlayDeliveryFlg: 'Y'
      })
    } else {
      this.setState({
        overlayDeliveryFlg: 'N'
      })
    }
  }

  handleQtyChange(type) {
    if (type === 'PLUS') {
      this.setState((prevState, props) => ({
        overlayPax: prevState.overlayPax + 1
      }));
    }
    else if (type === 'MINUS') {
      if (this.state.overlayPax === 0) return;
      this.setState((prevState, props) => ({
        overlayPax: prevState.overlayPax - 1
      }));
    }
  }

  handlePaxChange(text) {
    if (isNaN(text)) {
      this.setState({ overlayPax: 0 })
    } else {
      this.setState({ overlayPax: Number(text) })
    }
  }

  handlePressTab(type) {
    const overlayTabWidth = Dimensions.get('window').width / 5 * 3;

    if (type === 'VIP') {
      this.setState({
        activeTab: 'VIP',
      });
    } else if (type === 'PAX') {
      this.setState({
        activeTab: 'PAX',
      });
    } else if (type === 'SOURCE') {
      this.setState({
        activeTab: 'SOURCE',
      });
    } else if (type === 'DELIVERY') {
      this.setState({
        activeTab: 'DELIVERY',
      });
    }
  }

  handlePressNext() {
    const { activeTab } = this.state;
    const overlayTabWidth = Dimensions.get('window').width / 5 * 3;
    if (activeTab === 'PAX') {
      this.setState({
        activeTab: 'VIP',
      });
    } else if (activeTab === 'VIP') {
      this.setState({
        activeTab: 'SOURCE',
      });
    } else if (activeTab === 'SOURCE') {
      this.setState({
        activeTab: 'DELIVERY',
      });
    } else if (activeTab === 'DELIVERY') {
      return;
    }
  }

  handlePressPrev() {
    const { activeTab } = this.state;
    const overlayTabWidth = Dimensions.get('window').width / 5 * 3;
    if (activeTab === 'PAX') {
      return;
    } else if (activeTab === 'VIP') {
      this.setState({
        activeTab: 'PAX',
      });
    } else if (activeTab === 'SOURCE') {
      this.setState({
        activeTab: 'VIP',
      });
    } else if (activeTab === 'DELIVERY') {
      this.setState({
        activeTab: 'SOURCE',
      });
    }
  }

  handleSearchVip() {
    const { serviceEntry, home, masters, settings, selectedTable, overlayVip, overlayValidateVipId, overlayValidateCardNumber, overlayValidateVipName,
      overlayValidateNric, overlayValidateEmailAddress, overlayValidateVipPhone, overlayValidateVipPhoneCountryCode } = this.state;
    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/search-vip';
    let body = {
      opentableRecKey: selectedTable.recKey,
    };
    if (overlayValidateVipId) {
      body.vipId = overlayValidateVipId;
    }
    if (overlayValidateCardNumber) {
      body.cardNumber = overlayValidateCardNumber;
    }
    if (overlayValidateVipName) {
      body.vipName = overlayValidateVipName;
    }
    if (overlayValidateNric) {
      body.nric = overlayValidateNric;
    }
    if (overlayValidateEmailAddress) {
      body.emailAddress = overlayValidateEmailAddress;
    }
    if (overlayValidateVipPhone) {
      body.vipPhone = overlayValidateVipPhone;
      body.vipPhoneCountryCode = overlayValidateVipPhoneCountryCode
    }
    console.log('before searching vip', body);
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Search Vip',
            home.userName,
            response.message,
            selectedTable.tableId,
          );
        if (response.message) {
          this.setState({
            warning: 'Invalid VIP search',
          })
          return;
        }
        // to state
        this.setState({
          warning: response.length + ' VIP result(s) found',
          vipResults: response,
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleEnterVip(item) {
    this.setState({
      selectedVipResult: item,
    });
    const { serviceEntry, home, masters, settings, selectedTable } = this.state;
    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/enter-vip';
    let body = {
      opentableRecKey: selectedTable.recKey,
      customerNumber: item.customerNumber,
      name: item.name,
      mobileNumber: item.mobileNumber,
      emailAddress: item.emailaddress,
      customerSource: item.customerSource,
    };
    console.log('before entering vip', item);
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Input Vip',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            item.customerNumber + ',' + item.name,
          );
        if (response.message) {
          this.setState({
            warning: 'VIP input unsuccessful',
          })
          return;
        }
        // to state
        this.setState({
          overlayVip: {
            vipId: response.opentable.vipId || '',
            vipName: response.opentable.vipName || '',
            vipPhone: response.opentable.vipPhone || '',
            vipPtsMoney: response.opentable.vipPtsMoney || '',
            cumPts: response.opentable.cumPts || '',
            vipDisc: response.opentable.vipDisc || '',
            classId: response.opentable.classId || '',
          },
          warning: 'VIP input successful',
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleClearVip() {
    const { serviceEntry, home, masters, settings, selectedTable } = this.state;
    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/update-table-vip-info';
    let body = {
      vipId: '',
      vipName: '',
      classId: '',
      vipDisc: '',
      cumPts: '',
      pts: '',
      money: '',
      cardNo: '',
      birthday: '',
      qrcode: '',
      phone: '',
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Clear Vip',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            '',
          );
        if (response.message) {
          this.setState({
            warning: response.message,
          })
          return;
        }
        // to state
        this.setState({
          overlayVip: {
            vipId: response.opentable.vipId || '',
            vipName: response.opentable.vipName || '',
            vipPhone: response.opentable.vipPhone || '',
            vipPtsMoney: response.opentable.vipPtsMoney || '',
            cumPts: response.opentable.cumPts || '',
            vipDisc: response.opentable.vipDisc || '',
            classId: response.opentable.classId || '',
          },
          warning: 'VIP cleared',
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleOverlaySearchText(text) {
    const { vipSources, availableVipSources } = this.state;
    let availableVipSourcesUpdate = [];
    this.setState({ overlaySourceSearch: text });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);
    console.log('available', availableVipSources);

    availableVipSourcesUpdate = vipSources;
    if (!text) {
      this.setState({
        availableVipSources: availableVipSourcesUpdate
      });
      return;
    }
    availableVipSourcesUpdate = availableVipSourcesUpdate.filter(item => {
      return item.sourceId.toUpperCase().includes(caseText) || item.name.toUpperCase().includes(caseText);
    });
    this.setState({
      availableVipSources: availableVipSourcesUpdate
    });
  }

  render() {
    const { navigation } = this.props;
    const { mode, selectedTable, overlayProcedureMessageVisible,
      overlayPax, overlayVip, overlayValidateVipId, overlayValidateCardNumber, overlayValidateEmailAddress,
      overlayValidateVipPhone, overlayValidateVipPhoneCountryCode, overlayValidateVipName, overlayValidateNric,
      overlaySource, overlaySourceSearch, vipResults, selectedVipResult,
      overlayDeliveryFlg, overlayPostalCode, overlayAddr1, overlayAddr2, overlayAddr3, overlayAddr4,
      warning, activeTab, availableVipSources } = this.state;
    console.log('in overlayopentable');

    const overlayTabWidth = Dimensions.get('window').width / 5 * 3;
    return (
      <View style={styles.overlay}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('table')} {selectedTable.tableId}</Text>
            <Text style={styles.overlaySubtitleText}>{mode === 'NEW' ? intl.get('open') : intl.get('edit')}</Text>
            <Text style={styles.overlayText}>{warning ? warning : 'Enter Pax, VIP & Source'}</Text>
          </View>
          <View style={styles.tabsContainer}>
            <TouchableOpacity style={[styles.tabContainer, activeTab === 'PAX' && { backgroundColor: 'white' }]}
              onPress={() => this.handlePressTab('PAX')}>
              {
                overlayPax !== null ?
                  <Text style={[styles.tabFocusText, activeTab === 'PAX' && { color: theme.grey1 }]}>
                    {overlayPax}
                  </Text>
                  :
                  <Text style={[styles.tabFocusText, activeTab === 'PAX' && { color: theme.grey1 }]}>
                    TBD
                  </Text>
              }
              <Text style={[styles.tabText, activeTab === 'PAX' && { color: theme.grey1 }]}>
                PAX
              </Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.tabContainer, activeTab === 'VIP' && { backgroundColor: 'white' }]}
              onPress={() => this.handlePressTab('VIP')}>
              {
                overlayVip?.vipId ?
                  <Text style={[styles.tabFocusText, activeTab === 'VIP' && { color: theme.grey1 }]}>
                    {overlayVip?.vipId}
                  </Text>
                  :
                  <Text style={[styles.tabFocusText, activeTab === 'VIP' && { color: theme.grey1 }]}>
                    TBD
                  </Text>
              }
              <Text style={[styles.tabText, activeTab === 'VIP' && { color: theme.grey1 }]}>
                VIP
              </Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.tabContainer, activeTab === 'SOURCE' && { backgroundColor: 'white' }]}
              onPress={() => this.handlePressTab('SOURCE')}>
              {
                overlaySource.sourceId ?
                  <Text style={[styles.tabFocusText, activeTab === 'SOURCE' && { color: theme.grey1 }]}>
                    {overlaySource.sourceId}
                  </Text>
                  :
                  <Text style={[styles.tabFocusText, activeTab === 'SOURCE' && { color: theme.grey1 }]}>
                    TBD
                  </Text>
              }
              <Text style={[styles.tabText, activeTab === 'SOURCE' && { color: theme.grey1 }]}>
                SOURCE
              </Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.tabContainer, activeTab === 'DELIVERY' && { backgroundColor: 'white' }]}
              onPress={() => this.handlePressTab('DELIVERY')}>
              {
                overlayDeliveryFlg === 'Y' ?
                  <Text style={[styles.tabFocusText, activeTab === 'DELIVERY' && { color: theme.grey1 }]}>
                    YES
                  </Text>
                  :
                  <Text style={[styles.tabFocusText, activeTab === 'DELIVERY' && { color: theme.grey1 }]}>
                    NO
                  </Text>
              }
              <Text style={[styles.tabText, activeTab === 'DELIVERY' && { color: theme.grey1 }]}>
                DELIVERY
              </Text>
            </TouchableOpacity>
          </View>
          {
            activeTab === 'PAX' ?
              <View style={[styles.overlayTab, { width: overlayTabWidth }]}>
                <View style={styles.adjustContainer}>
                  <TouchableOpacity style={styles.adjustFirst}
                    onPress={() => this.handleQtyChange('MINUS')}>
                    <AntDesign name='minus' size={32} color='rgba(0,0,0,0.85)' />
                  </TouchableOpacity>
                  <View style={styles.adjustSecond}>
                    <TextInput style={styles.overlayPax}
                      selectTextOnFocus
                      keyboardType='numeric'
                      value={overlayPax?.toString()}
                      onChangeText={(searchText) => this.handlePaxChange(searchText)} />
                  </View>
                  <TouchableOpacity style={styles.adjustThird}
                    onPress={() => this.handleQtyChange('PLUS')}>
                    <AntDesign name='plus' size={32} color='rgba(0,0,0,0.85)' />
                  </TouchableOpacity>
                </View>
                <View style={styles.buttonsContainer}>
                  <TouchableOpacity style={[styles.button, overlayPax === 1 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 1 })}>
                    <Text style={styles.buttonText}>1</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 2 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 2 })}>
                    <Text style={styles.buttonText}>2</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 3 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 3 })}>
                    <Text style={styles.buttonText}>3</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 4 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 4 })}>
                    <Text style={styles.buttonText}>4</Text>
                  </TouchableOpacity>
                </View>
                <View style={styles.buttonsContainer}>
                  <TouchableOpacity style={[styles.button, overlayPax === 5 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 5 })}>
                    <Text style={styles.buttonText}>5</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 6 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 6 })}>
                    <Text style={styles.buttonText}>6</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 7 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 7 })}>
                    <Text style={styles.buttonText}>7</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 8 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 8 })}>
                    <Text style={styles.buttonText}>8</Text>
                  </TouchableOpacity>
                </View>
                <View style={styles.buttonsContainer}>
                  <TouchableOpacity style={[styles.button, overlayPax === 9 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 9 })}>
                    <Text style={styles.buttonText}>9</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 10 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 10 })}>
                    <Text style={styles.buttonText}>10</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 11 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 11 })}>
                    <Text style={styles.buttonText}>11</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={[styles.button, overlayPax === 12 && { backgroundColor: theme.occupied }]}
                    onPress={() => this.setState({ overlayPax: 12 })}>
                    <Text style={styles.buttonText}>12</Text>
                  </TouchableOpacity>
                </View>
              </View>
              :
              activeTab === 'VIP' ?
                <View style={[styles.overlayTab, { width: overlayTabWidth }]}>
                  {
                    (vipResults && vipResults.length !== 0) ?
                      <View style={styles.selectWrapper}>
                        <View style={styles.searchBoxWrapper}>
                          <View style={[styles.overlaySearchBox, { marginHorizontal: 0, justifyContent: 'space-between', }]}>
                            <TouchableOpacity style={styles.overlaySearchBoxButton}
                              onPress={() => this.setState({ vipResults: '' })}
                            >
                              <AntDesign name='close' size={24} color='white' />
                            </TouchableOpacity>
                          </View>
                        </View>
                        <View style={styles.searchBoxInfo}>
                          <Text style={styles.searchBoxInfoText}>{vipResults.length} ITEMS</Text>
                        </View>
                        <FlatList
                          style={[{ flex: 1 }]}
                          showsVerticalScrollIndicator={false}
                          data={vipResults}
                          keyExtractor={(item, index) => index.toString()}
                          renderItem={({ item }) => (
                            <TouchableOpacity
                              style={[styles.overlayGrid, item.customerNumber === selectedVipResult.customerNumber && { backgroundColor: theme.occupied }]}
                              onPress={() => this.handleEnterVip(item)}>
                              <View style={styles.overlayGridFirst}>
                                <View style={styles.overlayGridFirstLeft}>
                                  <Text style={styles.overlayGridFirstLeftText}>{item.customerNumber}</Text>
                                </View>
                                <View style={styles.overlayGridFirstRight}>
                                  <Text style={[styles.overlayGridFirstRightText, item.customerNumber === selectedVipResult.customerNumber && { color: 'white' }]}>{item.hasActiveMembership} {item.customerSource}</Text>
                                </View>
                              </View>
                              <View style={styles.overlayGridSecond}>
                                <Text style={styles.overlayGridSecondText}>
                                  {item.name}
                                </Text>
                              </View>
                              <View style={styles.overlayGridThird}>
                                <View style={styles.overlayGridThirdLeft}>
                                  <Text style={styles.overlayGridThirdLeftText}>{item.emailaddress}</Text>
                                </View>
                                <View style={styles.overlayGridThirdRight}>
                                  <Text style={styles.overlayGridThirdRightText}>{item.mobileNumber ? item.mobileNumber : ' '}</Text>
                                </View>
                              </View>
                            </TouchableOpacity>
                          )}
                        />
                      </View>
                      :
                      <View style={styles.vipInputsContainer}>
                        <View style={styles.vipInputsRow}>
                          <TextInput style={styles.vipInput}
                            placeholder='VIP ID'
                            placeholderTextColor='rgba(255,255,255,0.2)'
                            textContentType='none'
                            autoComplete='off'
                            autoCorrect={false}
                            onChangeText={(overlayValidateVipId) => this.setState({ overlayValidateVipId })}
                            value={overlayValidateVipId}
                            onSubmitEditing={() => this.handleSearchVip()}
                          />
                          <TextInput style={styles.vipInput}
                            placeholder='CARD NO.'
                            placeholderTextColor='rgba(255,255,255,0.2)'
                            textContentType='none'
                            autoComplete='off'
                            autoCorrect={false}
                            onChangeText={(overlayValidateCardNumber) => this.setState({ overlayValidateCardNumber })}
                            value={overlayValidateCardNumber}
                            onSubmitEditing={() => this.handleSearchVip()}
                          />
                        </View>
                        <View style={styles.vipInputsRow}>
                          <TextInput style={styles.vipInput}
                            placeholder='VIP NAME'
                            placeholderTextColor='rgba(255,255,255,0.2)'
                            textContentType='none'
                            autoComplete='off'
                            autoCorrect={false}
                            onChangeText={(overlayValidateVipName) => this.setState({ overlayValidateVipName })}
                            value={overlayValidateVipName}
                            onSubmitEditing={() => this.handleSearchVip()}
                          />
                          <TextInput style={[styles.vipInput, { width: 50 }]}
                            placeholderTextColor='rgba(255,255,255,0.2)'
                            textContentType='none'
                            autoComplete='off'
                            autoCorrect={false}
                            onChangeText={(overlayValidateVipPhoneCountryCode) => this.setState({ overlayValidateVipPhoneCountryCode })}
                            value={overlayValidateVipPhoneCountryCode}
                          />
                          <TextInput style={[styles.vipInput, { width: 130 }]}
                            placeholder='VIP PHONE'
                            placeholderTextColor='rgba(255,255,255,0.2)'
                            textContentType='none'
                            autoComplete='off'
                            autoCorrect={false}
                            onChangeText={(overlayValidateVipPhone) => this.setState({ overlayValidateVipPhone })}
                            value={overlayValidateVipPhone}
                            onSubmitEditing={() => this.handleSearchVip()}
                          />
                        </View>
                        <View style={styles.vipInputsRow}>
                          <TextInput style={styles.vipInput}
                            placeholder='VIP EMAIL'
                            placeholderTextColor='rgba(255,255,255,0.2)'
                            textContentType='none'
                            autoComplete='off'
                            autoCorrect={false}
                            onChangeText={(overlayValidateEmailAddress) => this.setState({ overlayValidateEmailAddress })}
                            value={overlayValidateEmailAddress}
                            onSubmitEditing={() => this.handleSearchVip()}
                          />
                          <View style={{ flex: 1, alignItems: 'flex-end', justifyContent: 'center' }}>
                            <TouchableOpacity style={{ paddingHorizontal: 10, height: 20, margin: 5, borderRadius: 40, backgroundColor: theme.paid, alignItems: 'center', justifyContent: 'center' }}
                              onPress={() => this.handleClearVip()}>
                              <Text style={{ fontFamily: 'varela-round', fontSize: 10, textTransform: 'uppercase', color: 'white' }}>{intl.get('remove')}</Text>
                            </TouchableOpacity>
                          </View>
                        </View>
                        <View style={styles.vipInputsRow}>
                          <View style={[styles.detailWrapper, { alignItems: 'flex-start' }]}>
                            <Text style={styles.detailTitle}>VIP ID</Text>
                            <Text style={styles.detailText}>{overlayVip?.vipId || '-'}</Text>
                          </View>
                          <View style={styles.detailWrapper}>
                            <Text style={styles.detailTitle}>VIP CLASS</Text>
                            <Text style={styles.detailText}>{overlayVip?.classId || '-'}</Text>
                          </View>
                          <View style={styles.detailWrapper}>
                            <Text style={styles.detailTitle}>VIP DISC</Text>
                            <Text style={styles.detailText}>{overlayVip?.vipDisc || '-'}</Text>
                          </View>
                        </View>
                        <View style={styles.vipInputsRow}>
                          <View style={[styles.detailWrapper, { alignItems: 'flex-start' }]}>
                            <Text style={styles.detailTitle}>VIP NAME</Text>
                            <Text style={styles.detailText}>{overlayVip?.vipName || '-'}</Text>
                          </View>
                          <View style={styles.detailWrapper}>
                            <Text style={styles.detailTitle}>CUMULATIVE PTS</Text>
                            <Text style={styles.detailText}>{overlayVip?.cumPts || '-'}</Text>
                          </View>
                          <View style={styles.detailWrapper}>
                            <Text style={styles.detailTitle}>REDEEMABLE</Text>
                            <Text style={styles.detailText}>{overlayVip?.vipPtsMoney || '-'}</Text>
                          </View>
                        </View>
                      </View>
                  }
                </View>
                :
                activeTab === 'SOURCE' ?
                  <View style={[styles.overlayTab, { width: overlayTabWidth }]}>
                    <View style={styles.selectWrapper}>
                      <View style={styles.searchBoxWrapper}>
                        <View style={[styles.overlaySearchBox, { marginHorizontal: 0 }]}>
                          <TextInput style={styles.overlaySearchBoxInput}
                            placeholder='ENTER SEARCH TEXT'
                            placeholderTextColor='rgba(255,255,255,0.2)'
                            onChangeText={(overlaySourceSearch) => this.handleOverlaySearchText(overlaySourceSearch)}
                            value={overlaySourceSearch}
                          />
                          <TouchableOpacity style={styles.overlaySearchBoxButton}
                            onPress={() => {
                              this.setState({ overlaySourceSearch: '', overlaySource: '' }, () => this.handleOverlaySearchText(''))
                              Keyboard.dismiss()
                            }}>
                            <AntDesign name='close' size={24} color={theme.grey4} />
                          </TouchableOpacity>
                        </View>
                      </View>
                      <View style={styles.searchBoxInfo}>
                        <Text style={styles.searchBoxInfoText}>{availableVipSources.length} ITEMS</Text>
                      </View>
                      <FlatList
                        style={[{ flex: 1 }]}
                        showsVerticalScrollIndicator={false}
                        data={availableVipSources}
                        keyExtractor={item => item.recKey.toString()}
                        renderItem={({ item }) => (
                          <TouchableOpacity
                            style={[styles.overlayGrid, item.dummy ? styles.hidden : null]}
                            onPress={() => this.setState({
                              overlaySource: item,
                              overlaySourceSearch: item.sourceId,
                            })}
                          >
                            <View style={styles.overlayGridFirst}>
                              <View style={styles.overlayGridFirstLeft}>
                                <Text style={styles.overlayGridFirstLeftText}>{item.sourceId}</Text>
                              </View>
                              <View style={styles.overlayGridFirstRight}>
                                <Text style={styles.overlayGridFirstRightText}></Text>
                              </View>
                            </View>
                            <View style={styles.overlayGridSecond}>
                              <Text style={styles.overlayGridSecondText}>
                                {item.name}
                              </Text>
                            </View>
                            <View style={styles.overlayGridThird}>
                              <View style={styles.overlayGridThirdLeft}>
                              </View>
                              <View style={styles.overlayGridThirdRight}>
                                <Text style={styles.overlayGridThirdRightText}></Text>
                              </View>
                            </View>
                          </TouchableOpacity>
                        )}
                      />
                    </View>
                  </View>
                  :
                  <View style={[styles.overlayTab, { width: overlayTabWidth }]}>
                    <View style={styles.vipInputsContainer}>
                      <View style={styles.vipInputsRow}>
                        <TextInput style={styles.vipInput}
                          placeholder='POSTAL CODE'
                          placeholderTextColor='rgba(255,255,255,0.2)'
                          textContentType='none'
                          autoComplete='off'
                          autoCorrect={false}
                          onChangeText={(text) => this.handleAddressChange('POSTALCODE', text)}
                          value={overlayPostalCode}
                        />
                      </View>
                      <View style={styles.vipInputsRow}>
                        <TextInput style={[styles.vipInput, { width: 390 }]}
                          placeholder='ADDR1'
                          placeholderTextColor='rgba(255,255,255,0.2)'
                          textContentType='none'
                          autoComplete='off'
                          autoCorrect={false}
                          onChangeText={(text) => this.handleAddressChange('ADDR1', text)}
                          value={overlayAddr1}
                        />
                      </View>
                      <View style={styles.vipInputsRow}>
                        <TextInput style={[styles.vipInput, { width: 390 }]}
                          placeholder='ADDR2'
                          placeholderTextColor='rgba(255,255,255,0.2)'
                          textContentType='none'
                          autoComplete='off'
                          autoCorrect={false}
                          onChangeText={(text) => this.handleAddressChange('ADDR2', text)}
                          value={overlayAddr2}
                        />
                      </View>
                      <View style={styles.vipInputsRow}>
                        <TextInput style={[styles.vipInput, { width: 390 }]}
                          placeholder='ADDR3'
                          placeholderTextColor='rgba(255,255,255,0.2)'
                          textContentType='none'
                          autoComplete='off'
                          autoCorrect={false}
                          onChangeText={(text) => this.handleAddressChange('ADDR3', text)}
                          value={overlayAddr3}
                        />
                      </View>
                      <View style={styles.vipInputsRow}>
                        <TextInput style={[styles.vipInput, { width: 390 }]}
                          placeholder='ADDR4'
                          placeholderTextColor='rgba(255,255,255,0.2)'
                          textContentType='none'
                          autoComplete='off'
                          autoCorrect={false}
                          onChangeText={(text) => this.handleAddressChange('ADDR4', text)}
                          value={overlayAddr4}
                        />
                      </View>
                    </View>
                  </View>
          }
          <View style={styles.overlayOptionsContainer}>
            <TouchableOpacity
              style={styles.overlayOptionContainer}
              onPress={() => navigation.goBack()}>
              <AntDesign name='close' size={32} color='white' />
              <Text style={styles.overlayOptionText}>
                {intl.get('close')}
              </Text>
            </TouchableOpacity>
            <TouchableOpacity
              style={styles.overlayOptionContainer}
              onPress={() => this.handlePressPrev()} >
              <AntDesign name='left' size={32} color='white' />
              <Text style={styles.overlayOptionText}>
                PREV
              </Text>
            </TouchableOpacity>
            <TouchableOpacity
              style={styles.overlayOptionContainer}
              onPress={() => this.handlePressNext()} >
              <AntDesign name='right' size={32} color='white' />
              <Text style={styles.overlayOptionText}>
                NEXT
              </Text>
            </TouchableOpacity>
            <TouchableOpacity
              style={styles.overlayOptionContainer}
              onPress={() => this.handleSubmit()}>
              <AntDesign name={mode === 'NEW' ? 'appstore-o' : 'edit'} size={32} color='white' />
              <Text style={styles.overlayOptionText}>
                {mode === 'NEW' ? intl.get('open') : intl.get('edit')}
              </Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  }
}

