import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions,
  FlatList, ScrollView,
} from 'react-native';
import { AntDesign } from '@expo/vector-icons';
import OverlayAlert from './OverlayAlert';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import theme from '../assets/theme';
import intl from 'react-intl-universal';

export default class Printer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,

      overlayProcedureMessageVisible: false,
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
      printers: [],
      loading: false,
    };
    this.handleUpdateFromAlert = this.handleUpdateFromAlert.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  componentDidMount() {
    const { home, serviceEntry } = this.state;
    let url = serviceEntry + 'api/iomodels';
    const params = new URLSearchParams();
    params.append('posNo', home.shopId);
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        console.log('iomodels', response);
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          printers: response,
        }, () => this.handlePingAll())
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handlePingPrinter(item) {
    const { serviceEntry, printers } = this.state;

    let printersUpdate0 = printers;
    printersUpdate0.find(el => el.recKey === item.recKey).statusFlg = 'PING...';
    this.setState({
      printers: printersUpdate0,
    });

    let url = serviceEntry + 'api/iomodels/' + item.recKey;
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        console.log('ping iomodel', response);
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        let printersUpdate = printers;
        if (response.msgId === 'OK') {
          printersUpdate.find(el => el.recKey === item.recKey).statusFlg = 'ONLINE';
        } else {
          printersUpdate.find(el => el.recKey === item.recKey).statusFlg = 'OFFLINE';
        }
        this.setState({
          printers: printersUpdate,
        })
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handlePressPingAll() {
    let options = [];
    options.push({ text: 'Yes', handleText: 'handlePingAll' });
    this.setState({
      overlayAlertInputValue: {
        title: intl.get('action'),
        subtitle: 'Ping All Printers',
        text: 'Send ping to all printers?',
        ok: false,
        cancel: true,
        options: options,
      },
      overlayAlertVisible: true,
    })
  }

  handlePressTestAll() {
    let options = [];
    options.push({ text: 'Yes', handleText: 'handleTestAll' });
    this.setState({
      overlayAlertInputValue: {
        title: intl.get('action'),
        subtitle: 'Test All Printers',
        text: 'Send test command to all printers?',
        ok: false,
        cancel: true,
        options: options,
      },
      overlayAlertVisible: true,
    })
  }

  handleTestAll() {
    const { serviceEntry, home } = this.state;
    let url = serviceEntry + 'api/test-printer';
    const params = new URLSearchParams();
    params.append('userId', home.userId);
    params.append('orgId', home.orgId);
    params.append('locId', home.locId);
    params.append('shopId', home.shopId);
    url += ('?' + params);
    fetch(url, {
      method: 'GET',
    })
      .then(response => response.json())
      .then(response => {
        console.log('post test-printer successful', response);
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handlePingAll() {
    const { printers } = this.state;
    printers.forEach(el => this.handlePingPrinter(el));
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromAlert(text) {
    this.setState({
      overlayAlertVisible: false,
      overlayAlertInputValue: '',
    });
    if (!text) {
      return;
    }
    if (text === 'handleTestAll') {
      this.handleTestAll();
    } else if (text === 'handlePingAll') {
      this.handlePingAll();
    }
  }

  render() {
    const { navigation } = this.props;
    const { printers, loading,
      overlayProcedureMessageVisible, overlayAlertVisible, overlayAlertInputValue, } = this.state;
    console.log('in printers');

    return (
      <View style={styles.container}>
        {
          overlayAlertVisible &&
          <OverlayAlert
            input={overlayAlertInputValue}
            handleUpdateFromAlert={this.handleUpdateFromAlert} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
              <View style={styles.selectionContainer}>
              </View>
            </View>
          </View>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingRight: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.gap}></View>
                <FlatList
                  style={{ flex: 1, width: '100%' }}
                  data={printers}
                  keyExtractor={item => item.recKey.toString()}
                  renderItem={({ item, index }) => {
                    if (index % 2 === 0) {
                      return (
                        <TouchableOpacity
                          style={styles.grid}
                          onPress={() => this.handlePingPrinter(item)}
                        >
                          <View style={styles.gridFirst}>
                            <View style={styles.gridFirstLeft}>
                              <Text style={styles.gridFirstLeftText}>{item.iomodelId}</Text>
                            </View>
                            <View style={styles.gridFirstRight}>
                              <Text style={[styles.gridFirstRightText, { color: item.statusFlg === 'PING...' ? theme.reserved : item.statusFlg === 'ONLINE' ? theme.vacant : theme.paid }]}>
                                {item.statusFlg === 'ONLINE' ? intl.get('online') : item.statusFlg === 'OFFLINE' ? intl.get('offline') : item.statusFlg}
                              </Text>
                            </View>
                          </View>
                          <View style={styles.gridSecond}>
                            <Text style={styles.gridSecondText}>
                              {item.name}
                            </Text>
                          </View>
                          <View style={styles.gridThird}>
                            <View style={styles.gridThirdLeft}>
                            </View>
                            <View style={styles.gridThirdRight}>
                              <Text style={styles.gridThirdRightText}>{item.printPort}</Text>
                            </View>
                          </View>
                        </TouchableOpacity>
                      )
                    }
                  }}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingLeft: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.gap}></View>
                <FlatList
                  style={{ flex: 1, width: '100%' }}
                  data={printers}
                  keyExtractor={item => item.recKey.toString()}
                  renderItem={({ item, index }) => {
                    if (index % 2) {
                      return (
                        <TouchableOpacity
                          key={item.recKey}
                          style={styles.grid}
                          onPress={() => this.handlePingPrinter(item)}
                        >
                          <View style={styles.gridFirst}>
                            <View style={styles.gridFirstLeft}>
                              <Text style={styles.gridFirstLeftText}>{item.iomodelId}</Text>
                            </View>
                            <View style={styles.gridFirstRight}>
                              <Text style={[styles.gridFirstRightText, { color: item.statusFlg === 'PING...' ? theme.reserved : item.statusFlg === 'ONLINE' ? theme.vacant : theme.paid }]}>
                                {item.statusFlg === 'ONLINE' ? intl.get('online') : item.statusFlg === 'OFFLINE' ? intl.get('offline') : item.statusFlg}
                              </Text>
                            </View>
                          </View>
                          <View style={styles.gridSecond}>
                            <Text style={styles.gridSecondText}>
                              {item.name}
                            </Text>
                          </View>
                          <View style={styles.gridThird}>
                            <View style={styles.gridThirdLeft}>
                            </View>
                            <View style={styles.gridThirdRight}>
                              <Text style={styles.gridThirdRightText}>{item.printPort}</Text>
                            </View>
                          </View>
                        </TouchableOpacity>
                      )
                    }
                  }}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => this.handlePressPingAll()}
              >
                <AntDesign
                  name='fork'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>PING {intl.get('all')}</Text>
              </TouchableOpacity>
              <TouchableOpacity style={styles.button}
                onPress={() => this.handlePressTestAll()}
              >
                <AntDesign
                  name='printer'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('test')} {intl.get('all')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

