import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TouchableOpacity, TextInput,
} from 'react-native';
import moment from 'moment';
import theme from '../assets/theme';
import intl from 'react-intl-universal';

export default class OverlayDeposit extends Component {

  constructor(props) {
    super(props);
    this.state = {
      serviceEntry: props.serviceEntry,
      selectedTable: props.selectedTable,

      homeDelivery: props.selectedTable.homeDelivery === 'Y' ? 'Y' : 'N',
      vipName: props.selectedTable.vipName || '',
      vipPhone: props.selectedTable.vipPhone || '',
      dlyDate: props.selectedTable.dlyDate ? moment(props.selectedTable.dlyDate).format('YYYY-MM-DD') : '',
      dpostalcode: props.selectedTable.dpostalcode || '',
      daddress1: props.selectedTable.daddress1 || '',
      daddress2: props.selectedTable.daddress2 || '',
    }
  }

  doPressDelivery(type) {
    this.setState({ homeDelivery: type })
  }

  doChangePostal(text) {
    this.setState({ dpostalcode: text })
    if (text.length === 6) {
      const { serviceEntry } = this.state;
      let url = serviceEntry + 'api/epbsggeo';
      const params = new URLSearchParams();
      params.append('postal', text);
      url += ('?' + params);
      fetch(url, {
        method: 'GET'
      })
        .then(response => {
          if (response.ok) return response.json();
        })
        .then(response => {
          if (response?.address) {
            this.setState({
              daddress1: response.address || '',
            });
          }
        })
    }
  }

  doSubmit() {
    const { vipName, vipPhone, homeDelivery, dlyDate, dpostalcode, daddress1, daddress2 } = this.state;
    this.props.handleUpdateFromDeposit('G', vipName, vipPhone, homeDelivery, dlyDate, dpostalcode, daddress1, daddress2)
  }

  render() {
    const { vipName, vipPhone, homeDelivery, dlyDate, dpostalcode, daddress1, daddress2 } = this.state;
    console.log('in overlayDeposit', this.props);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Deposit Info</Text>
            <View style={{ flexDirection: 'row', marginTop: 20 }}>
              <View style={{ flex: 1, alignItems: 'flex-end', }}>
                <TouchableOpacity style={[styles.tabContainer, { backgroundColor: homeDelivery === 'Y' ? theme.grey1 : 'white' }]}
                  onPress={() => this.doPressDelivery('N')}>
                  <Text style={{ color: homeDelivery === 'Y' ? 'white' : 'black', fontFamily: 'varela-round' }}>Self Collect</Text>
                </TouchableOpacity>
              </View>
              <View style={{ flex: 1, }}>
                <TouchableOpacity style={[styles.tabContainer, { backgroundColor: homeDelivery === 'N' ? theme.grey1 : 'white' }]}
                  onPress={() => this.doPressDelivery('Y')}>
                  <Text style={{ color: homeDelivery === 'N' ? 'white' : 'black', fontFamily: 'varela-round' }}>Delivery</Text>
                </TouchableOpacity>
              </View>
            </View>
          </View>
          <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
            <View style={styles.vipInputsContainer}>
              <View style={styles.vipInputsRow}>
                <TextInput style={styles.vipInput}
                  placeholder='NAME'
                  placeholderTextColor='rgba(255,255,255,0.2)'
                  textContentType='none'
                  autoComplete='off'
                  autoCorrect={false}
                  onChangeText={text => this.setState({ vipName: text })}
                  value={vipName}
                />
                <TextInput style={styles.vipInput}
                  placeholder='PHONE'
                  placeholderTextColor='rgba(255,255,255,0.2)'
                  keyboardType='numeric'
                  textContentType='none'
                  autoComplete='off'
                  autoCorrect={false}
                  onChangeText={text => this.setState({ vipPhone: text })}
                  value={vipPhone}
                />
              </View>
              {
                homeDelivery === 'Y' ?
                  <>
                    <View style={styles.vipInputsRow}>
                      <TextInput style={styles.vipInput}
                        placeholder='POSTAL'
                        placeholderTextColor='rgba(255,255,255,0.2)'
                        textContentType='none'
                        autoComplete='off'
                        autoCorrect={false}
                        onChangeText={text => this.doChangePostal(text)}
                        value={dpostalcode}
                      />
                      <TextInput style={styles.vipInput}
                        placeholder='DLY DATE'
                        placeholderTextColor='rgba(255,255,255,0.2)'
                        textContentType='none'
                        autoComplete='off'
                        autoCorrect={false}
                        onFocus={() => dlyDate === '' && this.setState({ dlyDate: moment().format('YYYY-MM-DD') })}
                        onChangeText={text => this.setState({ dlyDate: text })}
                        value={dlyDate}
                      />
                    </View>
                    <View style={styles.vipInputsRow}>
                      <TextInput style={[styles.vipInput, { flex: 1 }]}
                        placeholder='ADDR 1'
                        placeholderTextColor='rgba(255,255,255,0.2)'
                        textContentType='none'
                        autoComplete='off'
                        autoCorrect={false}
                        onChangeText={text => this.setState({ daddress1: text })}
                        value={daddress1}
                      />
                    </View>
                    <View style={styles.vipInputsRow}>
                      <TextInput style={[styles.vipInput, { flex: 1 }]}
                        placeholder='ADDR 2'
                        placeholderTextColor='rgba(255,255,255,0.2)'
                        textContentType='none'
                        autoComplete='off'
                        autoCorrect={false}
                        onChangeText={text => this.setState({ daddress2: text })}
                        value={daddress2}
                      />
                    </View>
                  </>
                  :
                  <View style={[styles.vipInputsRow, { justifyContent: 'flex-end' }]}>
                    <TextInput style={styles.vipInput}
                      placeholder='DLY DATE'
                      placeholderTextColor='rgba(255,255,255,0.2)'
                      textContentType='none'
                      autoComplete='off'
                      autoCorrect={false}
                      onFocus={() => dlyDate === '' && this.setState({ dlyDate: moment().format('YYYY-MM-DD') })}
                      onChangeText={text => this.setState({ dlyDate: text })}
                      value={dlyDate}
                    />
                  </View>
              }
            </View>
          </View>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.grey4 }]}
            onPress={() => this.doSubmit()}>
            <Text style={styles.overlayOptionText}>{intl.get('submit')}</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromDeposit('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    height: '90%',
    width: '40%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  vipInputsContainer: {
    marginBottom: 20,
    marginHorizontal: 5,
    width: 400,
    alignItems: 'center',
  },
  vipInputsRow: {
    height: 50,
    width: 400,
    flexDirection: 'row',
  },
  vipInput: {
    backgroundColor: theme.grey1,
    borderRadius: 5,
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 16,
    margin: 5,
    paddingHorizontal: 10,
    width: 190,
  },
  tabContainer: {
    backgroundColor: theme.grey1,
    width: 120,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 20,
    height: 40,
    marginHorizontal: 5,
  },
  tabText: {
    fontSize: 10,
    fontFamily: 'varela-round',
    color: 'white',
  },
  tabFocusText: {
    fontSize: 16,
    fontFamily: 'varela-round',
    color: 'white',
  },
});