import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard, Dimensions,
  FlatList,
} from 'react-native';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';
import OverlayRequestAccess from './OverlayRequestAccess';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import theme from '../assets/theme';
import { debug } from '../assets/utility';

export default class Transfer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      tableItems: props.route.params.tableItems,
      selectedTable: props.route.params.selectedTable,

      privileges: props.route.params.masters.privileges,
      settings: props.route.params.masters.settings,

      tables: [],
      availableTables: [],
      selectedTargetTable: '',
      selectedTableItems: [],
      searchText: '',

      overlayRequestAccessVisible: false,
      overlayProcedureMessageVisible: false,
    };
    this.handleSelectTargetTable = this.handleSelectTargetTable.bind(this);
    this.handlePressSelectAll = this.handlePressSelectAll.bind(this);
    this.handleSelectTableItem = this.handleSelectTableItem.bind(this);
    this.handleSearchText = this.handleSearchText.bind(this);
    this.handleUpdateFromRequestAccess = this.handleUpdateFromRequestAccess.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    const { serviceEntry, home, selectedTable } = this.state;
    let url = serviceEntry + 'api/tables';
    const params = new URLSearchParams();
    params.append('shopId', home.shopId);
    params.append('statusFlg', 'C');
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        console.log('tables', response);

        // to state
        this.setState({
          tables: response.filter(el => el.tableId !== selectedTable.tableId),
          availableTables: response.filter(el => el.tableId !== selectedTable.tableId),
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  generateTextForSelectAll() {
    const { selectedTableItems } = this.state;
    if (selectedTableItems.length === 0) {
      return 'SELECT ALL';
    } else {
      return 'DESELECT ALL';
    }
  }

  handlePressSelectAll() {
    const { selectedTableItems, tableItems } = this.state;

    if (selectedTableItems.length === 0) {
      this.setState({
        selectedTableItems: tableItems,
      })
    } else {
      this.setState({
        selectedTableItems: [],
      })
    }
  }

  handleSelectTargetTable(item) {
    this.setState({
      selectedTargetTable: item,
    })
  }

  handleSelectTableItem(item) {
    console.log('tableitem selected', item);
    const { selectedTableItems, tableItems } = this.state;

    let selectedTableItemsUpdate = selectedTableItems;

    if (!selectedTableItems.some(el => el.recKey === item.recKey)) {
      selectedTableItemsUpdate.push(item);
      let comboItems = [];
      comboItems = tableItems.filter(el => el.refRecKey === item.recKey);
      console.log('comboItems', comboItems);
      comboItems.forEach(el => selectedTableItemsUpdate.push(el));
    } else {
      selectedTableItemsUpdate = selectedTableItemsUpdate.filter(el => el.recKey !== item.recKey && el.refRecKey !== item.recKey);
    }
    this.setState({
      selectedTableItems: selectedTableItemsUpdate,
    });
  }

  handleSubmit(superUserId) {
    const { navigation } = this.props;
    const { selectedTable, selectedTableItems, selectedTargetTable, home, serviceEntry, privileges, settings } = this.state;

    if (selectedTableItems.length === 0 || !selectedTargetTable) {
      return;
    }

    if (!(home.adminFlg === 'Y' || privileges.some(el => el.priId === 'TRANSFERITEMS')) && !superUserId) {
      this.setState({
        overlayRequestAccessVisible: true,
      });
      return;
    }

    // convert array to string without items with refRecKey
    let converted = selectedTableItems.filter(el => !el.refRecKey).map(el => el.recKey).reduce((init, el) => { return init + el + ','; }, '');
    console.log('converted', converted);

    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/transfer-items';
    const body = {
      userId: home.userId,
      opentableRecKeyFrom: selectedTable.recKey,
      opentableRecKeyTo: selectedTargetTable.recKey,
      opentableItemRecKeys: converted,
      superUserId: superUserId,
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post transfer-items successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Transfer Items',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            selectedTargetTable.tableId,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        navigation.goBack();
      })
  }

  handleSearchText(text) {
    const { tables, availableTables, searchText } = this.state;
    let availableTablesUpdate = [];
    this.setState({ searchText: text });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);

    availableTablesUpdate = tables;

    if (!text) {
      this.setState({
        availableTables: availableTablesUpdate
      });
      return;
    }
    availableTablesUpdate = availableTablesUpdate.filter(item => {
      return item.tableId.toUpperCase().includes(caseText) || item.sectionId.toUpperCase().includes(caseText);
    });
    this.setState({
      availableTables: availableTablesUpdate
    });
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromRequestAccess(text) {
    const { home, serviceEntry, settings, selectedTable, selectedTargetTable } = this.state;
    if (!text) {
      this.setState({
        overlayRequestAccessVisible: false,
      });
      return;
    }
    let url = serviceEntry + 'api/super-user';
    const body = {
      locId: home.locId,
      accessCode: text,
      priId: 'TRANSFERITEMS',
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post super-user successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Request Access TRANSFERITEMS',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            response.userId,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          overlayRequestAccessVisible: false,
        });
        this.handleSubmit(response.userId);
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  render() {
    const { navigation } = this.props;
    const { tableItems, home, selectedTargetTable, availableTables, searchText, selectedTableItems,
      overlayRequestAccessVisible, overlayProcedureMessageVisible } = this.state;
    console.log('in transfer');

    return (
      <View style={styles.container}>
        {
          overlayRequestAccessVisible &&
          <OverlayRequestAccess
            home={home}
            requestedPrivilege='TRANSFERITEM'
            handleUpdateFromRequestAccess={this.handleUpdateFromRequestAccess} />
        }
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={styles.button}
                onPress={() => navigation.goBack()}>
                <AntDesign
                  name='left'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('back')}</Text>
              </TouchableOpacity>
            </View>
          </View>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingRight: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={[styles.searchBox, { backgroundColor: theme.grey3, alignItems: 'center', justifyContent: 'center' }]}>
                  <TouchableOpacity
                    onPress={() => this.handlePressSelectAll()}
                  >
                    <Text style={styles.titleText}>{this.generateTextForSelectAll()}</Text>
                  </TouchableOpacity>
                </View>
                <View style={styles.searchBoxInfo}>
                  <View style={styles.titleFirstContainer}>
                    <Text style={styles.titleText}>Qty</Text>
                  </View>
                  <View style={styles.titleSecondContainer}>
                    <Text style={styles.titleText}>Desc</Text>
                  </View>
                  <View style={styles.titleThirdContainer}>
                    <Text style={styles.titleText}>Price</Text>
                  </View>
                  <View style={styles.titleFourthContainer}>
                    <Text style={styles.titleText}>Total</Text>
                  </View>
                </View>
                <FlatList
                  style={{ flex: 1 }}
                  showsVerticalScrollIndicator={false}
                  data={tableItems}
                  keyExtractor={item => item.recKey.toString()}
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      style={[styles.tableItemContainer, selectedTableItems.some(el => el.recKey === item.recKey) && { backgroundColor: theme.occupied }, item.refRecKey && { opacity: 0.2 }]}
                      onPress={() => this.handleSelectTableItem(item)}
                      disabled={item.refRecKey ? true : false}
                    >
                      <View style={styles.tableItemFirstContainer}><Text style={styles.tableItemText}>{item.orderQty}</Text></View>
                      <View style={styles.tableItemSecondContainer}
                      >
                        <Text style={styles.tableItemText}>
                          {item.refRecKey ? '- ' : null}{item.menuName}
                        </Text>
                        {item.menuMod ? <Text style={styles.tableItemModText}>{item.menuMod}</Text> : null}
                      </View>
                      <View style={styles.tableItemThirdContainer}>
                        <Text style={styles.tableItemText}>
                          {item.netPrice && item.netPrice.toFixed(2)}
                        </Text>
                      </View>
                      <View style={styles.tableItemFourthContainer}>
                        <Text style={styles.tableItemText}>
                          {item.lineTotal && item.lineTotal.toFixed(2)}
                        </Text>
                      </View>
                    </TouchableOpacity>
                  )}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
        </View>
        <View style={styles.rightContainer}>
          <View style={styles.displayContainer}>
            <View style={[styles.displayWrapper, { paddingLeft: 5 }]}>
              <View style={styles.gap}></View>
              <View style={styles.gridContainer}>
                <View style={styles.searchBox}>
                  <TextInput style={styles.searchBoxInputValue}
                    placeholder='ENTER SEARCH TEXT'
                    placeholderTextColor='rgba(255,255,255,0.2)'
                    value={searchText}
                    onChangeText={(searchText) => this.handleSearchText(searchText)}
                  >
                  </TextInput>
                  <TouchableOpacity style={styles.searchBoxButton}
                    onPress={() => {
                      this.setState({ searchText: '' }, () => this.handleSearchText(''))
                      Keyboard.dismiss()
                    }}
                  >
                    <AntDesign name='close' size={24} color={theme.grey4} />
                  </TouchableOpacity>
                </View>
                <View style={[styles.searchBoxInfo, { justifyContent: 'center' }]}>
                  <Text style={styles.searchBoxInfoText}>{availableTables.length} ITEMS</Text>
                </View>
                <FlatList
                  style={{ flex: 1 }}
                  showsVerticalScrollIndicator={false}
                  data={availableTables}
                  keyExtractor={item => item.recKey.toString()}
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      style={[styles.grid, selectedTargetTable.tableId === item.tableId && { backgroundColor: theme.occupied }]}
                      onPress={() => this.handleSelectTargetTable(item)}
                    >
                      <View style={styles.gridFirst}>
                        <View style={styles.gridFirstLeft}>
                          <Text style={styles.gridFirstLeftText}>{item.sectionId}</Text>
                        </View>
                        <View style={styles.gridFirstRight}>
                          <Text style={[styles.gridFirstRightText, selectedTargetTable.tableId === item.tableId ? { color: 'white' } : { color: theme.occupied }]}>OCCUPIED</Text>
                        </View>
                      </View>
                      <View style={styles.gridSecond}>
                        <Text style={styles.gridSecondText}>
                          {item.tableId}
                        </Text>
                      </View>
                      <View style={styles.gridThird}>
                        <View style={styles.gridThirdLeft}>
                        </View>
                        <View style={styles.gridThirdRight}>
                        </View>
                      </View>
                    </TouchableOpacity>
                  )}
                />
              </View>
              <View style={styles.gap}></View>
            </View>
          </View>
          <View style={styles.navigateContainer}>
            <View style={styles.navigateWrapper}>
              <TouchableOpacity style={[styles.button, (!selectedTargetTable || selectedTableItems.length === 0) && { opacity: 0.2 }]}
                onPress={() => this.handleSubmit()}
                disabled={!selectedTargetTable || selectedTableItems.length === 0}
              >
                <AntDesign
                  name='export'
                  color='white'
                  size={36}
                />
                <Text style={styles.buttonText}>{intl.get('transfer')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </View>
    );
  }
}

