import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import intl from 'react-intl-universal';

export default class OverlayServiceEntry extends Component {

  constructor(props) {
    super(props);
    this.state = {
      overlayText: '',
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
  }

  handleSubmit(overlayText) {
    this.props.handleUpdateFromServiceEntry(overlayText);
  }

  render() {
    const { overlayText } = this.state;
    console.log('in overlayserviceentry');

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Register</Text>
          </View>
          <View style={styles.searchBoxWrapper}>
            <View style={styles.overlaySearchBox}>
              <TextInput style={styles.overlaySearchBoxInput}
                ref={ref => { this.scanTextInput = ref; }}
                autoFocus={true}
                placeholder='EPB ID OR IP:PORT'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(text) => this.setState({ overlayText: text })}
                onSubmitEditing={() => this.handleSubmit(overlayText)}
                value={overlayText}
              />
              <TouchableOpacity style={styles.overlaySearchBoxButton}
                onPress={() => {
                  this.setState({ overlayText: '' })
                  this.scanTextInput.focus()
                }}>
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
          </View>
          <TouchableOpacity style={styles.overlayOption}
            onPress={() => this.handleSubmit(overlayText)}>
            <Text style={styles.overlayOptionText}>{intl.get('submit')}</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromServiceEntry('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
  },
  searchBoxWrapper: {
    height: 50,
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 30,
  },
  overlaySearchBox: {
    backgroundColor: theme.grey1,
    height: 50,
    borderRadius: 100,
    marginHorizontal: 5,
    flexDirection: 'row',
    flex: 1,
  },
  overlaySearchBoxInput: {
    flex: 1,
    fontFamily: 'varela-round',
    fontSize: 16,
    color: 'white',
    paddingHorizontal: 20,
    height: 50,
  },
  overlaySearchBoxButton: {
    height: 50,
    width: 50,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 50,
  },
  searchBoxInfo: {
    height: 20,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  searchBoxInfoText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
});