import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TouchableOpacity,
} from 'react-native';
import theme from '../assets/theme';
import intl from 'react-intl-universal';

export default class OverlayAutoLogout extends Component {

  constructor(props) {
    super(props);
    this.state = {
      timer: 5,
    };
    this.countdown = () => { };
  }

  componentDidMount() {
    let initialTimer = 5;
    this.countdown = setInterval(() => {
      initialTimer -= 1;
      this.setState({
        timer: initialTimer,
      });
      if (initialTimer === 0) {
        clearInterval(this.countdown);
        this.props.handleUpdateFromAutoLogout(initialTimer);
      }
    }, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.countdown);
  }

  render() {
    const { timer } = this.state;
    console.log('in overlayautologout', this.props);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Auto Logout</Text>
            <Text style={styles.overlayText}>{timer}</Text>
          </View>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromAutoLogout('')}
          >
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View >
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
});