import React, { Component } from 'react';
import {
  StyleSheet, Text, View, TouchableOpacity,
  Dimensions, ScrollView,
} from 'react-native';
import theme from '../assets/theme';

import OverlayChoosePrinter from './OverlayChoosePrinter';
import OverlaySplitItems from './OverlaySplitItems';

export default class Split extends Component {

  constructor(props) {
    super(props);
    this.state = {
      receipts: this.props.availableReceipts.map(el => ({
        docId: el.docId,
        recKey: el.recKey,
        receiptItems: [],
      })),
      selectedTableItems: [],
      overlaySplitItemsVisible: false,
      overlayChoosePrinterVisible: false,
    };
    this.handleSelectTableItem = this.handleSelectTableItem.bind(this);
    this.handleSplitSelectedTableItems = this.handleSplitSelectedTableItems.bind(this);
    this.handleUndoAll = this.handleUndoAll.bind(this);
    this.handleSelectAllUnsplitTableItems = this.handleSelectAllUnsplitTableItems.bind(this);
    this.handleDeselectAllUnsplitTableItems = this.handleDeselectAllUnsplitTableItems.bind(this);
    this.handleUpdateFromSplitItems = this.handleUpdateFromSplitItems.bind(this);
    this.handleUpdateFromChoosePrinter = this.handleUpdateFromChoosePrinter.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {

  }

  handleSelectTableItem(item) {
    console.log('menu selected', item);
    const { selectedTableItems, receipts } = this.state;
    const { tableItems } = this.props;
    if (receipts[0].receiptItems.some(obj => obj.recKey === item.recKey)) {
      return;
    }

    let selectedTableItemsUpdate = selectedTableItems;
    if (!selectedTableItems.some(el => el.recKey === item.recKey)) {
      selectedTableItemsUpdate.push(item);
      let comboItems = [];
      comboItems = tableItems.filter(el => el.refRecKey === item.recKey);
      console.log('comboItems', comboItems);
      comboItems.forEach(el => selectedTableItemsUpdate.push(el));
    } else {
      selectedTableItemsUpdate = selectedTableItemsUpdate.filter(el => el.recKey !== item.recKey && el.refRecKey !== item.recKey);
    }
    this.setState({
      selectedTableItems: selectedTableItemsUpdate,
    });
  }

  handleSelectAllUnsplitTableItems() {
    const { tableItems } = this.props;
    const { selectedTableItems, receipts } = this.state;
    let selectedTableItemsUpdate = selectedTableItems;
    tableItems.forEach(el => {
      if (receipts[0].receiptItems.some(obj => obj.recKey === el.recKey) || selectedTableItems.some(obj => obj.recKey === el.recKey)) {
        return;
      }
      selectedTableItemsUpdate.push(el);
    });
    this.setState({
      selectedTableItems: selectedTableItemsUpdate,
    });
  }

  handleDeselectAllUnsplitTableItems() {
    this.setState({
      selectedTableItems: [],
    });
  }

  handleSplitSelectedTableItems() {
    const { selectedTableItems } = this.state;
    if (selectedTableItems.length === 0) {
      return;
    }
    this.setState({
      overlaySplitItemsVisible: true,
    });
  }

  handleUndoAll() {
    this.setState({
      receipts: this.props.availableReceipts.map(el => ({
        docId: el.docId,
        recKey: el.recKey,
        receiptItems: [],
      })),
    });
  }

  handleUpdateFromChoosePrinter(printer, purpose) {
    this.setState({
      overlayChoosePrinterVisible: false,
    });
    if (!printer) {
      return;
    }
    this.handleSubmit(printer);
  }

  handleUpdateFromSplitItems(distribution) {
    console.log('distribution', distribution);
    if (!distribution) {
      this.setState({
        overlaySplitItemsVisible: false,
        selectedTableItems: [],
      });
      return;
    }
    const { receipts, selectedTableItems } = this.state;
    let receiptsUpdate = receipts;
    receiptsUpdate.forEach(el => {
      selectedTableItems.forEach(obj => {
        el.receiptItems.push({
          ...obj,
          percentage: distribution.find(item => item.docId === el.docId).percentage
        });
      })
    })
    this.setState({
      overlaySplitItemsVisible: false,
      selectedTableItems: [],
      receipts: receiptsUpdate,
    });
  }

  handleSubmit(printer) {
    const { cashierPrinters, home } = this.props;
    const { receipts } = this.state;

    if (cashierPrinters.length > 1 && !printer && !home.defaultPrinter) {
      this.setState({
        overlayChoosePrinterVisible: true,
      });
      return;
    }

    this.props.handleUpdateFromSplit(receipts, home.defaultPrinter || printer);
  }

  render() {
    const { serviceEntry, home, selectedTable, availableReceipts, tableItems, cashierPrinters } = this.props;
    const { receipts, selectedTableItems, overlaySplitItemsVisible, overlayChoosePrinterVisible } = this.state;
    console.log('in split');
    return (
      <View style={styles.container}>
        {
          overlaySplitItemsVisible &&
          <OverlaySplitItems
            receipts={receipts}
            selectedTableItems={selectedTableItems}
            handleUpdateFromSplitItems={this.handleUpdateFromSplitItems} />
        }
        {
          overlayChoosePrinterVisible &&
          <OverlayChoosePrinter
            printPurpose='SPLIT BY ITEM'
            cashierPrinters={cashierPrinters}
            handleUpdateFromChoosePrinter={this.handleUpdateFromChoosePrinter} />
        }
        <View style={styles.leftContainer}>
          <View style={styles.wrapper}>
            <View style={styles.overlayHeader}>
              <Text style={styles.overlayTitleText}>TABLE</Text>
              <Text style={styles.overlaySubtitleText}>{selectedTable.tableId}</Text>
              <Text style={styles.overlayTitleText}>{tableItems.length} ITEMS</Text>
            </View>
            <View style={styles.buttonsContainer}>
              <TouchableOpacity style={styles.button}
                onPress={() => this.handleUndoAll()}
              >
                <Text style={styles.buttonText}>Undo</Text>
                <Text style={styles.buttonText}>All</Text>
              </TouchableOpacity>
              <TouchableOpacity style={styles.button}
                onPress={() => this.handleDeselectAllUnsplitTableItems()}
              >
                <Text style={styles.buttonText}>Deselect</Text>
                <Text style={styles.buttonText}>All</Text>
              </TouchableOpacity>
              <TouchableOpacity style={styles.button}
                onPress={() => this.handleSelectAllUnsplitTableItems()}
              >
                <Text style={styles.buttonText}>Select</Text>
                <Text style={styles.buttonText}>All</Text>
              </TouchableOpacity>
            </View>
            <ScrollView style={[styles.itemsContainer]}>
              <View style={{ paddingTop: 5 }}></View>
              {
                tableItems.map(item => (
                  <TouchableOpacity key={item.recKey}
                    disabled={(receipts[0].receiptItems.some(obj => obj.recKey === item.recKey) || item.refRecKey) ? true : false}
                    style={[styles.tableItemContainer, selectedTableItems.some(el => el.recKey === item.recKey) && { backgroundColor: theme.occupied }, (receipts[0].receiptItems.some(obj => obj.recKey === item.recKey) || item.refRecKey) && { opacity: 0.2 }]}
                    onPress={() => this.handleSelectTableItem(item)}
                  >
                    <View style={styles.tableItemFirstContainer}><Text style={styles.tableItemText}>{item.orderQty}</Text></View>
                    <View style={styles.tableItemSecondContainer}
                    >
                      <Text style={styles.tableItemText}>
                        {item.refRecKey ? '- ' : null}{item.menuName}
                      </Text>
                      {item.menuMod ? <Text style={styles.tableItemModText}>{item.menuMod}</Text> : null}
                    </View>
                    <View style={styles.tableItemThirdContainer}>
                      <Text style={styles.tableItemText}>
                      </Text>
                    </View>
                  </TouchableOpacity>
                ))
              }
            </ScrollView>
            <TouchableOpacity style={[styles.actionButton, selectedTableItems.length !== 0 ? { backgroundColor: theme.occupied } : { opacity: 0.2 }]}
              disabled={selectedTableItems.length === 0}
              onPress={() => this.handleSplitSelectedTableItems()}
            >
              <Text style={styles.buttonText}>Split Selected</Text>
            </TouchableOpacity>
            {
              receipts[0].receiptItems.length === tableItems.length ?
                <TouchableOpacity style={styles.actionButton}
                  onPress={() => this.handleSubmit()}
                >
                  <Text style={[styles.buttonText, { color: theme.reserved }]}>Confirm Split</Text>
                </TouchableOpacity>
                :
                <TouchableOpacity style={styles.actionButton}
                  onPress={() => this.props.handleUpdateFromSplit('')}
                >
                  <Text style={[styles.buttonText, { color: theme.paid }]}>Quit</Text>
                </TouchableOpacity>
            }
          </View>
        </View>
        <View style={styles.rightContainer}>
          <ScrollView style={styles.splitScroll}
            horizontal={true}
            snapToInterval={wrapperWidth}
            decelerationRate='fast'
          >
            {
              receipts.map((obj, index) => (
                <View style={styles.wrapper}
                  key={obj.docId}
                >
                  <View style={styles.overlayHeader}>
                    <Text style={[styles.overlayTitleText, { color: theme.paid, opacity: 1 }]}>SPLIT {index + 1} OF {receipts.length}</Text>
                    <Text style={styles.overlaySubtitleText}>{obj.docId}</Text>
                    <Text style={styles.overlayTitleText}>{obj.receiptItems.length} ITEMS</Text>
                  </View>
                  <ScrollView style={styles.itemsContainer}>
                    <View style={{ paddingTop: 5 }}></View>
                    {
                      obj.receiptItems.map(item => (
                        <TouchableOpacity key={item.recKey}
                          style={styles.tableItemContainer}
                        // onPress={() => this.handleSelectTableItem(item)}
                        >
                          <View style={styles.tableItemFirstContainer}><Text style={styles.tableItemText}>{item.orderQty}</Text></View>
                          <View style={styles.tableItemSecondContainer}
                          >
                            <Text style={styles.tableItemText}>
                              {item.refRecKey ? '- ' : null}{item.menuName}
                            </Text>
                            {item.menuMod ? <Text style={styles.tableItemModText}>{item.menuMod}</Text> : null}
                          </View>
                          <View style={styles.tableItemThirdContainer}>
                            <Text style={styles.tableItemText}>
                              <Text style={{ color: theme.reserved }}>{item.percentage}</Text><Text style={{ color: theme.grey1 }}> %</Text>
                            </Text>
                          </View>
                        </TouchableOpacity>
                      ))
                    }
                  </ScrollView>
                </View>
              ))
            }
          </ScrollView>
        </View>
      </View>
    );

  }
}

const wrapperWidth = Dimensions.get('window').width / 3;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'row',
    backgroundColor: theme.grey1,
  },
  leftContainer: {
    flex: 1,
  },
  rightContainer: {
    flex: 2,
  },
  wrapper: {
    flex: 1,
    width: wrapperWidth,
  },
  splitScroll: {
    width: '100%',
    height: '100%',
  },
  itemsContainer: {
    flex: 1,
    borderRadius: 5,
    margin: 5,
    backgroundColor: theme.grey2,
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  buttonsContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  button: {
    height: 72,
    width: 72,
    borderRadius: 5,
    margin: 5,
    backgroundColor: theme.grey2,
    alignItems: 'center',
    justifyContent: 'center',
  },
  buttonText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 12,
    textTransform: 'uppercase',
  },
  actionButton: {
    height: 72,
    margin: 5,
    backgroundColor: theme.grey2,
    alignItems: 'center',
    justifyContent: 'center',
  },
  tableItemText: {
    fontFamily: 'varela-round',
    color: 'white',
  },
  tableItemModText: {
    fontFamily: 'varela-round',
    fontSize: 12,
    color: theme.reserved,
  },
  tableItemContainer: {
    flex: 1,
    flexDirection: 'row',
    minHeight: 50,
    paddingVertical: 5,
    marginHorizontal: 5,
    marginBottom: 5,
    backgroundColor: theme.grey4,
  },
  tableItemFirstContainer: {
    width: 50,
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 3,
    borderRightColor: 'white',
  },
  tableItemSecondContainer: {
    flex: 1,
    justifyContent: 'center',
    paddingLeft: 10,
  },
  tableItemThirdContainer: {
    alignItems: 'center',
    justifyContent: 'center',
    width: 50,
  },
});