import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Keyboard,
  FlatList,
} from 'react-native';
import theme from '../assets/theme';
import { AntDesign } from '@expo/vector-icons';
import OverlayRequestAccess from './OverlayRequestAccess';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import { debug } from '../assets/utility';
import intl from 'react-intl-universal';

export default class OverlayChangePrice extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.home,
      serviceEntry: props.serviceEntry,
      settings: props.settings,
      privileges: props.privileges,
      discounts: props.discounts,
      selectedTableItem: props.selectedTableItem,

      availableDiscounts: props.discounts,
      overlaySearchText: '',
      overlayText: '',
      discountSelected: false,
      selectedDiscount: '',
      validatePass: true,
      requestedPrivilege: '',
      overlayRequestAccessVisible: false,
      overlayProcedureMessageVisible: false,
    };
    this.handleOverlaySearchText = this.handleOverlaySearchText.bind(this);
    this.handleUpdateFromRequestAccess = this.handleUpdateFromRequestAccess.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
    this.handleValidate = this.handleValidate.bind(this);
  }

  componentDidMount() {
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromRequestAccess(text) {
    const { home, serviceEntry, settings, requestedPrivilege } = this.state;
    if (!text) {
      this.setState({
        overlayRequestAccessVisible: false,
      });
      return;
    }
    let url = serviceEntry + 'api/super-user';
    const body = {
      locId: home.locId,
      accessCode: text,
      priId: requestedPrivilege,
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post super-user successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Request Access ' + requestedPrivilege,
            home.userName,
            response.message,
            '',
            '',
            response.userId,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          overlayRequestAccessVisible: false,
        });
        this.handleValidate(response.userId);
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleValidate(superUserId) {
    const { home, privileges, overlayText, selectedDiscount, selectedTableItem } = this.state;
    if (!overlayText) {
      this.setState({
        validatePass: false,
      });
      return;
    }
    if (!(home.adminFlg === 'Y' || privileges.some(el => el.priId === 'CHANGEPRICE') || selectedTableItem.listPrice === 0) && !superUserId) {
      this.setState({
        overlayRequestAccessVisible: true,
        requestedPrivilege: 'CHANGEPRICE',
      });
      return;
    }
    this.props.handleUpdateFromChangePrice(overlayText, selectedDiscount || '-', superUserId);
  }

  handleOverlaySearchText(text) {
    const { discounts, availableDiscounts } = this.state;
    let availableDiscountsUpdate = [];
    this.setState({ overlaySearchText: text });
    let caseText = text.toUpperCase();
    console.log('caseText', caseText);
    console.log('available', this.state.availableDiscounts);

    availableDiscountsUpdate = discounts;
    if (!text) {
      this.setState({
        availableDiscounts: availableDiscountsUpdate
      });
      return;
    }
    availableDiscountsUpdate = availableDiscountsUpdate.filter(item => {
      return item.discId.toUpperCase().includes(caseText) || item.discName.toUpperCase().includes(caseText);
    });
    this.setState({
      availableDiscounts: availableDiscountsUpdate
    });
  }

  render() {
    const { home, overlaySearchText, overlayText, discountSelected, availableDiscounts, discounts,
      selectedDiscount, validatePass, overlayProcedureMessageVisible,
      overlayRequestAccessVisible, requestedPrivilege } = this.state;
    console.log('in overlaychangeprice, state', this.state);

    return (
      <View style={styles.overlay}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        {
          overlayRequestAccessVisible &&
          <OverlayRequestAccess
            home={home}
            requestedPrivilege={requestedPrivilege}
            handleUpdateFromRequestAccess={this.handleUpdateFromRequestAccess} />
        }
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Change Price</Text>
          </View>
          <View style={styles.searchBoxWrapper}>
            <View style={styles.overlaySearchBox}>
              <TextInput style={[styles.overlaySearchBoxInput, validatePass ? null : { color: theme.paid }]}
                placeholder='ENTER PRICE'
                placeholderTextColor='rgba(255,255,255,0.2)'
                onChangeText={(text) => this.setState({ overlayText: text, validatePass: true })}
                value={overlayText}
              />
              <TouchableOpacity style={styles.overlaySearchBoxButton}
                onPress={() => {
                  this.setState({ overlayText: '' })
                  Keyboard.dismiss()
                }}>
                <AntDesign name='close' size={24} color={theme.grey4} />
              </TouchableOpacity>
            </View>
          </View>
          <View style={{ paddingBottom: 10 }}></View>
          {
            discountSelected ?
              <View style={{ flex: 1 }}>
                <View style={styles.searchBoxWrapper}>
                  <TouchableOpacity style={styles.overlaySearchBox}
                    onPress={() => this.setState({
                      overlaySearchText: '',
                      selectedDiscount: '',
                      discountSelected: false,
                    })}>
                    <Text style={styles.selectText}>{selectedDiscount.discId}</Text>
                  </TouchableOpacity>
                </View>
                <View style={{ paddingBottom: 5 }}></View>
              </View>
              :
              <View style={styles.selectWrapper}>
                <View style={styles.searchBoxWrapper}>
                  <View style={[styles.overlaySearchBox, { marginHorizontal: 0 }]}>
                    <TextInput style={styles.overlaySearchBoxInput}
                      placeholder='ENTER SEARCH TEXT'
                      placeholderTextColor='rgba(255,255,255,0.2)'
                      onChangeText={(searchText) => this.handleOverlaySearchText(searchText)}
                      value={overlaySearchText}
                    />
                    <TouchableOpacity style={styles.overlaySearchBoxButton}
                      onPress={() => {
                        this.setState({ overlaySearchText: '' }, () => this.handleOverlaySearchText(''))
                        Keyboard.dismiss()
                      }}>
                      <AntDesign name='close' size={24} color={theme.grey4} />
                    </TouchableOpacity>
                  </View>
                </View>
                <View style={styles.searchBoxInfo}>
                  <Text style={styles.searchBoxInfoText}>{availableDiscounts.length} ITEMS</Text>
                </View>
                <FlatList
                  style={[{ flex: 1 }]}
                  data={availableDiscounts}
                  keyExtractor={item => item.recKey.toString()}
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      style={[styles.overlayGrid, item.dummy ? styles.hidden : null]}
                      onPress={() => this.setState({
                        discountSelected: true,
                        selectedDiscount: item,
                        validatePass: true,
                      })}>
                      <View style={styles.overlayGridFirst}>
                        <View style={styles.overlayGridFirstLeft}>
                          <Text style={styles.overlayGridFirstLeftText}>{item.discId}</Text>
                        </View>
                        <View style={styles.overlayGridFirstRight}>
                          <Text style={styles.overlayGridFirstRightText}></Text>
                        </View>
                      </View>
                      <View style={styles.overlayGridSecond}>
                        <Text style={styles.overlayGridSecondText}>
                          {item.discName}
                        </Text>
                      </View>
                      <View style={styles.overlayGridThird}>
                        <View style={styles.overlayGridThirdLeft}>
                        </View>
                        <View style={styles.overlayGridThirdRight}>
                          <Text style={styles.overlayGridThirdRightText}></Text>
                        </View>
                      </View>
                    </TouchableOpacity>
                  )}
                />
              </View>
          }
          <TouchableOpacity style={styles.overlayOption}
            onPress={() => this.handleValidate()}>
            <Text style={styles.overlayOptionText}>{intl.get('submit')}</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromChangePrice('', '')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    height: '80%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
  },
  searchBoxWrapper: {
    height: 50,
    flexDirection: 'row',
    alignItems: 'center',
  },
  overlaySearchBox: {
    backgroundColor: theme.grey1,
    height: 50,
    borderRadius: 100,
    marginHorizontal: 5,
    flexDirection: 'row',
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlaySearchBoxInput: {
    flex: 1,
    fontFamily: 'varela-round',
    fontSize: 16,
    color: 'white',
    paddingHorizontal: 20,
    height: 50,
  },
  overlaySearchBoxButton: {
    height: 50,
    width: 50,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 50,
  },
  overlayBackButton: {
    // backgroundColor: 'white',
    width: 50,
    height: 50,
    borderRadius: 50,
    marginHorizontal: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayGrid: {
    flex: 1,
    margin: 5,
    marginTop: 0,
    marginBottom: 10,
    backgroundColor: theme.grey4,
    flexDirection: 'column',
    padding: 10,
  },
  overlayGridFirst: {
    flexDirection: 'row'
  },
  overlayGridSecond: {
    marginVertical: 5,
  },
  overlayGridThird: {
    flexDirection: 'row'
  },
  overlayGridFirstLeft: {
    flex: 1,
  },
  overlayGridFirstRight: {
    flex: 1,
    alignItems: 'flex-end',
  },
  overlayGridThirdLeft: {
    flex: 1,
  },
  overlayGridThirdRight: {
    flex: 1,
    alignItems: 'flex-end',
  },
  overlayGridFirstLeftText: {
    color: 'white',
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  overlayGridFirstRightText: {
    color: theme.vacant,
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  overlayGridSecondText: {
    color: 'white',
    fontSize: 16,
    fontFamily: 'varela-round',
    opacity: 0.8,
  },
  overlayGridThirdRightText: {
    color: 'white',
    fontSize: 12,
    fontFamily: 'varela-round'
  },
  searchBoxInfo: {
    height: 20,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  searchBoxInfoText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
  selectText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 16,
  },
  selectWrapper: {
    flex: 1,
    backgroundColor: theme.grey1,
    borderRadius: 5,
    marginHorizontal: 5,
    marginBottom: 5,
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
});