import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TouchableOpacity,
} from 'react-native';
import intl from 'react-intl-universal';
import { AntDesign, MaterialCommunityIcons } from '@expo/vector-icons';
import theme from '../assets/theme';

export default class OverlayTableActions extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    const { selectedTable, tableItems } = this.props;
    console.log('in overlayadditionalfunctions', this.props);

    return (
      <View style={styles.overlay}>
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('table')} {selectedTable.tableId}</Text>
            <Text style={styles.overlaySubtitleText}>{intl.get('table')} {intl.get('action')}</Text>
            {/* <Text style={styles.overlayText}>Select an operation</Text> */}
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={[styles.button, (!selectedTable || selectedTable.sectionId === 'SYS' || selectedTable.statusFlg !== 'A') && { opacity: 0.2 }]}
              disabled={!selectedTable || selectedTable.sectionId === 'SYS' || selectedTable.statusFlg !== 'A'}
              onPress={() => this.props.handleUpdateFromTableActions('openTable')}>
              <AntDesign name='appstore-o' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('open')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, (!selectedTable || !(selectedTable.statusFlg === 'C' || selectedTable.sectionId === 'SYS')) && { opacity: 0.2 }]}
              disabled={!selectedTable || !(selectedTable.statusFlg === 'C' || selectedTable.sectionId === 'SYS')}
              onPress={() => this.props.handleUpdateFromTableActions('editTable')}>
              <AntDesign name='edit' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('edit')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, (!selectedTable || selectedTable.sectionId === 'SYS' || selectedTable.statusFlg !== 'A') && { opacity: 0.2 }]}
              disabled={!selectedTable || selectedTable.sectionId === 'SYS' || selectedTable.statusFlg !== 'A'}
              onPress={() => this.props.handleUpdateFromTableActions('reserveTable')}>
              <AntDesign name='calendar' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('reserve')}</Text>
            </TouchableOpacity>
            {
              (selectedTable?.sectionId !== 'SYS' && selectedTable?.statusFlg === 'A') ?
                <TouchableOpacity style={[styles.button, (!selectedTable || selectedTable.statusFlg !== 'A') && { opacity: 0.2 }]}
                  disabled={!selectedTable || selectedTable.statusFlg !== 'A'}
                  onPress={() => this.props.handleUpdateFromTableActions('disableTable')}>
                  <MaterialCommunityIcons name='cancel' color='white' size={32} />
                  <Text style={styles.buttonText}>{intl.get('disable')}</Text>
                </TouchableOpacity>
                :
                <TouchableOpacity style={[styles.button, (!selectedTable || selectedTable.statusFlg === 'A') && { opacity: 0.2 }]}
                  disabled={!selectedTable || selectedTable.statusFlg === 'A'}
                  onPress={() => this.props.handleUpdateFromTableActions('resetTable')}>
                  <AntDesign name='reload1' color='white' size={32} />
                  <Text style={styles.buttonText}>{intl.get('reset')}</Text>
                </TouchableOpacity>
            }
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={[styles.button, (!selectedTable || !(selectedTable.statusFlg === 'B' || selectedTable.statusFlg === 'C') || selectedTable?.sectionId === 'SYS') && { opacity: 0.2 }]}
              disabled={!selectedTable || !(selectedTable.statusFlg === 'B' || selectedTable.statusFlg === 'C') || selectedTable?.sectionId === 'SYS'}
              onPress={() => this.props.handleUpdateFromTableActions('mergeTable')}>
              <AntDesign name='menufold' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('mergetable')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, (!selectedTable || !(selectedTable.statusFlg === 'B' || selectedTable.statusFlg === 'C') || selectedTable?.sectionId === 'SYS') && { opacity: 0.2 }]}
              disabled={!selectedTable || !(selectedTable.statusFlg === 'B' || selectedTable.statusFlg === 'C') || selectedTable?.sectionId === 'SYS'}
              onPress={() => this.props.handleUpdateFromTableActions('splitTable')}>
              <AntDesign name='menuunfold' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('splittable')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, (!selectedTable || selectedTable.statusFlg !== 'C') && { opacity: 0.2 }]}
              disabled={!selectedTable || selectedTable.statusFlg !== 'C'}
              onPress={() => this.props.handleUpdateFromTableActions('changeTable')}>
              <AntDesign name='swap' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('changetable')}</Text>
            </TouchableOpacity>
          </View>
          <View style={styles.buttonsContainer}>
            <TouchableOpacity style={[styles.button, (!selectedTable || !(selectedTable.statusFlg === 'C' || selectedTable.sectionId === 'SYS')) && { opacity: 0.2 }]}
              disabled={!selectedTable || !(selectedTable.statusFlg === 'C' || selectedTable.sectionId === 'SYS')}
              onPress={() => this.props.handleUpdateFromTableActions('viewResequence')}>
              <AntDesign name='bars' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('resequence')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, (!selectedTable || selectedTable.statusFlg !== 'C') && { opacity: 0.2 }]}
              disabled={!selectedTable || selectedTable.statusFlg !== 'C'}
              onPress={() => this.props.handleUpdateFromTableActions('viewTransfer')}>
              <AntDesign name='export' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('transferitems')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, (!selectedTable || !(selectedTable.statusFlg === 'C' || selectedTable.sectionId === 'SYS')) && { opacity: 0.2 }]}
              disabled={!selectedTable || !(selectedTable.statusFlg === 'C' || selectedTable.sectionId === 'SYS')}
              onPress={() => this.props.handleUpdateFromTableActions('headerDiscount')}>
              <AntDesign name='exception1' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('headerdisc')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, (!selectedTable || !(selectedTable.statusFlg === 'C' || selectedTable.sectionId === 'SYS')) && { opacity: 0.2 }]}
              disabled={!selectedTable || !(selectedTable.statusFlg === 'C' || selectedTable.sectionId === 'SYS')}
              onPress={() => this.props.handleUpdateFromTableActions('printBill')}>
              <AntDesign name='book' color='white' size={32} />
              <Text style={styles.buttonText}>{intl.get('printbill')}</Text>
            </TouchableOpacity>
          </View>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid, marginTop: 20 }]}
            onPress={() => this.props.handleUpdateFromTableActions('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View >
    );
  }
}

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    minWidth: '50%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  buttonsContainer: {
    paddingHorizontal: 10,
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
  },
  button: {
    height: 100,
    width: 100,
    margin: 5,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: theme.grey1,
    borderRadius: 5,
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
});