import React, { Component } from 'react';
import {
  StyleSheet, Text, View, TouchableOpacity, Dimensions,
  FlatList, ScrollView,
} from 'react-native';
import { AntDesign, Fontisto, FontAwesome, MaterialCommunityIcons, } from '@expo/vector-icons';
import theme from '../assets/theme';
import moment from 'moment';
import { debug } from '../assets/utility';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import intl from 'react-intl-universal';

export default class Trace extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      selectedTable: props.route.params.selectedTable,

      maps: [],
      thisTable: props.route.params.selectedTable ? true : false,
      thisUser: false,

      overlayChoosePrinterVisible: false,
    };
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  componentDidMount() {
    this.doReload()
  }

  doReload() {
    const { serviceEntry, home, selectedTable, thisTable, thisUser } = this.state;
    let url = serviceEntry + 'api/debuglogs';
    const params = new URLSearchParams();
    thisUser && params.append('msg2', home.userName);
    thisTable && params.append('msg4', selectedTable.tableId);
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        this.setState({
          maps: response,
        });
      })
  }

  doLoad() {
    const { serviceEntry, home, selectedTable, thisTable, thisUser, maps } = this.state;
    // end reached
    if (maps.length < 40) return;
    let url = serviceEntry + 'api/debuglogs';
    const params = new URLSearchParams();
    params.append('recKey', maps[maps.length - 1].recKey);
    thisUser && params.append('msg2', home.userName);
    thisTable && params.append('msg4', selectedTable.tableId);
    url += ('?' + params);
    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        this.setState({
          maps: [...maps, ...response],
        });
      })
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  doToggleTable() {
    const { thisTable } = this.state;
    this.setState({
      thisTable: !thisTable,
    }, () => this.doReload())
  }

  doToggleUser() {
    const { thisUser } = this.state;
    this.setState({
      thisUser: !thisUser,
    }, () => this.doReload())
  }

  render() {
    const { navigation } = this.props;
    const { home, selectedTable,
      maps, thisUser, thisTable,
      overlayProcedureMessageVisible } = this.state;
    // console.log('render trace', maps && maps[0]);

    return (
      <View style={styles.container}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.containerFirst}>
          <View style={{ flex: 1 }}>
            <TouchableOpacity style={styles.button}
              onPress={() => navigation.goBack()}>
              <AntDesign name='left' color='white' size={36} />
              <Text style={styles.buttonText}>{intl.get('back')}</Text>
            </TouchableOpacity>
          </View>
        </View>
        <View style={styles.containerSecond}>
          <TouchableOpacity style={styles.overlayHeader} onPress={() => this.traceRef.scrollToOffset({ animated: true, offset: 0 })}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>{intl.get('trace')}</Text>
            <Text style={styles.overlayTitleText}></Text>
          </TouchableOpacity>
          <FlatList
            style={{ flex: 1 }}
            ref={ref => this.traceRef = ref}
            showsVerticalScrollIndicator={false}
            data={maps}
            onEndReachedThreshold={0.4}
            onEndReached={() => this.doLoad()}
            keyExtractor={(item) => item.recKey.toString()}
            renderItem={({ item, index }) => (
              <ScrollView
                style={{ minHeight: 40, paddingVertical: 5, }}
                contentContainerStyle={{ alignItems: 'center' }}
                horizontal
                alwaysBounceHorizontal={false}
                showsHorizontalScrollIndicator={false}>
                <Text style={[styles.traceText, { textAlign: 'right', width: 40, color: theme.grey5 }]}>{moment(item.createDate).format('ddd')}</Text>
                <Text style={[styles.traceText, { marginLeft: 10, }]}>{moment(item.createDate).format('HH:mm')}</Text>
                <View style={{ width: 30, marginHorizontal: 5, alignItems: 'center', }}>
                  {
                    item.msg1 === 'Fire Course' &&
                    <Fontisto name="fire" size={20} color={theme.reserved} style={{ marginLeft: 1 }} />
                  }
                  {
                    item.msg1 === 'Send Order' &&
                    <FontAwesome name="send" size={18} color={theme.occupied} style={{}} />
                  }
                  {
                    item.msg1 === 'Finish Payment' &&
                    <MaterialCommunityIcons name="checkbox-blank" size={22} color={theme.paid} style={{}} />
                  }
                  {
                    item.msg1 === 'Finish Dayclose' &&
                    <MaterialCommunityIcons name="moon-full" size={22} color={theme.reserved} style={{}} />
                  }
                  {
                    (item.msg1 === 'Open Table' || item.msg1 === 'Create Table' || (item.msg1 === 'Reopen Receipt' && !item.msg3)) &&
                    <MaterialCommunityIcons name="checkbox-blank" size={22} color={theme.occupied} style={{}} />
                  }
                </View>
                <Text style={[styles.traceText, { color: theme.grey5 }]}>{item.msg2} </Text>
                <Text style={[styles.traceText, { marginLeft: 5, color: theme.paid }]}>{item.msg1}</Text>
                {
                  item.msg5 &&
                  <Text style={[styles.traceText, { marginLeft: 5, color: theme.reserved }]}>"{item.msg5}"</Text>
                }
                {
                  item.msg6 &&
                  <Text style={[styles.traceText, { marginLeft: 5, color: theme.vacant }]}>&#60;{item.msg6}&#62;</Text>
                }
                {
                  item.msg4 &&
                  <Text style={[styles.traceText, { marginLeft: 5, color: theme.occupied }]}>#{item.msg4}</Text>
                }
                {
                  item.msg3 &&
                  <Text style={[styles.traceText, { marginLeft: 5, color: theme.adhoc }]}>[{item.msg3}]</Text>
                }
              </ScrollView>
            )}
          />
        </View>
        <View style={styles.containerThird}>
          <View style={{ flex: 1 }}>
            <TouchableOpacity style={[styles.button,]}
              onPress={() => this.doToggleUser()}>
              <AntDesign
                name='woman'
                color='white'
                size={36}
              />
              <Text style={styles.buttonText} numberOfLines={1}>{thisUser ? home.userName : `${intl.get('all')} ${intl.get('user')}`}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button,]}
              disabled={!selectedTable}
              onPress={() => this.doToggleTable()}>
              <AntDesign
                name='appstore-o'
                color='white'
                size={36}
              />
              <Text style={styles.buttonText}>{thisTable ? `${intl.get('table')} ${selectedTable.tableId}` : `${intl.get('all')} ${intl.get('table')}`}</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'row',
    backgroundColor: theme.grey1,
  },
  containerFirst: {
    flex: 1,
    flexDirection: 'row',
    padding: 10,
  },
  containerSecond: {
    flex: 6,
    marginVertical: 10,
    borderRadius: 5,
    backgroundColor: theme.grey3,
  },
  containerThird: {
    flex: 1,
    flexDirection: 'row',
    padding: 10,
  },
  searchBoxInfo: {
    height: 20,
    margin: 5,
    flexDirection: 'row',
  },
  button: {
    backgroundColor: 'transparent',
    borderRadius: 10,
    aspectRatio: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  traceText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 12,
  }
});