  handleUpdateFromChoosePrinter(printer, purpose) {
    const { selectedTable, home } = this.state;
    if (!printer) {
      this.setState({
        printPurpose: '',
        selectedPrinter: '',
        overlayChoosePrinterVisible: false,
      });
      return;
    }
    this.setState({
      printPurpose: '',
      selectedPrinter: printer,
      overlayChoosePrinterVisible: false,
    });
    if (purpose === 'SEND ORDER') {
      this.doSendOrder(printer);
    } else if (purpose === 'PRINT BILL') {
      this.doPrintBill(printer);
    } else if (purpose === 'START PAYMENT') {
      this.doStartPayment(printer);
    } else if (purpose === 'OPEN DRAWER') {
      this.doOpenDrawer(printer);
    } else if (purpose === 'DEFAULT PRINTER') {
      let homeUpdate = home;
      homeUpdate.defaultPrinter = printer;
      this.setState({
        home: homeUpdate,
      });
    }
  }

  handleUpdateFromRequestAccess(text) {
    const { home, serviceEntry, masters, settings, requestedPrivilege, selectedPrinter,
      selectedTable, selectedTableItem } = this.state;
    if (!text) {
      this.setState({
        overlayRequestAccessVisible: false,
      });
      return;
    }
    let url = serviceEntry + 'api/super-user';
    const body = {
      locId: home.locId,
      accessCode: text,
      priId: requestedPrivilege,
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post super-user successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Request Access ' + requestedPrivilege,
            home.userName,
            response.message,
            selectedTable?.tableId,
            selectedTableItem?.menuName,
            response.userId,
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          overlayRequestAccessVisible: false,
        });
        if (requestedPrivilege === 'OPENDRAWER') {
          this.doOpenDrawer(selectedPrinter, response.userId);
        } else if (requestedPrivilege === 'PAYMENT') {
          this.doStartPayment(selectedPrinter, response.userId);
        } else if (requestedPrivilege === 'DAYCLOSE') {
          this.doStartDayclose(response.userId);
        } else if (requestedPrivilege === 'VOIDITEM') {
          this.doDeleteTableItem(response.userId);
        } else if (requestedPrivilege === 'EDITITEM') {
          this.doEditTableItem(response.userId);
        }
      })
  }

  handleUpdateFromAdditionalFunctions(text) {
    const { navigation } = this.props;
    const { serviceEntry, home, masters, selectedTable, tableItems } = this.state;

    this.setState({
      overlayAdditionalFunctionsVisible: false,
    });
    if (!text) {
      return;
    }
    if (text === 'viewReprint') {
      navigation.navigate('Reprint', { home, serviceEntry, masters });
    } else if (text === 'viewVoid') {
      navigation.navigate('Void', { home, serviceEntry, masters, handleUpdateFromVoid: this.handleUpdateFromVoid });
    } else if (text === 'viewCollect') {
      navigation.navigate('Collect', { home, serviceEntry, masters, });
    } else if (text === 'viewDayclose') {
      this.doStartDayclose();
    } else if (text === 'viewReports') {
      navigation.navigate('Report', { home, serviceEntry, masters });
    } else if (text === 'openDrawer') {
      this.doOpenDrawer();
    } else if (text === 'viewPrinters') {
      navigation.navigate('Printer', { home, serviceEntry });
    } else if (text === 'viewActivate') {
      navigation.navigate('Activate', { home, serviceEntry, masters });
    } else if (text === 'logOut') {
      navigation.goBack();
    } else if (text === 'syncMasters') {
      navigation.navigate('Login', { sync: new Date() });
    } else if (text === 'viewTrace') {
      navigation.navigate('Trace', { home, serviceEntry, selectedTable });
    } else if (text === 'viewLimit') {
      navigation.navigate('Limit', { home, serviceEntry, masters });
    }
  }

  async handleUpdateFromTableActions(text) {
    const { navigation } = this.props;
    const { serviceEntry, home, masters, selectedTable, tableItems } = this.state;

    this.setState({
      overlayTableActionsVisible: false,
    });
    if (!text) {
      return;
    }
    if (text === 'viewResequence') {
      navigation.navigate('Resequence', { home, serviceEntry, masters, tableItems, selectedTable });
    } else if (text === 'viewTransfer') {
      navigation.navigate('Transfer', { home, serviceEntry, masters, tableItems, selectedTable });
    } else if (text === 'headerDiscount') {
      this.setState({
        overlayHeaderDiscountVisible: true,
      })
    } else if (text === 'printBill') {
      this.doPrintBill();
    } else if (text === 'openTable') {
      await this.handlePressTable(selectedTable)
      navigation.navigate('Table', { home, serviceEntry, masters, selectedTable, mode: 'NEW', handleUpdateFromTable: this.handleUpdateFromTable });
    } else if (text === 'editTable') {
      navigation.navigate('Table', { home, serviceEntry, masters, selectedTable, mode: 'EDIT' });
    } else if (text === 'resetTable') {
      this.doChangeTableStatus('A');
    } else if (text === 'reserveTable') {
      this.doChangeTableStatus('B');
    } else if (text === 'disableTable') {
      this.doChangeTableStatus('E');
    } else if (text === 'mergeTable') {
      this.setState({
        overlayMergeTableVisible: true,
      });
    } else if (text === 'splitTable') {
      this.setState({
        overlaySplitTableVisible: true,
      });
    } else if (text === 'changeTable') {
      this.setState({
        overlayChangeTableVisible: true,
      });
    }
  }

  handleUpdateFromReviseItem(text) {
    const { navigation } = this.props;
    const { serviceEntry, home, settings, masters, selectedTable, selectedTableItem } = this.state;
    if (!text) {
      this.setState({
        overlayReviseItemVisible: false,
      });
      return;
    }
    if (text === 'voidItem') {
      let options = [];
      options.push({ text: 'Yes', handleText: 'doDeleteTableItem' });
      this.setState({
        overlayReviseItemVisible: false,
        overlayAlertInputValue: {
          title: intl.get('action'),
          subtitle: 'Remove Item',
          text: 'Remove ' + selectedTableItem.menuName + ' ?',
          ok: false,
          cancel: true,
          options: options,
        },
        overlayAlertVisible: true,
      })
    } else if (text === 'editItem') {
      this.doEditTableItem();
    } else if (text === 'revertItem') {
      this.doRevertTableItem();
    } else if (text === 'changePrice') {
      this.setState({
        overlayChangePriceVisible: true,
        overlayReviseItemVisible: false,
      })
    } else if (text === 'lineDiscount') {
      this.setState({
        overlayLineDiscountVisible: true,
        overlayReviseItemVisible: false,
      })
    } else if (text === 'lineBreak') {
      this.doUpdateLineBreak();
    } else if (text === 'duplicateItem') {
      this.doDuplicateTableItem();
    }
  }

