import React, { useContext } from 'react'
import * as RN from 'react-native';
// import ThemeContext from '../ThemeContext';

// text wrapper for custom style text component
export function Text({ style, children, ...rest }) {

  // const themeContext = useContext(ThemeContext);

  let styleObject = style;
  // convert array to object
  if (Array.isArray(style)) {
    styleObject = {};
    style.forEach(el => {
      styleObject = Object.assign(styleObject, el);
    })
  }

  return (
    // override font size
    <RN.Text style={{ ...styleObject, fontSize: (styleObject?.fontSize || 14), fontFamily: (styleObject?.fontFamily || 'varela-round') }} {...rest}>
      {children}
    </RN.Text>
  );
};

export function debug(serviceEntry, msg1, msg2, msg3, msg4, msg5, msg6) {
  fetch(serviceEntry + 'api/debug/', {
    method: 'POST',
    headers: {
      "Content-Type": "application/json; charset=utf-8",
    },
    body: JSON.stringify({
      msg1, // action
      msg2, // user name
      msg3, // error
      msg4, // target id (table)
      msg5, // detail name (tableItem)
      msg6, // additional info (other)
    }),
  })
}