import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TextInput, TouchableOpacity,
  Dimensions, ScrollView,
} from 'react-native';
import theme from '../assets/theme';
import OverlayChoosePrinter from './OverlayChoosePrinter';
import intl from 'react-intl-universal';

export default class OverlaySplitAmount extends Component {

  constructor(props) {
    super(props);
    this.state = {
      receipts: props.receipts.map(item => ({
        docId: item.docId,
        recKey: item.recKey,
        amount: '0',
      })),
      warning: 'Total amount to split is ' + props.selectedTable.grandTotal.toFixed(2),
      overlayChoosePrinterVisible: false,
    };
    this.handleFillRemainder = this.handleFillRemainder.bind(this);
    this.handleSetAmountForReceipt = this.handleSetAmountForReceipt.bind(this);
    this.handleUpdateFromChoosePrinter = this.handleUpdateFromChoosePrinter.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleFillRemainder(item) {
    const { selectedTable } = this.props;
    const { receipts } = this.state;
    let receiptsUpdate = receipts;
    const index = receipts.findIndex(el => el.recKey === item.recKey);
    const aggregate = receipts.filter(el => el.recKey !== item.recKey).reduce((init, obj) => { return init + Number(obj.amount); }, 0);
    if (aggregate <= Number(selectedTable.grandTotal)) {
      receiptsUpdate[index].amount = (Number(selectedTable.grandTotal) - aggregate).toFixed(2).toString();
      this.setState({
        receipts: receiptsUpdate,
      });
    }
  }

  handleSetAmountForReceipt(number, docId) {
    const { receipts } = this.state;
    let receiptsUpdate = receipts;
    let index = receipts.findIndex(el => el.docId === docId);
    receiptsUpdate[index].amount = number;
    this.setState({
      receipts: receiptsUpdate
    }, () => console.log('receiptsUpdated', receipts));
  }

  handleUpdateFromChoosePrinter(printer, purpose) {
    this.setState({
      overlayChoosePrinterVisible: false,
    });
    if (!printer) {
      return;
    }
    this.handleSubmit(printer);
  }

  handleSubmit(printer) {
    const { selectedTable, cashierPrinters, home } = this.props;
    const { receipts } = this.state;
    const aggregate = receipts.reduce((init, obj) => { return init + Number(obj.amount); }, 0).toFixed(2);
    console.log('aggregate', aggregate);
    if (receipts.some(item => Number(item.amount) === 0) || (Number(aggregate) !== Number(selectedTable.grandTotal))) {
      console.log('validation failed');
      this.setState({
        warning: 'Ensure numbers are valid and add up to ' + selectedTable.grandTotal.toFixed(2),
      });
      return;
    }

    if (cashierPrinters.length > 1 && !printer && !home.defaultPrinter) {
      this.setState({
        overlayChoosePrinterVisible: true,
      });
      return;
    }

    this.props.handleUpdateFromSplitAmount(receipts, home.defaultPrinter || printer);
  }

  doFillEvenly() {
    const { selectedTable } = this.props;
    const { receipts } = this.state;
    let receiptsUpdate = receipts;

    const equal = Number(selectedTable.grandTotal / receipts.length).toFixed(2);

    receiptsUpdate.forEach((el, index) => {
      if (index !== receiptsUpdate.length - 1) {
        el.amount = Number(equal).toFixed(2);
      } else {
        el.amount = (selectedTable.grandTotal - equal * (receiptsUpdate.length - 1)).toFixed(2);
      }
    })

    this.setState({
      receipts: receiptsUpdate
    }, () => console.log('receiptsUpdated', receipts));
  }

  render() {
    const { selectedTable, cashierPrinters } = this.props;
    const { receipts, warning, overlayChoosePrinterVisible } = this.state;
    console.log('in overlaysplitamount');

    return (
      <View style={styles.overlay}>
        {
          overlayChoosePrinterVisible &&
          <OverlayChoosePrinter
            printPurpose='SPLIT BY AMOUNT'
            cashierPrinters={cashierPrinters}
            handleUpdateFromChoosePrinter={this.handleUpdateFromChoosePrinter} />
        }
        <View style={styles.overlayContent}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>Split Amount</Text>
            <Text style={styles.overlayText}>{warning}</Text>
          </View>
          <ScrollView style={styles.overlayScroll}>
            {
              receipts.map(item => (
                <View style={styles.inputRow}
                  key={item.docId}
                >
                  <TouchableOpacity style={styles.inputRowLeft}
                    onPress={() => this.handleFillRemainder(item)}
                  >
                    <Text style={styles.inputRowTitleText}>RECEIPT</Text>
                    <Text style={styles.inputRowSubtitleText}>{item.docId}</Text>
                  </TouchableOpacity>
                  <View style={styles.inputRowRight}>
                    <TextInput style={styles.textInput}
                      keyboardType='numeric'
                      selectTextOnFocus={true}
                      value={receipts.find(el => el.docId === item.docId).amount}
                      onChangeText={(number) => this.handleSetAmountForReceipt(number, item.docId)}
                    />
                    <Text style={styles.percentageText}></Text>
                  </View>
                </View>
              ))
            }
          </ScrollView>
          <TouchableOpacity style={[styles.overlayOption, { marginTop: 20 }]}
            onPress={() => this.doFillEvenly()}>
            <Text style={styles.overlayOptionText}>fill evenly</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption,]}
            onPress={() => this.handleSubmit()}>
            <Text style={styles.overlayOptionText}>{intl.get('submit')}</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.overlayOption, { backgroundColor: theme.paid }]}
            onPress={() => this.props.handleUpdateFromSplitAmount('')}>
            <Text style={styles.overlayOptionText}>{intl.get('cancel')}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
}

const keyHeight = Dimensions.get('window').height / 8;

const styles = StyleSheet.create({
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.9)',
    position: 'absolute',
    zIndex: 1,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayContent: {
    width: '40%',
    backgroundColor: theme.grey2,
    borderRadius: 5,
    overflow: 'hidden',
    position: 'relative',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
  },
  overlayText: {
    marginTop: 15,
    fontFamily: 'varela-round',
    color: 'white',
    textAlign: 'center',
  },
  overlayNumber: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 32,
  },
  overlayOption: {
    height: 60,
    backgroundColor: theme.grey4,
    alignItems: 'center',
    justifyContent: 'center',
    margin: 5,
  },
  overlayOptionText: {
    fontFamily: 'varela-round',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  },
  overlayScroll: {
    height: 280, // 4 times of (60 + 10) inputRow class
  },
  inputRow: {
    height: 60,
    // backgroundColor: 'white',
    marginVertical: 5,
    marginHorizontal: 40,
    flexDirection: 'row',
  },
  inputRowLeft: {
    flex: 1,
    justifyContent: 'center',
  },
  inputRowRight: {
    flex: 1,
    borderRadius: 5,
    flexDirection: 'row',
    backgroundColor: theme.grey1,
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  inputRowTitleText: {
    fontFamily: 'varela-round',
    fontSize: 12,
    color: theme.paid,
  },
  inputRowSubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
  },
  textInput: {
    width: '75%',
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    textAlign: 'right',
  },
  percentageText: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    opacity: 0.6,
    paddingHorizontal: 10,
  },
});