import React, { Component } from 'react';
import {
  StyleSheet, Text, View, TouchableOpacity,
  Dimensions, ScrollView, FlatList,
} from 'react-native';
import { AntDesign } from '@expo/vector-icons';
import theme from '../assets/theme';
import intl from 'react-intl-universal';
import { debug } from '../assets/utility';
import OverlayProcedureMessage from './OverlayProcedureMessage';

export default class Resequence extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      masters: props.route.params.masters,
      settings: props.route.params.masters.settings,
      selectedTable: props.route.params.selectedTable,
      tableItems: props.route.params.tableItems,

      selectedTableItems: [],
      arrangedItems: props.route.params.tableItems.filter(el => el.confirmFlg === 'N'),

      overlayProcedureMessageVisible: false,
    };
    this.handleSelectTableItem = this.handleSelectTableItem.bind(this);
    this.handleReset = this.handleReset.bind(this);
    this.handleAddLineBreak = this.handleAddLineBreak.bind(this);
    this.handleRemoveLineBreak = this.handleRemoveLineBreak.bind(this);
    this.handleResequence = this.handleResequence.bind(this);
    this.handleShiftTop = this.handleShiftTop.bind(this);
    this.handleShiftUp = this.handleShiftUp.bind(this);
    this.handleShiftDown = this.handleShiftDown.bind(this);
    this.handleShiftBottom = this.handleShiftBottom.bind(this);

    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  handleSelectTableItem(item) {
    console.log('menu selected', item);
    const { selectedTableItems, arrangedItems, tableItems } = this.state;

    let selectedTableItemsUpdate = [];
    selectedTableItemsUpdate.push(item);
    let comboItems = [];
    comboItems = tableItems.filter(el => el.refRecKey === item.recKey);
    console.log('comboItems', comboItems);
    comboItems.forEach(el => selectedTableItemsUpdate.push(el));

    this.setState({
      selectedTableItems: selectedTableItemsUpdate,
    });
  }

  handleReset() {
    this.setState({
      arrangedItems: this.state.tableItems.filter(el => el.confirmFlg === 'N'),
      selectedTableItems: [],
    });
  }

  handleAddLineBreak() {
    const { arrangedItems, selectedTableItems } = this.state;
    if (selectedTableItems.length === 0) return;
    let arrangedItemsUpdate = arrangedItems;
    const index = arrangedItemsUpdate.indexOf(arrangedItemsUpdate.find(el => el.recKey === selectedTableItems[0].recKey));
    arrangedItemsUpdate[index].lineBreak = 'Y';
    this.setState({
      arrangedItems: arrangedItemsUpdate,
    });
  }

  handleRemoveLineBreak() {
    const { arrangedItems, selectedTableItems } = this.state;
    if (selectedTableItems.length === 0) return;
    let arrangedItemsUpdate = arrangedItems;
    const index = arrangedItemsUpdate.indexOf(arrangedItemsUpdate.find(el => el.recKey === selectedTableItems[0].recKey));
    arrangedItemsUpdate[index].lineBreak = 'N';
    this.setState({
      arrangedItems: arrangedItemsUpdate,
    });
  }

  handleShiftTop() {
    const { selectedTableItems, arrangedItems } = this.state;
    let arrangedItemsUpdate = arrangedItems;
    selectedTableItems.forEach(el => {
      arrangedItemsUpdate = arrangedItemsUpdate.filter(obj => obj.recKey !== el.recKey)
    });
    arrangedItemsUpdate.unshift(...selectedTableItems);
    this.setState({
      arrangedItems: arrangedItemsUpdate,
    }, () => this.scrollViewRef.scrollToOffset({ animated: true, offset: 0 }));
  }

  handleShiftUp() {
    const { selectedTableItems, arrangedItems } = this.state;
    let arrangedItemsUpdate = arrangedItems;
    let index = arrangedItems.indexOf(arrangedItems.find(obj => obj.recKey === selectedTableItems[0].recKey));
    if (index === 0) return;
    selectedTableItems.forEach(el => {
      arrangedItemsUpdate = arrangedItemsUpdate.filter(obj => obj.recKey !== el.recKey)
    });
    let spliceIndex = index - 1;
    if (arrangedItems[spliceIndex].refRecKey) {
      spliceIndex = arrangedItems.indexOf(arrangedItems.find(obj => obj.recKey === arrangedItems[index - 1].refRecKey));
    }
    arrangedItemsUpdate.splice(spliceIndex, 0, ...selectedTableItems);
    this.setState({
      arrangedItems: arrangedItemsUpdate,
    });
  }

  handleShiftDown() {
    const { selectedTableItems, arrangedItems } = this.state;
    let arrangedItemsUpdate = arrangedItems;
    let index = arrangedItems.indexOf(arrangedItems.find(obj => obj.recKey === selectedTableItems[0].recKey));
    if (index === arrangedItems.length - selectedTableItems.length) return;
    selectedTableItems.forEach(el => {
      arrangedItemsUpdate = arrangedItemsUpdate.filter(obj => obj.recKey !== el.recKey)
    });
    let spliceIndex = index + 1;
    if (arrangedItemsUpdate.some(el => el.refRecKey === arrangedItemsUpdate[index].recKey)) {
      let skip = arrangedItems.filter(el => el.refRecKey === arrangedItemsUpdate[index].recKey).length;
      console.log('skip', skip);
      spliceIndex += skip;
    }
    arrangedItemsUpdate.splice(spliceIndex, 0, ...selectedTableItems);
    this.setState({
      arrangedItems: arrangedItemsUpdate,
    });
  }

  handleShiftBottom() {
    const { selectedTableItems, arrangedItems } = this.state;
    let arrangedItemsUpdate = arrangedItems;
    selectedTableItems.forEach(el => {
      arrangedItemsUpdate = arrangedItemsUpdate.filter(obj => obj.recKey !== el.recKey)
    });
    arrangedItemsUpdate.push(...selectedTableItems);
    this.setState({
      arrangedItems: arrangedItemsUpdate,
    }, () => this.scrollViewRef.scrollToEnd());
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleResequence() {
    const { navigation } = this.props;
    const { serviceEntry, home, settings, selectedTable, tableItems, arrangedItems } = this.state;

    // count sent table items
    let skip = tableItems.filter(el => el.confirmFlg === 'Y').length;
    console.log('skip', skip);

    let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/resequence';
    const itemPayloads = arrangedItems.map((el, index) =>
    ({
      opentableItemRecKey: el.recKey,
      lineNo: el.lineNo,
      newLineNo: index + 1 + skip,
      lineBreak: el.lineBreak,
    })
    );
    const body = {
      userId: home.userId,
      opentableRecKey: selectedTable.recKey,
      itemPayloads: itemPayloads,
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post resequence successful', response);
        settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
          debug(
            serviceEntry,
            'Resequence Items',
            home.userName,
            response.message,
            selectedTable.tableId,
            '',
            '',
          );
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        navigation.goBack();
      })
  }

  render() {
    const { navigation } = this.props;
    const { selectedTable, tableItems, arrangedItems, selectedTableItems,
      overlayProcedureMessageVisible } = this.state;
    console.log('in resequence', selectedTableItems, arrangedItems);
    return (
      <View style={styles.container}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        <View style={styles.containerFirst}>
          <ScrollView style={{ flex: 1 }}
            alwaysBounceVertical={false}
            showsVerticalScrollIndicator={false}>
            <TouchableOpacity style={styles.button}
              onPress={() => navigation.goBack()}>
              <AntDesign name='left' color='white' size={36} />
              <Text style={styles.buttonText}>{intl.get('back')}</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button}
              onPress={() => this.handleReset()}>
              <AntDesign name='reload1' size={36} color='white' />
              <Text style={styles.buttonText}>Reset</Text>
            </TouchableOpacity>
            {
              !selectedTableItems[0] ?
                <View style={[styles.button, { opacity: 0.2 }]}>
                  <AntDesign name='minus' size={36} color='white' />
                  <Text style={styles.buttonText}>Add Break</Text>
                </View>
                :
                selectedTableItems[0].lineBreak === 'Y' ?
                  <TouchableOpacity style={styles.button}
                    onPress={() => this.handleRemoveLineBreak()}>
                    <AntDesign name='minus' size={36} color='white' />
                    <Text style={styles.buttonText}>Remove Break</Text>
                  </TouchableOpacity>
                  :
                  <TouchableOpacity style={styles.button}
                    onPress={() => this.handleAddLineBreak()}>
                    <AntDesign name='minus' size={36} color='white' />
                    <Text style={styles.buttonText}>Add Break</Text>
                  </TouchableOpacity>
            }
            <TouchableOpacity style={styles.button}
              onPress={() => this.handleResequence()}>
              <AntDesign name='bars' size={36} color={theme.reserved} />
              <Text style={[styles.buttonText, { color: theme.reserved }]}>Confirm</Text>
            </TouchableOpacity>
          </ScrollView>
        </View>
        <View style={styles.containerSecond}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('table')} {selectedTable.tableId}</Text>
            <Text style={styles.overlaySubtitleText}>{intl.get('resequence')}</Text>
          </View>
          <FlatList
            style={{ flex: 1 }}
            ref={ref => this.scrollViewRef = ref}
            showsVerticalScrollIndicator={false}
            data={arrangedItems}
            keyExtractor={(item) => item.recKey.toString()}
            renderItem={({ item, index }) => (
              <View>
                {
                  item.lineBreak === 'Y' &&
                  <View style={styles.lineBreakContainer}>
                    <Text style={[styles.tableItemText, { color: theme.paid }]}>- Line Break -</Text>
                  </View>
                }
                <TouchableOpacity
                  disabled={item.refRecKey ? true : false}
                  style={[styles.tableItemContainer, selectedTableItems.some(el => el.recKey === item.recKey) && { backgroundColor: theme.occupied }, item.refRecKey && { opacity: 0.2 }]}
                  onPress={() => this.handleSelectTableItem(item)}>
                  <View style={styles.tableItemFirstContainer}><Text style={styles.tableItemText}>{item.orderQty}</Text></View>
                  <View style={styles.tableItemSecondContainer}>
                    <Text style={styles.tableItemText}>
                      {item.refRecKey && '- '}{item.menuName}
                    </Text>
                    {
                      item.menuMod &&
                      <Text style={styles.tableItemModText}>{item.menuMod}</Text>
                    }
                  </View>
                  <View style={styles.tableItemThirdContainer}>
                    <Text style={styles.tableItemText}>
                    </Text>
                  </View>
                </TouchableOpacity>
              </View>
            )}
          />
        </View>
        <View style={styles.containerThird}>
          <ScrollView style={{ flex: 1 }}
            alwaysBounceVertical={false}
            showsVerticalScrollIndicator={false}>
            <TouchableOpacity style={[styles.button, selectedTableItems.length !== 0 ? null : { opacity: 0.2 }]}
              disabled={selectedTableItems.length === 0}
              onPress={() => this.handleShiftTop()}>
              <AntDesign name='arrowup' size={36} color='white' />
              <Text style={styles.buttonText}>TOP</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, selectedTableItems.length !== 0 ? null : { opacity: 0.2 }]}
              disabled={selectedTableItems.length === 0}
              onPress={() => this.handleShiftUp()}>
              <AntDesign name='up' size={36} color='white' />
              <Text style={styles.buttonText}>SHIFT</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, selectedTableItems.length !== 0 ? null : { opacity: 0.2 }]}
              disabled={selectedTableItems.length === 0}
              onPress={() => this.handleShiftDown()}>
              <AntDesign name='down' size={36} color='white' />
              <Text style={styles.buttonText}>SHIFT</Text>
            </TouchableOpacity>
            <TouchableOpacity style={[styles.button, selectedTableItems.length !== 0 ? null : { opacity: 0.2 }]}
              disabled={selectedTableItems.length === 0}
              onPress={() => this.handleShiftBottom()}>
              <AntDesign name='arrowdown' size={36} color='white' />
              <Text style={styles.buttonText}>BOTTOM</Text>
            </TouchableOpacity>
          </ScrollView>
        </View>
      </View>
    );

  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'row',
    backgroundColor: theme.grey1,
  },
  containerFirst: {
    flex: 1,
    flexDirection: 'row',
    padding: 10,
  },
  containerSecond: {
    flex: 6,
    marginVertical: 10,
    borderRadius: 5,
    backgroundColor: theme.grey3,
  },
  containerThird: {
    flex: 1,
    flexDirection: 'row',
    padding: 10,
  },
  searchBoxInfo: {
    height: 20,
    margin: 5,
    flexDirection: 'row',
  },
  button: {
    backgroundColor: 'transparent',
    borderRadius: 10,
    aspectRatio: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  tableItemText: {
    fontFamily: 'varela-round',
    color: 'white',
  },
  tableItemModText: {
    fontFamily: 'varela-round',
    fontSize: 12,
    color: theme.reserved,
  },
  tableItemContainer: {
    flex: 1,
    flexDirection: 'row',
    minHeight: 50,
    paddingVertical: 5,
    marginHorizontal: 5,
    marginBottom: 5,
    backgroundColor: theme.grey4,
  },
  tableItemFirstContainer: {
    width: 50,
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 3,
    borderRightColor: 'white',
  },
  tableItemSecondContainer: {
    flex: 1,
    justifyContent: 'center',
    paddingLeft: 10,
  },
  tableItemThirdContainer: {
    alignItems: 'center',
    justifyContent: 'center',
    width: 50,
  },
  lineBreakContainer: {
    flex: 1,
    flexDirection: 'row',
    minHeight: 50,
    paddingVertical: 5,
    marginHorizontal: 5,
    marginBottom: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
});