import React, { Component } from 'react';
import {
  StyleSheet, Text, View,
  TouchableOpacity,
  Dimensions, ScrollView,
} from 'react-native';
import moment from 'moment';
import { AntDesign } from '@expo/vector-icons';
import OverlayRequestAccess from './OverlayRequestAccess';
import OverlayChoosePrinter from './OverlayChoosePrinter';
import OverlayProcedureMessage from './OverlayProcedureMessage';
import OverlayEnterDate from './OverlayEnterDate';
import theme from '../assets/theme';
import intl from 'react-intl-universal';

export default class Report extends Component {

  constructor(props) {
    super(props);
    this.state = {
      home: props.route.params.home,
      serviceEntry: props.route.params.serviceEntry,
      cashierPrinters: props.route.params.masters.cashierPrinters,
      privileges: props.route.params.masters.privileges,

      selectedButton: '',
      fromDateString: moment().format('YYYY-MM-DD'),
      toDateString: moment().format('YYYY-MM-DD'),
      reportDetails: '',
      requestedPrivilege: '',
      overlayRequestAccessVisible: false,
      overlayChoosePrinterVisible: false,
      overlayEnterDateVisible: false,
      overlayProcedureMessageVisible: false,
    };
    this.handlePressDate = this.handlePressDate.bind(this);
    this.handleSelectReport = this.handleSelectReport.bind(this);
    this.handlePrintReport = this.handlePrintReport.bind(this);
    this.handleUpdateFromEnterDate = this.handleUpdateFromEnterDate.bind(this);
    this.handleUpdateFromRequestAccess = this.handleUpdateFromRequestAccess.bind(this);
    this.handleUpdateFromChoosePrinter = this.handleUpdateFromChoosePrinter.bind(this);
    this.handleUpdateFromProcedureMessage = this.handleUpdateFromProcedureMessage.bind(this);
  }

  componentDidMount() {
  }

  handlePressDate(type) {
    const { fromDateString, toDateString } = this.state;
    this.setState({
      overlayEnterDateVisible: true,
      selectedDateType: type,
      selectedDateString: type === 'FROMDATE' ? fromDateString : toDateString,
    })
  }

  handleSelectReport(report, superUserId) {
    const { serviceEntry, home, privileges, fromDateString, toDateString } = this.state;
    if (!(home.adminFlg === 'Y' || privileges.some(el => el.priId === report)) && !superUserId) {
      this.setState({
        overlayRequestAccessVisible: true,
        requestedPrivilege: report,
      });
      return;
    }
    this.setState({
      selectedButton: report,
    });
    console.log('report');
    let url = serviceEntry + 'api/bistro-report';
    const body = {
      userId: home.userId,
      orgId: home.orgId,
      locId: home.locId,
      shopId: home.shopId,
      reportCode: report,
      fromDateString,
      toDateString,
    };
    console.log('body', body, url);
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post bistro-report successful', response);
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          reportDetails: response,
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handlePrintReport(printer) {
    const { home, serviceEntry, cashierPrinters, selectedButton, fromDateString, toDateString } = this.state;

    if (!selectedButton) {
      return;
    }

    if (cashierPrinters.length > 1 && !printer & !home.defaultPrinter) {
      this.setState({
        overlayChoosePrinterVisible: true,
      });
      return;
    }

    let url = serviceEntry + 'api/print-bistro-report';
    const body = {
      userId: home.userId,
      orgId: home.orgId,
      locId: home.locId,
      shopId: home.shopId,
      reportCode: selectedButton,
      iomodelId: (home.defaultPrinter && home.defaultPrinter.iomodelId) || (printer && printer.iomodelId),
      fromDateString,
      toDateString,
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post print-bistro-report successful', response);
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          reportDetails: response,
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleUpdateFromEnterDate(text, type) {
    this.setState({
      overlayEnterDateVisible: false,
    });
    if (!text) {
      return;
    }
    const { serviceEntry, home, selectedButton, fromDateString, toDateString } = this.state;

    let fromDateStringUpdate = fromDateString;
    let toDateStringUpdate = toDateString;

    if (type === 'TODATE') {
      toDateStringUpdate = moment(text).format('YYYY-MM-DD');
    } else {
      fromDateStringUpdate = moment(text).format('YYYY-MM-DD');
    }

    this.setState({
      fromDateString: fromDateStringUpdate,
      toDateString: toDateStringUpdate,
    });

    let url = serviceEntry + 'api/bistro-report';
    const body = {
      userId: home.userId,
      orgId: home.orgId,
      locId: home.locId,
      shopId: home.shopId,
      reportCode: selectedButton,
      fromDateString: fromDateStringUpdate,
      toDateString: toDateStringUpdate,
    };
    console.log('body', body, url);
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('receipts', response);
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          reportDetails: response,
        });
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  handleUpdateFromProcedureMessage(text) {
    if (!text) {
      this.setState({
        overlayProcedureMessageVisible: false,
      })
    }
  }

  handleUpdateFromChoosePrinter(printer, purpose) {
    this.setState({
      overlayChoosePrinterVisible: false,
    });
    if (!printer) {
      return;
    }
    this.handlePrintReport(printer);
  }

  handleUpdateFromRequestAccess(text) {
    const { home, serviceEntry, requestedPrivilege } = this.state;
    if (!text) {
      this.setState({
        overlayRequestAccessVisible: false,
      });
      return;
    }
    let url = serviceEntry + 'api/super-user';
    const body = {
      locId: home.locId,
      accessCode: text,
      priId: requestedPrivilege,
    };
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8",
      },
      body: JSON.stringify(body),
    })
      .then(response => response.json())
      .then(response => {
        console.log('post super-user successful', response);
        if (response.message) {
          this.setState({
            overlayProcedureMessageVisible: response.message.toUpperCase(),
          })
          return;
        }
        // to state
        this.setState({
          overlayRequestAccessVisible: false,
        });
        this.handleSelectReport(requestedPrivilege, response.userId);
      })
      .catch(error => {
        console.log(error);
        // to state
        this.setState({
          loading: false
        });
      });
  }

  render() {
    const { navigation } = this.props;
    const { home, cashierPrinters,
      selectedButton, reportDetails, requestedPrivilege,
      fromDateString, toDateString, selectedDateString, selectedDateType,
      overlayProcedureMessageVisible, overlayEnterDateVisible,
      overlayChoosePrinterVisible, overlayRequestAccessVisible } = this.state;
    console.log('in reports');

    return (
      <View style={styles.container}>
        {
          overlayProcedureMessageVisible &&
          <OverlayProcedureMessage
            text={overlayProcedureMessageVisible}
            handleUpdateFromProcedureMessage={this.handleUpdateFromProcedureMessage} />
        }
        {
          overlayEnterDateVisible &&
          <OverlayEnterDate
            dateString={selectedDateString}
            dateType={selectedDateType}
            handleUpdateFromEnterDate={this.handleUpdateFromEnterDate} />
        }
        {
          overlayChoosePrinterVisible &&
          <OverlayChoosePrinter
            printPurpose='PRINT REPORT'
            cashierPrinters={cashierPrinters}
            handleUpdateFromChoosePrinter={this.handleUpdateFromChoosePrinter} />
        }
        {
          overlayRequestAccessVisible &&
          <OverlayRequestAccess
            home={home}
            requestedPrivilege={requestedPrivilege}
            handleUpdateFromRequestAccess={this.handleUpdateFromRequestAccess} />
        }
        <View style={styles.containerFirst}>
          <View style={{ flex: 1 }}>
            <TouchableOpacity style={styles.button}
              onPress={() => navigation.goBack()}>
              <AntDesign
                name='left'
                color='white'
                size={36}
              />
              <Text style={styles.buttonText}>{intl.get('back')}</Text>
            </TouchableOpacity>
            <TouchableOpacity
              style={[styles.selection, selectedButton === 'REVENUESUM' && { backgroundColor: 'white' }]}
              onPress={() => this.handleSelectReport('REVENUESUM')}
            >
              <Text style={[styles.selectionText, selectedButton === 'REVENUESUM' && { color: theme.grey1 }]}>REVENUE</Text>
              <Text style={[styles.selectionText, selectedButton === 'REVENUESUM' && { color: theme.grey1 }]}>SUMMARY</Text>
            </TouchableOpacity>
            <View style={styles.gap}></View>
            <TouchableOpacity
              style={[styles.selection, selectedButton === 'REVENUEDTL' && { backgroundColor: 'white' }]}
              onPress={() => this.handleSelectReport('REVENUEDTL')}
            >
              <Text style={[styles.selectionText, , selectedButton === 'REVENUEDTL' && { color: theme.grey1 }]}>REVENUE</Text>
              <Text style={[styles.selectionText, , selectedButton === 'REVENUEDTL' && { color: theme.grey1 }]}>DETAIL</Text>
            </TouchableOpacity>
            <View style={styles.gap}></View>
            <TouchableOpacity
              style={[styles.selection, selectedButton === 'COLLECTION' && { backgroundColor: 'white' }]}
              onPress={() => this.handleSelectReport('COLLECTION')}
            >
              <Text style={[styles.selectionText, selectedButton === 'COLLECTION' && { color: theme.grey1 }]}>PAYMENT</Text>
              <Text style={[styles.selectionText, selectedButton === 'COLLECTION' && { color: theme.grey1 }]}>COLLECTION</Text>
            </TouchableOpacity>
            <View style={styles.gap}></View>
          </View>
        </View>
        <View style={styles.containerSecond}>
          <View style={styles.overlayHeader}>
            <Text style={styles.overlayTitleText}>{intl.get('action')}</Text>
            <Text style={styles.overlaySubtitleText}>{intl.get('reports')}</Text>
            <View style={styles.gap}></View>
            <View style={{ flexDirection: 'row', alignItems: 'center' }}>
              <TouchableOpacity style={{ height: 30, justifyContent: 'center', backgroundColor: 'none' }}
                onPress={() => this.handlePressDate('FROMDATE')}>
                <Text style={styles.overlayTitleText}>{fromDateString}</Text>
              </TouchableOpacity>
              <Text style={styles.overlayTitleText}>    /    </Text>
              <TouchableOpacity style={{ height: 30, justifyContent: 'center', backgroundColor: 'none' }}
                onPress={() => this.handlePressDate('TODATE')}>
                <Text style={styles.overlayTitleText}>{toDateString}</Text>
              </TouchableOpacity>
            </View>
            <Text style={[styles.overlayTitleText, { color: theme.paid, opacity: 1 }]}>
              {selectedButton === 'REVENUESUM' ?
                'REVENUE SUMMARY'
                :
                selectedButton === 'REVENUEDTL' ?
                  'REVENUE DETAIL'
                  :
                  selectedButton === 'COLLECTION' ?
                    'PAYMENT COLLECTION'
                    : null}
            </Text>
          </View>
          {
            reportDetails ?
              <ScrollView style={styles.reportContainer}
                showsVerticalScrollIndicator={false}>
                {
                  reportDetails.map((item, index) =>
                    <View style={styles.reportLineContainer}
                      key={item.recKey}
                    >
                      <View style={styles.reportLineFirst}>
                        <Text style={styles.reportLineFirstText}>{item.description}</Text>
                      </View>
                      <View style={styles.reportLineSecond}>
                        <Text style={styles.reportLineSecondText}>{item.qty}</Text>
                      </View>
                      <View style={styles.reportLineThird}>
                        <Text style={styles.reportLineThirdText}>{item.val ? item.val.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) : null}</Text>
                      </View>
                    </View>
                  )
                }
              </ScrollView>
              : null
          }
        </View>
        <View style={styles.containerThird}>
          <View style={{ flex: 1 }}>
            <TouchableOpacity style={[styles.button, !selectedButton && { opacity: 0.2 }]}
              disabled={!selectedButton}
              onPress={() => this.handlePrintReport()}>
              <AntDesign
                name='printer'
                color='white'
                size={36}
              />
              <Text style={styles.buttonText}>{intl.get('print')}</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'row',
    backgroundColor: theme.grey1,
  },
  containerFirst: {
    flex: 1,
    flexDirection: 'row',
    padding: 10,
  },
  containerSecond: {
    flex: 6,
    marginVertical: 10,
    borderRadius: 5,
    backgroundColor: theme.grey3,
    alignItems: 'center',
  },
  containerThird: {
    flex: 1,
    flexDirection: 'row',
    padding: 10,
  },
  button: {
    backgroundColor: 'transparent',
    borderRadius: 10,
    aspectRatio: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
  overlayHeader: {
    marginVertical: 15,
    alignItems: 'center',
    justifyContent: 'center',
  },
  overlayTitleText: {
    textTransform: 'uppercase',
    fontFamily: 'varela-round',
    fontSize: 12,
    color: 'white',
    opacity: 0.6,
    textAlign: 'center',
  },
  overlaySubtitleText: {
    fontFamily: 'varela-round',
    fontSize: 24,
    color: 'white',
    textAlign: 'center',
  },
  selection: {
    width: '100%',
    backgroundColor: theme.grey3,
    borderRadius: 5,
    alignItems: 'center',
    justifyContent: 'center',
    height: 70,
  },
  selectionText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 12,
    textAlign: 'center',
  },
  gap: {
    padding: 5,
  },
  reportContainer: {
    flex: 1,
    width: 440,
    borderTopLeftRadius: 5,
    borderTopRightRadius: 5,
    backgroundColor: theme.grey2,
    padding: 20,
  },
  reportLineContainer: {
    height: 50,
    flexDirection: 'row',
  },
  reportLineFirst: {
    width: 240,
  },
  reportLineSecond: {
    width: 60,
  },
  reportLineThird: {
    width: 100,
    alignItems: 'flex-end',
  },
  reportLineFirstText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
  reportLineSecondText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
  reportLineThirdText: {
    fontFamily: 'varela-round',
    color: theme.grey5,
  },
});