import React, { Component } from 'react';
import { StyleSheet, Text, View, Dimensions, TouchableOpacity } from 'react-native';
import { BarCodeScanner } from 'expo-barcode-scanner';
import { AntDesign, Ionicons } from '@expo/vector-icons';
import theme from '../assets/theme';
import intl from 'react-intl-universal';
import { debug } from '../assets/utility';

export default class Scan extends Component {

  constructor(props) {
    super(props);
    this.state = {
      serviceEntry: props.route.params.serviceEntry,
      home: props.route.params.home,
      selectedTable: props.route.params.selectedTable,
      settings: props.route.params.masters.settings,

      hasCameraPermission: null,
      detected: false,
    };
    this.handleBarCodeScanned = this.handleBarCodeScanned.bind(this);
  }

  async componentDidMount() {
    const { status } = await BarCodeScanner.requestPermissionsAsync();
    this.setState({ hasCameraPermission: status === 'granted' });
  }

  handleBarCodeScanned({ type, data }) {
    const { navigation } = this.props;
    const { serviceEntry, home, settings, selectedTable, detected } = this.state;
    this.setState({ detected: true }, () => {
      let url = serviceEntry + 'api/tables/' + selectedTable.recKey + '/scan-barcode';
      const body = {
        userId: home.userId,
        orgId: home.orgId,
        locId: home.locId,
        shopId: home.shopId,
        vipId: home.vipId,
        barcode: data,
        opentableRecKey: selectedTable.recKey,
      };
      fetch(url, {
        method: 'POST',
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
        body: JSON.stringify(body),
      })
        .then(response => {
          if (!response.ok) {
            this.setState({ detected: false });
            throw response;
          } else {
            return response.json();
          }
        })
        .then(response => {
          console.log('post scan-barcode successful', response);
          settings.find(el => el.setId === 'BISTRODEBUG')?.setString === 'Y' &&
            debug(
              serviceEntry,
              'Scan Barcode',
              home.userName,
              response.message,
              selectedTable?.tableId,
              '',
              data,
            );
          navigation.goBack();
        })
        .catch(error => {
          console.log('error: ', JSON.stringify(error));
        });
    });
  }

  render() {
    const { navigation, route } = this.props;
    const { hasCameraPermission, detected } = this.state;

    // if (hasCameraPermission === null) {
    //     return <Text>Requesting for camera permission</Text>;
    // }
    // if (hasCameraPermission === false) {
    //     return <Text>No access to camera</Text>;
    // }

    return (
      <View style={{ flex: 1 }}>
        <BarCodeScanner
          onBarCodeScanned={detected ? null : this.handleBarCodeScanned}
          style={{ flex: 1 }}
        />
        <TouchableOpacity style={styles.backButton}
          onPress={() => navigation.goBack()}>
          <AntDesign name='left' size={48} color='white' />
          <Text style={styles.buttonText}>{intl.get('back')}</Text>
        </TouchableOpacity>
        <View style={styles.iconContainer}>
          <Ionicons name='ios-barcode' color={detected ? 'red' : 'white'} size={400} />
        </View>
      </ View>
    );
  }

}

const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;

const styles = StyleSheet.create({
  iconContainer: {
    position: 'absolute',
    right: windowWidth / 2 - 200,
    top: windowHeight / 2 - 200,
    opacity: 0.5,
    width: 400,
    height: 400,
    alignItems: 'center',
    justifyContent: 'center',
  },
  backButton: {
    position: 'absolute',
    left: 50,
    top: 50,
    width: 100,
    height: 100,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 10,
  },
  buttonText: {
    color: 'white',
    fontFamily: 'varela-round',
    fontSize: 10,
    marginTop: 10,
    textTransform: 'uppercase',
  },
});
